import React, {useState, useRef, useEffect } from 'react';
import '../makecontents.css';



import {ReactComponent as Delete} from '../../../../img/homepage/makecontents/delete_icon.svg';
import {ReactComponent as Bgiconsmall} from '../../../../img/homepage/makecontents/bg-pic_small.svg';


function QuizItem({ id , bgImage, answer, updateQuiz, deleteItem, newQuizNumber }) {
    const [imgErrorMsg, setImgErrorMsg] = useState(false);
    const [txtErrorMsg, setTxtErrorMsg] = useState(false);
    const fileInputRef = useRef(null);
    const [inputMode,setInputMode]=useState(false);
    // const [bgImage, setBgImage] = useState(null); //   
    // const [answer, setAnswer] = useState('');//  
    
    
    const timerRef = useRef(null); // ref


    const handleBgImageChange = (newBgImage) => {
        updateQuiz(id, newBgImage, answer);
    };
    // const handleAnswerChange = (newAnswer) => {
    //     // updateQuiz(id, bgImage, newAnswer);
    //     if (newAnswer.length > 5 || /\.[a-z]+/.test(newAnswer)) {
    //         setTxtErrorMsg(true);
    //     } else {
    //         setTxtErrorMsg(false);
    //         updateQuiz(id, bgImage, newAnswer);
    //     }
    // };
    const handleAnswerChange = (newAnswer) => {
        updateQuiz(id, bgImage, newAnswer);
        if (newAnswer.length > 5 || /\.[a-z]+/.test(newAnswer)) {
            setTxtErrorMsg(true);
        } else {
            setTxtErrorMsg(false);
        }
    };
    const handleDivClick = () => {
        fileInputRef.current.click();
    };

    const handleFileChange = (e) => {
        const file = e.target.files[0];
        if (file && (file.type === 'image/png' || file.type === 'image/jpeg')) {
            const reader = new FileReader();
            reader.onloadend = () => {
                updateQuiz(id, reader.result, answer);
            };
            reader.readAsDataURL(file);
        } else {
            setImgErrorMsg(true);
            e.target.value = null;
            }
            
    };

    const handleImgerror=()=>{
        setImgErrorMsg(false);
    }

    const handleTxterror=()=>{
        setTxtErrorMsg(false);
    }

    const handleInputTxt =()=>{
        setInputMode(true);
    }

    const handleDrop = (event) => {
        event.preventDefault();
        const file = event.dataTransfer.files[0];
        if (file && (file.type === 'image/png' || file.type === 'image/jpeg')) {
            const reader = new FileReader();
            reader.onloadend = () => {
                updateQuiz(id, reader.result, answer);
            };
            reader.readAsDataURL(file);
        } else {
            setImgErrorMsg(true);
        }
    };
    
    const handleDragOver = (event) => {
        event.preventDefault();
    };


    useEffect(() => {
        if (answer) {
          setInputMode(true);
        }
      }, [answer]);

    useEffect(() => {
        let timer;
        if (imgErrorMsg) {
            timer = setTimeout(() => setImgErrorMsg(false), 3000);
        }
        return () => clearTimeout(timer);
    }, [imgErrorMsg]);

    // useEffect(() => {
    //     let timer;
    //     if (txtErrorMsg) {
    //         timer = setTimeout(() => setTxtErrorMsg(false), 3000);
    //     }
    //     return () => clearTimeout(timer);
    // }, [txtErrorMsg]);

    useEffect(() => {
        if (txtErrorMsg) {
            const timer = setTimeout(() => {
                setTxtErrorMsg(false);
            }, 3000);
            return () => clearTimeout(timer);
        }
    }, [txtErrorMsg]);


    return (
        <div className='make_modal_content_quiz_list_item' key={newQuizNumber}>
                <div className='quiz_list_header'>
                    <p>{` ${newQuizNumber}`}</p>
                    
                </div>
                    <div className='quiz_list_contents'>
                        <div className='quiz_list_contents_img' onDrop={handleDrop} onDragOver={handleDragOver}>
                            {bgImage ? (
                                <>
                                 <img src={bgImage} alt="preview" style={{maxWidth: '100%', maxHeight: '100%'}}/> 
                                 <input type="file" ref={fileInputRef} onChange={handleFileChange} style={{display: 'none'}}  />
                                 <div className='quiz_list_contents_imgbtn2' onClick={handleDivClick}><p></p></div>

                                </>
                                

                            ):(
                                <>
                                 <div><Bgiconsmall/></div>
                                <p className='quiz_list_contents_font'> <br/>  </p>
                                <input type="file" ref={fileInputRef} onChange={handleFileChange} style={{display: 'none'}}  />
    
                                <div className='quiz_list_contents_imgbtn1' onClick={handleDivClick}><p></p></div>

                                </>
                               

                            )}
                           

                            {imgErrorMsg &&(
                                <div className='quiz_list_contents_img_error' onClick={handleImgerror}>
                                    <p>  .<br/> .jpg, .png <br/> .</p>
                                </div>
                            )}
                                
                            
                        </div>
                        <div className='quiz_list_contents_txt' onClick={handleInputTxt}>
                            {inputMode ? (

                                <input
                                className='quiz_list_contents_txt_answer'
                                type="text" 
                                value={answer} 
                                maxLength={5}
                                onChange={(e)=>handleAnswerChange(e.target.value)}
                                onBlur={(e) => {
                                    if (!e.target.value.trim()) {
                                        setInputMode(false);
                                    }
                                }}
                                onFocus={(e) => {
                                    if (!e.target.value.trim()) {
                                        handleAnswerChange('');
                                    }
                                }}
                                autoFocus
                                />

                            ):(
                                <>
                                <p className='quiz_list_contents_font'><br/> .</p>
                                <p className='quiz_list_contents_lightfont'>( 5 )</p>
                                </>
                               
                            )}

                            {txtErrorMsg &&(
                            <div className='quiz_list_contents_img_error' onClick={handleTxterror}>
                                <p>  <br/><br/>.</p>
                            </div>
                            )}
                          
                        </div>
                    </div>
                <div className='quiz_list_delete'  onClick={() => deleteItem(id)}><Delete/></div>
            </div>
    );
}

export default QuizItem;
