import React, {useState, useEffect } from 'react';
import '../makecontents.css';



import {ReactComponent as Delete} from '../../../../img/homepage/makecontents/delete_icon.svg';

function QuizItem({ answer, setAnswer ,id ,updateButton, updateQuiz, deleteItem, newQuizNumber,tfActiveButton,textCount,updateTextCount }) {
    
    const [txtErrorMsg, setTxtErrorMsg] = useState(false);
    // const [textCount, setTextCount] = useState(0); //   
    const [localTextCount, setLocalTextCount] = useState(textCount);

    const handleButtonClick = (value) => {
        updateButton(id,value);
    }
    const handleTextChange = (e) => {
        setLocalTextCount(e.target.value.length);
        if(e.target.value.length > 60 || /\.[a-z]+/.test(e.target.value)){
          setTxtErrorMsg(true);
        }else{
          setTxtErrorMsg(false);
        }
        updateQuiz(id, e.target.value);
      };
      
    
    const handleTxterror=()=>{
        setTxtErrorMsg(false);
    }

   

    useEffect(() => {
        let timer;
        if (txtErrorMsg) {
            timer = setTimeout(() => setTxtErrorMsg(false), 3000);
        }
        return () => clearTimeout(timer);
    }, [txtErrorMsg]);

    useEffect(() => {
        // textCount     
        updateTextCount(id, textCount);
      }, [textCount]);
      
      useEffect(() => {
        updateTextCount(id, localTextCount);
    }, [localTextCount]);


    return (
        <div className='make_modal_content_quiz_list_item' key={newQuizNumber}>
                <div className='quiz_list_header'>
                    <p>{` ${newQuizNumber}`}</p>
                    
                </div>
                    <div className='quiz_list_contents'>
                        <div className='quiz_list_contents_txtarea'>
                            <textarea 
                            onChange={handleTextChange}
                            value={answer}
                            placeholder='   ( 60 )'
                            maxLength={60}
                            ></textarea>
                            <div className='quiz_list_contents_txtarea_count'>{textCount}<span style={{color:'#AEAEAE'}}>/60</span></div>
                            {txtErrorMsg &&(
                            <div className='quiz_list_contents_img_error tf' onClick={handleTxterror}>
                                <p>  <br/><br/>.</p>                                
                            </div>
                            )}
                        </div>
                        <div className='quiz_list_contents_tfbtn_wrap'>
                        <div className={`quiz_list_contents_tfbtn ${tfActiveButton  === true ? 'active' : ''}`} onClick={() => handleButtonClick(true)}><p></p></div>
                    <div className={`quiz_list_contents_tfbtn ${tfActiveButton  === false ? 'active' : ''}`} onClick={() => handleButtonClick(false)}><p></p></div>
                        </div>
                    </div>
                <div className='quiz_list_delete'  onClick={() => deleteItem(id)}><Delete/></div>
            </div>
    );
}

export default QuizItem;
