import React, {useRef, useState, useEffect } from 'react';
import '../makecontents.css';
import QuizItem from './makequiz_multiple_item';

import {ReactComponent as Bgicon} from '../../../../img/homepage/makecontents/quiz_pic.svg';
import {ReactComponent as Close} from '../../../../img/homepage/makecontents/close.svg';
import {ReactComponent as Warning} from '../../../../img/homepage/makecontents/icon-warning.svg';
import {ReactComponent as Setting} from '../../../../img/homepage/makecontents/icon-settings.svg';
import {ReactComponent as Optionarrow} from '../../../../img/homepage/makecontents/option_arrow.svg';




function Makequiz({moduleTitle,listItems,setListItems,setQuizItemsState,useImageCnt, setUseImageCnt,multipleCnt, setMultipleCnt,goalAnswerCnt, setGoalAnswerCnt,queTime, setQueTime}){

    const [isModalOpen, setModalOpen] = useState(false);
    const [errorModalOpen, setErrorModalOpen] = useState(false);
    const isConfirmBtnActive = listItems.length > 0 && listItems.every(item => item.bgImage !== null && item.answer !== '' && item.answer.length<=5);

    const [apply, setapply] = useState(false); //

    const [useImageCntMenu, setUseImageCntMenu] = useState(false) // 
    const [multipleCntMenu, setMultipleCntMenu] = useState(false) //  
    const [goalAnswerCntMenu, setGoalAnswerCntMenu] = useState(false) // 
    const [queTimeMenu, setQueTimeMenu] = useState(false) //  
    
    const [keyReset, setKeyReset] = useState(0);

    const useImageCntRef = useRef(null);
    const multipleCntRef = useRef(null);
    const goalAnswerCntRef = useRef(null);
    const queTimeRef = useRef(null);



    const handleClick = () => {
        setModalOpen(!isModalOpen);
    }

    const handleCloseClick = () => {
        if(listItems.length > 0) {
            setErrorModalOpen(true); //   
            
        } else {
            setModalOpen(false); //    
        }
    }

    const handleErrorModalClose = () => {
        setErrorModalOpen(false);
       
        
    }

    const handleModalClose =()=>{
        setErrorModalOpen(false);
        setapply(false);
        setListItems([]);
        setQuizItemsState([]);
        setModalOpen(false);

    }
    


    const handleCloseAndConfirm = () => {
        if (isConfirmBtnActive) { 
            setModalOpen(!isModalOpen);
        }
    }

   

    const updateListItem = (id, newBgImage, newAnswer) => {
        setListItems(prevItems => prevItems.map(item => 
            item.id === id ? {...item, bgImage: newBgImage, answer: newAnswer} : item
        ));
    };

    const handleDropImageCnt =()=>{
        setUseImageCntMenu(!useImageCntMenu);
    }
    const handleDropMultipleCnt=()=>{
        setMultipleCntMenu(!multipleCntMenu);
    }
    const handleDropGoalCnt=()=>{
        setGoalAnswerCntMenu(!goalAnswerCntMenu);
    }
    const handleDropQueTimeCnt=()=>{
        setQueTimeMenu(!queTimeMenu);
    }

   
    const handleImageCntSelect = (number) => {
        setUseImageCnt(number);
        setUseImageCntMenu(false);
    }

    const handleMultipleCntSelect = (number) => {
        setMultipleCnt(number);  
        setMultipleCntMenu(false);
    }

    const handleGoalCntSelect = (number) => {
        setGoalAnswerCnt(number);  
        setGoalAnswerCntMenu(false);
    }

    const handleQueTimeCntSelect = (number) => {
        setQueTime(number);  
        setQueTimeMenu(false);
    }


   
    const handleApply = ()=>{      
        const itemCount = useImageCnt;  
        
        let newItems = Array.from({ length: itemCount }, (_, i) => ({
            id: i + 1,
            bgImage: null,
            answer: ''
        }));
           
        setListItems(newItems);
        setapply(true);      
        setKeyReset(prevKey => prevKey + 1);

    };

    const handleClickOutside1 = (event) => {
        if (useImageCntRef.current && !useImageCntRef.current.contains(event.target)) {
            setUseImageCntMenu(false);
        }
      };


    const handleClickOutside2 = (event) => {
        if (multipleCntRef.current && !multipleCntRef.current.contains(event.target)) {
            setMultipleCntMenu(false);
        }
      };

    const handleClickOutside3 = (event) => {
    if (goalAnswerCntRef.current && !goalAnswerCntRef.current.contains(event.target)) {
        setGoalAnswerCntMenu(false);
    }
    };

    const handleClickOutside4 = (event) => {
    if (queTimeRef.current && !queTimeRef.current.contains(event.target)) {
        setQueTimeMenu(false);
    }
    };

      useEffect(() => {
        document.addEventListener('click', handleClickOutside1);
        document.addEventListener('click', handleClickOutside2);
        document.addEventListener('click', handleClickOutside3);
        document.addEventListener('click', handleClickOutside4);
        return () => {
            document.removeEventListener('click', handleClickOutside1);
            document.removeEventListener('click', handleClickOutside2);
            document.removeEventListener('click', handleClickOutside3);
            document.removeEventListener('click', handleClickOutside4);
        };
      }, []);


    return(
        <>
        <div className='make_contents_quiz_wrap' onClick={handleClick}>
            <div className='make_contents_quiz1'>
            {listItems.length > 0 ?(
                <>
                <div className='make_contents_bg1_thumbnail'>
                    {listItems[0].bgImage && (
                        <img src={listItems[0].bgImage} alt="Quiz" />
                    )}
                     
                
                <div className='make_contents_bg1_thumbnail_circle'> <p></p> </div>
                </div>
                </>
                
                
            ):(
                <Bgicon/>
            ) }
                
            </div>
            <div className='make_contents_quiz2'>
                {listItems.length > 0 ? (
                    <>
                     <div className='make_contents_bgtitle1_wrap'>
                        <p className='make_contents_title1'> </p>
                    </div>  
                    </>
                ):(
                    <>
                    <p className='make_contents_title1'></p>
                <p className='make_contents_title2'>   <br/>   <br/>   .</p>
                    </>
                )}
                
            </div>
        </div>

        {isModalOpen && (
            <>
            <div className='overlay'></div>
            <div className='make_modal'>
                <div className='make_modal_header'>
                    <p>{moduleTitle}</p>
                    <div className='make_modal_header_close' onClick={handleCloseClick}>
                        <Close/>
                    </div>
                </div>
                <div className='make_modal_content_wrap'>
                    <div className='make_modal_nev'>
                        <div className='make_modal_nev_li active'>
                            <p> </p>
                        </div>
                        <div className='make_modal_content'>
                        <div className='make_modal_content_quiz_wrap'>
                            <div className='make_modal_content_quiz_left'>
                                <div className='make_modal_content_quiz_setting'>
                                    <div className='quiz_setting_header'>
                                        <div className='setting_icon'><Setting/></div>
                                        <div className='setting_title'> </div>
                                    </div>
                                    <div className='quiz_setting_contents'>
                                        <div className='quiz_setting_content'>
                                            <p className='quiz_setting_content_title'>  </p>
                                            <div className='quiz_setting_option'>
                                                <div className='quiz_setting_option_btn' style={{width: 5.8125+'rem', marginRight:0}} onClick={handleDropImageCnt} ref={useImageCntRef}>
                                                    <p>{useImageCnt}</p>
                                                    <Optionarrow/>

                                                    {useImageCntMenu && (
                                                        <div className='drop_down_number' style={{width: 5.8125+'rem'}}>
                                                            {[4, 5, 6, 7, 8, 9, 10].map(num => (
                                                                <div key={num} 
                                                                    className='drop_down_number_item' 
                                                                    onClick={() => handleImageCntSelect(num)}>
                                                                    {num}
                                                                </div>
                                                            ))}
                                                        </div>
                                                    )}
                                                    
                                                </div>
                                                <p className='quiz_setting_content_title'></p>
                                            </div>
                                            
                                        </div>
                                        <div className='quiz_setting_content'>
                                            <p className='quiz_setting_content_title'> </p>
                                            <div className='quiz_setting_option'>
                                                <div className='quiz_setting_option_btn'  style={{width: 5.8125+'rem', marginRight:0}} onClick={handleDropMultipleCnt} ref={multipleCntRef}>
                                                    <p>{multipleCnt}</p>
                                                    <Optionarrow/>

                                                    {multipleCntMenu &&(
                                                       <div className='drop_down_number'  style={{width: 5.8125+'rem', height:4.9+'rem'}}>
                                                       {[2,3,4].map(num => (
                                                           <div key={num} 
                                                               className='drop_down_number_item' 
                                                               onClick={() => handleMultipleCntSelect(num)}>
                                                               {num}
                                                           </div>
                                                       ))}
                                                   </div>
                                                    )}
                                                </div>
                                                <p className='quiz_setting_content_title'></p>
                                            </div>
                                           
                                        </div>  
                                        <div className='quiz_setting_content'>
                                            <p className='quiz_setting_content_title'>  </p>
                                            <div className='quiz_setting_option'>
                                                <div className='quiz_setting_option_btn'  style={{width: 5.8125+'rem', marginRight:0}} onClick={handleDropGoalCnt} ref={goalAnswerCntRef}>
                                                    <p>{goalAnswerCnt}</p>
                                                    <Optionarrow/>

                                                    {goalAnswerCntMenu &&(
                                                       <div className='drop_down_number'  style={{width: 5.8125+'rem'}}>
                                                       {[2,4,6,8,10].map(num => (
                                                           <div key={num} 
                                                               className='drop_down_number_item' 
                                                               onClick={() => handleGoalCntSelect(num)}>
                                                               {num}
                                                           </div>
                                                       ))}
                                                   </div>
                                                    )}
                                                </div>
                                                <p className='quiz_setting_content_title'></p>
                                            </div>
                                           
                                        </div> 

                                        <div className='quiz_setting_content'>
                                            <p className='quiz_setting_content_title'></p>
                                            <div className='quiz_setting_option'>
                                                <div className='quiz_setting_option_btn'  style={{width: 5.8125+'rem', marginRight:0}} onClick={handleDropQueTimeCnt}  ref={queTimeRef}>
                                                    <p>{queTime}</p>
                                                    <Optionarrow/>

                                                    {queTimeMenu &&(
                                                       <div className='drop_down_number'  style={{width: 5.8125+'rem'}}>
                                                       {[3,6,9,12,15,18,21,24,27,30].map(num => (
                                                           <div key={num} 
                                                               className='drop_down_number_item' 
                                                               onClick={() => handleQueTimeCntSelect(num)}>
                                                               {num}
                                                           </div>
                                                       ))}
                                                   </div>
                                                    )}
                                                </div>
                                                <p className='quiz_setting_content_title'></p>
                                            </div>
                                            
                                           
                                        </div>
                                               
                                    </div>
                                    
                                    <div className='quiz_setting_btn' onClick={handleApply}><p></p></div>
                                    
                                </div>
        
                            </div>
                            <div className='make_modal_content_quiz_right'>

                                {apply ? (
                                    <>
                                <div className='make_modal_content_quiz_list'>
                                   
                                    {

                                        [...listItems].reverse().map((item, index)=>(

                                            <QuizItem 
                                            key={item.id+keyReset} 
                                            id={item.id} 
                                            bgImage={item.bgImage} 
                                            answer={item.answer}                                             
                                            updateQuiz={updateListItem}
                                            newQuizNumber={listItems.length - index}
                                            />


                                        ))

                                    }
                                    
                                </div>
                                    </>
                                ):(
                                    <>
                                    <p className='content_quiz_before_font'>     .</p>
                                    </>
                                )}                            
                               

                            </div>
                        </div>


                        
                        
                    </div>
                    </div>
                    <div className='make_modal_footer'>
                                <div className={`make_modal_bg_confirm_btn ${isConfirmBtnActive  ? 'active' : ''}`} onClick={handleCloseAndConfirm}><p></p></div>

                        </div>
                </div>

                {errorModalOpen && (
                    <div className='error_modal-close'>
                    <div className='error_modal_header'>
                        <p> </p>
                        <div className='make_modal_header_close' onClick={handleErrorModalClose}>
                            <Close/>
                        </div>
                    </div>
                    <div className='error_modal-close_content_wrap'>
                        <div className='error_modal-close_content'>
                            <Warning/>
                            <p>      .<br/> ?</p>
                            
                        </div>
                        <div className='error_modal_footer'>
                            <div className='make_modal_bg_confirm_btn2-1' onClick={handleErrorModalClose}><p></p></div>
                            <div className='make_modal_bg_confirm_btn2-2   active' onClick={handleModalClose}><p></p></div>

                        </div>   
                    </div>
                    </div>
                )}
            </div>

            </>

        )}

        </>
        
    )
}

export default Makequiz;