import React, {useState, useRef, useEffect } from 'react';

import '../makecontents.css';

import {ReactComponent as Bgicon} from '../../../../img/homepage/makecontents/bg_pic.svg';
import {ReactComponent as Close} from '../../../../img/homepage/makecontents/close.svg';
import {ReactComponent as Greybg} from '../../../../img/homepage/makecontents/grey_bg.svg';
import {ReactComponent as Warning} from '../../../../img/homepage/makecontents/icon-warning.svg';

function Makebg({moduleTitle,selectedImage,setSelectedImage}){
  
    // const { moduleTitle} = useContext(MyContext);
    const [isModalOpen, setModalOpen] = useState(false);
    const [activeTab, setActiveTab] = useState('upload');
    const [activeIcon, setActiveIcon] = useState('mtc');
    const [bgImage, setBgImage] = useState(null); // 
    const [errorMessage, setErrorMessage] = useState(null);
    const [errorModalOpen, setErrorModalOpen] = useState(false);
    

    const handleClick = () => {
        setModalOpen(!isModalOpen);
    }

    const handleTabClick = (tab) => {
        setActiveTab(tab);
    }
    
    const handleIconClick = (Icon)=>{
        setActiveIcon(Icon);
    }


    const handleImageClick = (e) => {
        //   active  
        const imageElements = document.querySelectorAll('.make_modal_free_bg_contents_img');
        imageElements.forEach(imgDiv => imgDiv.classList.remove('active'));
    
        //   active  
        const imgDiv = e.currentTarget;
        imgDiv.classList.add('active');
    
        //   src  
        setSelectedImage(imgDiv.querySelector('img').src);
    }

    const fileInputRef = useRef(null); //    

    const handleUploadClick = () => {
        fileInputRef.current.click(); //    
    }

    const handleModifyClick = () => {
        if(fileInputRef.current) {
            fileInputRef.current.click(); //   input   
        }
    }


    const handleCloseAndConfirm = () => {
        if (selectedImage) { //     
            setModalOpen(false);
            setBgImage(selectedImage);
        }
    }

    const handleFileUpload = event => {
        const file = event.target.files[0];
        if (file) {  //       validateAndPreviewFile  
            validateAndPreviewFile(file);
        }
    }

    const validateAndPreviewFile = (file) => {
        if(file && !['image/jpeg', 'image/png'].includes(file.type)) {
            setErrorMessage("  . .jpg, .png   .");
        } else {
            const reader = new FileReader();
            reader.onloadend = () => {
                setSelectedImage(reader.result);
            }
            reader.readAsDataURL(file);
        }
    }

    const handleDrop = (event) => {
        event.preventDefault();
        const file = event.dataTransfer.files[0];
        validateAndPreviewFile(file);
    }

    const handleDragOver = (event) => {
        event.preventDefault();
    }

    const handleCloseClick = () => {
        if(selectedImage) {
            setErrorModalOpen(true);
        } else {
            setModalOpen(false);
        }
    }

    const handleErrorModalClose = () => {
        setErrorModalOpen(false);
        
    }

    const handleModalClose = () => {
        setErrorModalOpen(false);
        setModalOpen(false);
        setSelectedImage(null);
        setBgImage(null)
    }

    //      useEffect
    useEffect(() => {
        if(errorMessage) {
            setTimeout(() => {
                setErrorMessage(null);
            }, 3000);
        }
    }, [errorMessage]);

    return(
        <>
        
        <div className='make_contents_bg_wrap' onClick={handleClick}>
            <div className='make_contents_bg1'>
                {bgImage ? ( //    
                    <div className='make_contents_bg1_thumbnail'>
                        <img src={bgImage} alt=" " className='bgImage'/> 
                        <div className='make_contents_bg1_thumbnail_circle'> <p></p> </div>
                    </div>
                    
                ) : (
                    <Bgicon/> //      
                )}
            </div>
            <div className='make_contents_bg2'>
                {bgImage ? (
                    <>
                    <div className='make_contents_bgtitle1_wrap'>
                        <p className='make_contents_title1'> </p>
                    </div>   
                    </>
                    
                ):(
                    <>
                    <p className='make_contents_title1'> </p>
                    <p className='make_contents_title2'>    <br/>    <br/>  .</p>

                    </>
                    
                )}
                
            </div>
            
        </div>
        {isModalOpen && (
                <>
                <div className='overlay'></div>
                <div className='make_modal'>
                    <div className='make_modal_header'>
                        <p>{moduleTitle}</p>
                        <div className='make_modal_header_close' onClick={handleCloseClick}>
                            <Close/>
                        </div>
                    </div>
                    <div className='make_modal_content_wrap'>
                        <div className='make_modal_nev'>
                            <div className={activeTab === 'upload' ? 'make_modal_nev_li active' : 'make_modal_nev_li'} onClick={() => handleTabClick('upload')}>
                                <p>  </p>
                            </div>
                            <div  className={activeTab === 'free' ? 'make_modal_nev_li active' : 'make_modal_nev_li'} onClick={() => handleTabClick('free')}>
                                <p>  </p>
                            </div>
                        </div>
                        <div className='make_modal_content'>
                            {activeTab === 'upload' && (
                                <div className='make_modal_bg_upload_wrap'  onDrop={handleDrop} onDragOver={handleDragOver}>
                                    {errorMessage && <div className="error_wrong-image"><p>  . .jpg, .png   .</p></div>} 
                                    {/*   */}
                                    {selectedImage ? ( //     
                                        <>
                                        <div className='make_modal_bg_upload_img'>
                                            <img src={selectedImage} alt=" " />
                                        </div>
                                        <input type="file" ref={fileInputRef} style={{display: 'none'}} onChange={handleFileUpload} />
                                        <div className='make_modal_bg_modify_btn' onClick={handleModifyClick}></div>
                                        </>
                                        
                                         
                                    ):( //        

                                        <div className='make_modal_bg_upload'>
                                            
                                            <div><Greybg/></div>
                                            <p>   !<br/>(png, jpg )</p>
                                            <input type="file" ref={fileInputRef} style={{display: 'none'}} onChange={handleFileUpload} />
                                            <div className='make_modal_bg_upload_btn' onClick={handleUploadClick}> </div>
                                        </div>

                                    )}
                                    
                                </div>
                                
                            )}
                            {activeTab === 'free' && (
                                <div className='make_modal_free_bg'>
                                    <div className='make_modal_free_bg_icons'>
                                        <div className={activeIcon === 'mtc' ? 'make_modal_free_bg_icon active' : 'make_modal_free_bg_icon' } onClick={() => handleIconClick('mtc')}>
                                            <img src="http://13.125.65.215/BS/CommonRes/icon/common_icon_mtc.png" alt=""/>
                                        </div>
                                        <div className={activeIcon === 'omnom' ? 'make_modal_free_bg_icon active' : 'make_modal_free_bg_icon' } onClick={() => handleIconClick('omnom')}>
                                            <img src="http://13.125.65.215/BS/CommonRes/icon/common_icon_omnom.png" alt=""/>
                                        </div>
                                        <div className={activeIcon === 'poli' ? 'make_modal_free_bg_icon active' : 'make_modal_free_bg_icon' } onClick={() => handleIconClick('poli')}>
                                            <img src="http://13.125.65.215/BS/CommonRes/icon/common_icon_poli.png" alt=""/>
                                        </div>
                                        <div className={activeIcon === 'ppd' ? 'make_modal_free_bg_icon active' : 'make_modal_free_bg_icon' } onClick={() => handleIconClick('ppd')}>
                                            <img src="http://13.125.65.215/BS/CommonRes/icon/common_icon_ppd.png" alt=""/>
                                        </div>
                                    </div>
                                    <div className='make_modal_free_bg_contents_wrap'>
                                         <div className='make_modal_free_bg_contents'>
                                            {activeIcon === 'mtc' &&(
                                                <>
                                                <div className='make_modal_free_bg_contents_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/bg/mtc/common_bg_mtc_0.png" alt=""/>
                                                </div>
                                                <div className='make_modal_free_bg_contents_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/bg/mtc/common_bg_mtc_1.png" alt=""/>
                                                </div>
                                                <div className='make_modal_free_bg_contents_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/bg/mtc/common_bg_mtc_2.png" alt=""/>
                                                </div>
                                                <div className='make_modal_free_bg_contents_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/bg/mtc/common_bg_mtc_3.png" alt=""/>
                                                </div>
                                                <div className='make_modal_free_bg_contents_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/bg/mtc/common_bg_mtc_4.png" alt=""/>
                                                </div>
                                                <div className='make_modal_free_bg_contents_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/bg/mtc/common_bg_mtc_5.png" alt=""/>
                                                </div>
                                                </>
                                            )}
                                            {activeIcon === 'omnom' &&(
                                                <>
                                                <div className='make_modal_free_bg_contents_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/bg/omnom/common_bg_omnom_0.png" alt=""/>
                                                </div>
                                                <div className='make_modal_free_bg_contents_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/bg/omnom/common_bg_omnom_1.png" alt=""/>
                                                </div>
                                                <div className='make_modal_free_bg_contents_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/bg/omnom/common_bg_omnom_2.png" alt=""/>
                                                </div>
                                                <div className='make_modal_free_bg_contents_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/bg/omnom/common_bg_omnom_3.png" alt=""/>
                                                </div>
                                                <div className='make_modal_free_bg_contents_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/bg/omnom/common_bg_omnom_4.png" alt=""/>
                                                </div>
                                                <div className='make_modal_free_bg_contents_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/bg/omnom/common_bg_omnom_5.png" alt=""/>
                                                </div>
                                                </>
                                            )}
                                            {activeIcon === 'ppd' &&(
                                                <>
                                                <div className='make_modal_free_bg_contents_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/bg/pangpang/common_bg_ppd_0.png" alt=""/>
                                                </div>
                                                <div className='make_modal_free_bg_contents_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/bg/pangpang/common_bg_ppd_1.png" alt=""/>
                                                </div>
                                                <div className='make_modal_free_bg_contents_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/bg/pangpang/common_bg_ppd_2.png" alt=""/>
                                                </div>
                                                <div className='make_modal_free_bg_contents_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/bg/pangpang/common_bg_ppd_3.png" alt=""/>
                                                </div>
                                                <div className='make_modal_free_bg_contents_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/bg/pangpang/common_bg_ppd_4.png" alt=""/>
                                                </div>
                                                <div className='make_modal_free_bg_contents_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/bg/pangpang/common_bg_ppd_5.png" alt=""/>
                                                </div>
                                                </>
                                            )}
                                            {activeIcon === 'poli' &&(
                                                <>
                                                 <div className='make_modal_free_bg_contents_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/bg/poli/common_bg_poli_0.png" alt=""/>
                                                </div>
                                                <div className='make_modal_free_bg_contents_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/bg/poli/common_bg_poli_1.png" alt=""/>
                                                </div>
                                                <div className='make_modal_free_bg_contents_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/bg/poli/common_bg_poli_2.png" alt=""/>
                                                </div>
                                                <div className='make_modal_free_bg_contents_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/bg/poli/common_bg_poli_3.png" alt=""/>
                                                </div>
                                                <div className='make_modal_free_bg_contents_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/bg/poli/common_bg_poli_4.png" alt=""/>
                                                </div>
                                                <div className='make_modal_free_bg_contents_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/bg/poli/common_bg_poli_5.png" alt=""/>
                                                </div>
                                                </>
                                            )}

                                         </div>

                                    </div>
                                </div>
                            )}
                            
                            
                        </div>
                        <div className='make_modal_footer'>
                                <div className={`make_modal_bg_confirm_btn ${selectedImage ? 'active' : ''}`} onClick={handleCloseAndConfirm}><p></p></div>
                            </div>
                    </div>

                    {errorModalOpen && (

                        <div className='error_modal-close'>
                        <div className='error_modal_header'>
                            <p> </p>
                            <div className='make_modal_header_close' onClick={handleErrorModalClose}>
                                <Close/>
                            </div>
                        </div>
                        <div className='error_modal-close_content_wrap'>
                            <div className='error_modal-close_content'>
                                <Warning/>
                                <p>      .<br/> ?</p>
                                
                            </div>
                            <div className='error_modal_footer'>
                                <div className='make_modal_bg_confirm_btn2-1' onClick={handleErrorModalClose}><p></p></div>
                                <div className='make_modal_bg_confirm_btn2-2   active' onClick={handleModalClose}><p></p></div>

                            </div>   
                        </div>
                        </div>

                    )}

                </div>
                
                </>

            )}
           
            
             
        </>
    )
}

export default Makebg;

