import React, {useState, useEffect, useRef } from 'react';
import '../makecontents.css';



import {ReactComponent as Delete} from '../../../../img/homepage/makecontents/delete_icon.svg';
import {ReactComponent as Bgiconsmall} from '../../../../img/homepage/makecontents/bg-pic_small.svg';

function QuizItem({ id , updateQuiz, deleteItem, newQuizNumber,bgImage }) {
    const [imgErrorMsg, setImgErrorMsg] = useState(false);
    const fileInputRef = useRef(null);
   
    const handleDivClick = () => {
        fileInputRef.current.click();
    };

    const handleFileChange = (e) => {
        const file = e.target.files[0];
        if (file && (file.type === 'image/png' || file.type === 'image/jpeg')) {
            const reader = new FileReader();
            reader.onloadend = () => {
                updateQuiz(id, reader.result);
            };
            reader.readAsDataURL(file);
        } else {
            setImgErrorMsg(true);
            e.target.value = null;
            }
            
    };

    const handleImgerror=()=>{
        setImgErrorMsg(false);
    }

    const handleDrop = (event) => {
        event.preventDefault();
        const file = event.dataTransfer.files[0];
        if (file && (file.type === 'image/png' || file.type === 'image/jpeg')) {
            const reader = new FileReader();
            reader.onloadend = () => {
                updateQuiz(id, reader.result);
            };
            reader.readAsDataURL(file);
        } else {
            setImgErrorMsg(true);
        }
    };
    
    const handleDragOver = (event) => {
        event.preventDefault();
    };

    useEffect(() => {
        let timer;
        if (imgErrorMsg) {
            timer = setTimeout(() => setImgErrorMsg(false), 3000);
        }
        return () => clearTimeout(timer);
    }, [imgErrorMsg]);

    return (
        <div className='make_modal_content_quiz_list_item puzzle_list' key={newQuizNumber}>
                <div className='quiz_list_header'>
                    <p>{` ${newQuizNumber}`}</p>
                    
                </div>
                    <div className='quiz_list_contents puzzle_list'>
                        <div className='quiz_list_contents_img puzzle_list'  onDrop={handleDrop} onDragOver={handleDragOver}>
                            {bgImage ? (
                                <>
                                 <img src={bgImage} alt="preview" style={{maxWidth: '100%', maxHeight: '100%'}}/> 
                                 <input type="file" ref={fileInputRef} onChange={handleFileChange} style={{display: 'none'}}  />
                                 <div className='quiz_list_contents_imgbtn2' onClick={handleDivClick}><p></p></div>

                                </>
                                

                            ):(
                                <>
                                 <div><Bgiconsmall/></div>
                                <p className='quiz_list_contents_font'> <br/>  </p>
                                <input type="file" ref={fileInputRef} onChange={handleFileChange} style={{display: 'none'}}  />
    
                                <div className='quiz_list_contents_imgbtn1' onClick={handleDivClick}><p></p></div>

                                </>
                               

                            )}
                           

                            {imgErrorMsg &&(
                                <div className='quiz_list_contents_img_error' onClick={handleImgerror}>
                                    <p>  .<br/> .jpg, .png <br/> .</p>
                                </div>
                            )}
                                
                            
                        </div>
                        
                    </div>
                <div className='quiz_list_delete'  onClick={() => deleteItem(id)}><Delete/></div>
            </div>
    );
}

export default QuizItem;
