import React, { useContext, useEffect, useState } from "react";
// import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import "./makecontents.css";
import { MyContext } from "../../../routes/homepage2";

import Makebg from "./makecontentsbtn/makebg";
import Makecard from "./makecontentsbtn/makecard";
import Makequiz from "./makecontentsbtn/makequiz_memory";
import Makebgm from "./makecontentsbtn/makebgm";


function MemoryQuizContents() {
  const {
  
    moduleTitles,
    moduleIntros,

    selectedImage,
    setSelectedImage, //

    selectedCard, setSelectedCard,//

    

    listItems, setListItems, quizItemsState, setQuizItemsState,cardPiece, setCardPiece, cardoption, setCardOption, //

    setProjectItems, projectItems,
    selectAudio, setSelectAudio, //bgm


    resetValues, //  
  } = useContext(MyContext);
  const navigate = useNavigate();

  const [showError, setShowError] = useState(false); //     
  const [count, setCount] = useState(0); //   count
  const [gameTitle, setGameTitle] = useState('');

  const [isActive, setIsActive] = useState(true);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const handleInputChange = (e) => { //   
  const inputLength = e.target.value.length;
    setCount(inputLength); //   
    setGameTitle(e.target.value);
    
  }


async function getUserName() {
  const url = 'http://13.125.65.215/BS/GetCookie_userID.php';
  
  try {
    const response = await fetch(url);
    if (!response.ok) throw new Error('Network response was not ok');
    const data = await response.text();
    // console.log(`User Name: ${data}`);
    return data;
  } catch (error) {
    throw error;
  }
}

async function convertToFile(data, filename) {
  let response;
  let blob;
  let file;
  let mimeType;

  if (data.startsWith('http') || data.startsWith('https')) {
    // URL 
    response = await fetch(data);
    blob = await response.blob();
    mimeType = blob.type;
  } else if (data.startsWith('data:')) {
    // Base64  
    const base64Data = data.split(',')[1];
    mimeType = data.split(';')[0].split(':')[1];  // MIME  
    const byteCharacters = atob(base64Data);
    const byteNumbers = new Array(byteCharacters.length);
    for (let i = 0; i < byteCharacters.length; i++) {
      byteNumbers[i] = byteCharacters.charCodeAt(i);
    }
    const byteArray = new Uint8Array(byteNumbers);
    blob = new Blob([byteArray], { type: mimeType });
  }

  if (blob) {
    // MIME   
    const extension = mimeType.split('/')[1];
    //    
    const fullFilename = `${filename}.${extension}`;
    file = new File([blob], fullFilename, { type: mimeType });  // MIME   File  
  }

  return file;
}

async function fetchProjectInfoLength(userName) {
  const url = `http://13.125.65.215/BS/GetProjectInfoLength.php?folderPath=${userName}`;
  const response = await fetch(url);
  const data = await response.text();
  // console.log(url);
  return parseInt(data); //   
}


async function checkAndCreateProjectInfoFile(userName) {
  let num = await fetchProjectInfoLength(userName);

  const url = `http://13.125.65.215/BS/GetProjectInfo.php?folderPath=${userName}`;
  // console.log(url);
  let existingProjectInfo;
  let response;

  let ip_idx_value; //  
  let bgImageFileName = "";

  let bgm_ip_idx_value; //  
  let bgmFileName = "";

  let bgImageFile;
  let bgmFile;

  let card_ip_idx_value; //  
  let cardFileName = "";
  let cardFile;

  let pieces = cardPiece.split('X');
  let boardSize = parseInt(pieces[0]);  //     
  let cardCount = boardSize * parseInt(pieces[1]);  //      
 
  let options = cardoption.split('-');  // '-'  

  
  let memorygameType = options.map(option => {
  if (option === "") {
    return "image";
  } else if (option === "") {
    return "text";
  }
}).join('_');  //    '_' 


  // folder_path 
  //  let folderPath = 'oxgameinfo_0';
  let folderPath = `memorygameinfo_${num}`
  try {
    response = await fetch(url);
    const data = await response.text();
  if (data) {
    try {
      //   JSON 
      // existingProjectInfo= JSON.parse(data);
      // console.log(`Data from project_info.json: ${data}`);
      
      
      if (num<=0) {
        existingProjectInfo.project_info = [];
      }else{
        existingProjectInfo = { "project_info": [] };

        let newProjectData = {
          "p_name": gameTitle,  // gameTitle state 
          "folder_path": folderPath,  // postData folder_path  
          "m_type": "memorygame"  //    
        }
      
        //    
        existingProjectInfo.project_info.push(newProjectData);
        setProjectItems(prevItems => [...prevItems, newProjectData]);
        // console.log('projectItems: ',projectItems);

      }
    } catch (error) {
      // JSON.parse()     
      // console.log('Error parsing data:', error);
      existingProjectInfo = { "project_info": [] };
    }
  } else {
    // project_info.json   ,  
    existingProjectInfo = { "project_info": [] };

    let newProjectData = {
      "p_name": gameTitle,  // gameTitle state 
      "folder_path": folderPath,  // postData folder_path  
      "m_type": "memorygame"  //    
    }
  
    //    
    existingProjectInfo.project_info.push(newProjectData);
    setProjectItems(prevItems => [...prevItems, newProjectData]);
    // console.log('projectItems: ',projectItems);
    
  }


    let files = []; //   
   

    let itemFilesPromise = listItems.map(async (item, index) => {
      let filePromises = [];
    
      //   'bgImage1' 'bgImage2' 
      for (let i = 1; i <= 2; i++) {
        let key = `bgImage${i}`;
        if (item[key]) {
          let file = await convertToFile(item[key], `quizItemImage_${index}_${i}`);
          filePromises.push(file);
        }
      }
    
      return Promise.all(filePromises);
    });
  
    
  
    let itemFiles = await Promise.all(itemFilesPromise);
    itemFiles = itemFiles.flat();
    files = [...files, ...itemFiles];


    // //     
    // let quizImageNames = itemFiles.map(file => file.name);

    // // //   
    // let quizAnswers = listItems.map(item => item.answer);

    // selectedImage base64  
    if (selectedImage && typeof selectedImage === "string") {
      if (selectedImage.startsWith("data:image")) {
        ip_idx_value = -1;
        bgImageFile = await convertToFile(selectedImage, 'bgImage'); //
        bgImageFileName = bgImageFile.name; //    
        files.push(bgImageFile); //  
      } else if (selectedImage.startsWith("http")) {
        const urlParts = selectedImage.split("/");
        bgImageFileName = urlParts[urlParts.length - 1]; // URL   
        // ip_idx_value = parseInt(bgImageFileName.match(/\d+/)[0]); //    
        if (selectedImage.includes("/mtc/")) {
          ip_idx_value = 0;
        } else if (selectedImage.includes("/omnom/")) {
          ip_idx_value = 1;
        } else if (selectedImage.includes("/poli/")) {
          ip_idx_value = 2;
        } else if (selectedImage.includes("/pangpang/")) {
          ip_idx_value = 3;
        }
      }
    }

    if (selectedCard && typeof selectedCard === "string") {
      if (selectedCard.startsWith("data:image")) {
        card_ip_idx_value = -1;
        cardFile = await convertToFile(selectedCard, 'cardImage'); //
        cardFileName = cardFile.name; //    
        files.push(cardFile); //  
      } else if (selectedCard.startsWith("http")) {
        const urlParts = selectedCard.split("/");
        cardFileName = urlParts[urlParts.length - 1]; // URL   
        // ip_idx_value = parseInt(bgImageFileName.match(/\d+/)[0]); //    
        if (selectedCard.includes("/mtc/")) {
          card_ip_idx_value = 0;
        } else if (selectedCard.includes("/omnom/")) {
          card_ip_idx_value = 1;
        } else if (selectedCard.includes("/poli/")) {
          card_ip_idx_value = 2;
        } else if (selectedCard.includes("/pangpang/")) {
          card_ip_idx_value = 3;
        }
      }
    }


     // selectAudio URL   
  if (selectAudio && typeof selectAudio === 'string') {
    if (selectAudio.startsWith('http')) {
      const urlParts = selectAudio.split("/");
      bgmFileName = urlParts[urlParts.length - 1]; // URL   
      // bgm_ip_idx_value = parseInt(bgmFileName.match(/\d+/)[0]); //    
      if (selectAudio.includes("/mtc/")) {
        bgm_ip_idx_value = 0;
      } else if (selectAudio.includes("/omnom/")) {
        bgm_ip_idx_value = 1;
      } else if (selectAudio.includes("/poli/")) {
        bgm_ip_idx_value = 2;
      } else if (selectAudio.includes("/pangpang/")) {
        bgm_ip_idx_value = 3;
      }
    }
  } else if (selectAudio instanceof File) {
    bgm_ip_idx_value = -1;
    bgmFileName = selectAudio.name; //    
    bgmFile = selectAudio; //    
    files.push(bgmFile); //  
  }

  


    const newModuleInfo={
      "module_type": "memorygame",
      "memory_bg_info": {
        "ip_idx": ip_idx_value,
        "bg": bgImageFileName
      },
      "memory_bgm_info": {
        "ip_idx": bgm_ip_idx_value,
        "bgm": bgmFileName
      },
      "memory_card_back": {
        "ip_idx": card_ip_idx_value,
        "card_back": cardFileName
      },
      "board_size": boardSize, //3x2 / 7x2 / 6x3 / 5x4 / 6x4 / 7x4
      "card_count": cardCount, //   
      "memorygame_type": memorygameType, //image_image, image_text, text_text
      "memory_card_info": {
        "image_list": [],
        "text_list": []
      }
    }
  

    for (let i = 0; i < listItems.length; i++) {
      let item = listItems[i];
      
      // 'bgImage1'  'answer2'  'image_list' 'bgImage1'    'text_list' 'answer2' .
      if (item.bgImage1 && item.answer2) {
        let file = files.find(file => file.name.startsWith(`quizItemImage_${i}_1`)); //   
        newModuleInfo.memory_card_info.image_list.push(file.name); //     
        newModuleInfo.memory_card_info.text_list.push(item.answer2); //  
      }
      
      // 'bgImage1'  'bgImage2'  'image_list' 'bgImage1'    'text_list' 'bgImage2'   .
      if (item.bgImage1 && item.bgImage2) {
        let fileQuiz = files.find(file => file.name.startsWith(`quizItemImage_${i}_1`)); //   
        let fileAnswer = files.find(file => file.name.startsWith(`quizItemImage_${i}_2`)); //   
        newModuleInfo.memory_card_info.image_list.push(fileQuiz.name); //     
        newModuleInfo.memory_card_info.text_list.push(fileAnswer.name); //     
      }

      // 'answer1'  'answer2'  'image_list' 'answer1'  'text_list' 'answer2' .
      if (item.answer1 && item.answer2) {
        newModuleInfo.memory_card_info.image_list.push(item.answer1); //  
        newModuleInfo.memory_card_info.text_list.push(item.answer2); //  
      }
    }

    // moduleinfo.json  
    const moduleInfoFile = new File([JSON.stringify(newModuleInfo)], "moduleinfo.json", {type: "application/json"});
    
    //   moduleinfo.json  
    files.push(moduleInfoFile);

    const postData = new FormData();  // FormData  
    postData.append('userID', userName);
    postData.append('folder_path', folderPath);
    postData.append('projectInfo', JSON.stringify(existingProjectInfo));



    files.forEach((file, index) => {
      postData.append(`file[${index}]`, file); //   'file[index]' key 
    });

    const postUrl = 'http://13.125.65.215/BS/UploadFiles.php';
    const postResponse = await fetch(postUrl, {
      method: 'POST',
      body: postData,  // FormData  body 
    });
    const createData = await postResponse.text();

    
    // console.log(`Created Project Info: ${createData}`);
    return createData;  //  
  } catch (error) {
    console.error('Error:', error);
    throw error;
  }
}



const handleDoneClick = async () => {  // async  
  if (isSubmitting) return; //      

  if (gameTitle && gameTitle.length <= 20) {
      try {
        setIsSubmitting(true); //  
          const userName = await getUserName();  // await  
          const projectInfo = await checkAndCreateProjectInfoFile(userName);  // await  
          setGameTitle(''); //   
          setIsActive(false); //  active  
            
      } catch (error) {
          console.error(error);
      } finally {
        setIsSubmitting(false); //   
      }
  }
  navigate('/logged/make_done');
}
  
  useEffect(() => {
    if (count > 20) {
        setShowError(true);
        const timer = setTimeout(() => {
            setShowError(false);
        }, 3000);
        return () => clearTimeout(timer);
    } else {
        setShowError(false);
    }
}, [count]); // count   
  

  useEffect(() => {
    //    
    return () => {
      resetValues(); //    
    }
  }, []);

  useEffect(() => {
    const handleBeforeUnload = (e) => {
      if (selectedImage !== null || 
        selectedCard !== null ||
        listItems.length > 0 ||
         quizItemsState.length > 0 ||
         selectAudio !==null ) { //
        e.preventDefault();
        e.returnValue = '';
      }
    };
  
    window.addEventListener('beforeunload', handleBeforeUnload);
  
    return () => {
      
      window.removeEventListener('beforeunload', handleBeforeUnload);
      
    };
  }, [selectedImage,selectedCard,listItems.length,quizItemsState.length,selectAudio]); // selectedImage,text    effect .

  // useEffect(() => {
  //   console.log('selectedImage:', selectedImage !== null);
  //   console.log(selectedImage);
  //   console.log('listItems:', listItems.length > 0);
  //   console.log(listItems);
  //   console.log('selectAudio:', selectAudio !== null);

  // }, [selectedImage, listItems, quizItemsState, selectAudio]);

//   useEffect(() => {
//     const image_list = listItems.map(item => item.bgImage1 || item.answer1 || '');
//     const text_list = listItems.map(item => item.answer2 || item.bgImage2 || '');
//     console.log('image_list:', image_list);
//     console.log('text_list:', text_list);
// }, [listItems]);

 
  

  return (
    <>
      <div className="make_header_wrap">
        <div className="make_header">
          <div className="make_title">
            <p>{moduleTitles[2]}</p>
          </div>
          <div className="make_intro">
            <p>{moduleIntros[2]}</p>
          </div>
        </div>

        <div className="make_contents">
          <div className="make_contents_item">
            <Makebg
              selectedImage={selectedImage}
              setSelectedImage={setSelectedImage}
              moduleTitle={moduleTitles[2]}
            />
          </div>

          <div className="make_contents_item">
            <Makecard
            selectedCard={selectedCard}
            setSelectedCard={setSelectedCard}
            moduleTitle={moduleTitles[2]}
            />
          </div>

          <div className="make_contents_item">
            <Makequiz
            listItems={listItems}
            setListItems={setListItems}
            quizItemsState={quizItemsState}
            setQuizItemsState={setQuizItemsState}
            cardPiece={cardPiece} setCardPiece={setCardPiece}cardoption={cardoption} setCardOption={setCardOption}
            moduleTitle={moduleTitles[2]}/>
          </div>


          <div className="make_contents_item">
            <Makebgm 
            selectAudio={selectAudio} 
            setSelectAudio={setSelectAudio}
            moduleTitle={moduleTitles[2]} />
          </div> 
        </div>
        {selectedImage !== null && 
        selectedCard !==null &&
        listItems.length > 0 &&
        selectAudio !== null && (
          <>

            <div className="makecontents_done_wrap">
            <div className="makecontents_done_input">
              <input 
              type="text" 
              className="makecontents_done_title" 
              placeholder="   .(20 )" 
              onChange={handleInputChange}
              maxLength={20}></input>
              <div 
              className={`makecontents_done_btn ${gameTitle && gameTitle.length <=20 ? 'active' : ''}`}
              onClick={!isSubmitting && gameTitle && gameTitle.length <=20 ? handleDoneClick : undefined}
              >
                <p></p>
              </div>
            </div>
            {showError &&(
              <>
              <p className="title_error_overcount">    </p>
              </>
            )}
            <p className="makecontents_done_caution"></p>
            <ul>
              <li>    .</li>
              <li>     .   .</li>
            </ul>
              
            </div>
            
          </>
          )}
          
      </div>
      
      
    </>
  );
}

export default MemoryQuizContents;
