import React, {useState , useEffect, useRef} from 'react';
import '../makecontents.css';

import {ReactComponent as Bgicon} from '../../../../img/homepage/makecontents/quiz_pic.svg';
import {ReactComponent as Close} from '../../../../img/homepage/makecontents/close.svg';
import {ReactComponent as Warning} from '../../../../img/homepage/makecontents/icon-warning.svg';
import {ReactComponent as Setting} from '../../../../img/homepage/makecontents/icon-settings.svg';
import {ReactComponent as Optionarrow} from '../../../../img/homepage/makecontents/option_arrow.svg';

import {ReactComponent as Mathplus} from '../../../../img/homepage/makecontents/math_plus.svg';
import {ReactComponent as Mathmultiply} from '../../../../img/homepage/makecontents/math_multiply.svg';
import {ReactComponent as Mathdivide} from '../../../../img/homepage/makecontents/math_devide.svg';
import {ReactComponent as Checkboxon} from '../../../../img/homepage/makecontents/blue_checkboxon.svg';





function Makequiz({
    mathExForm, setMathExForm, 
    mathAnswerNumber, setMathAnswerNumber,
    mathTimeNumber, setMathTimeNumber,
    mathMinValue, setMathMinValue,
    mathMaxValue, setMathMaxValue,
    // checkSelected, setCheckSelected,
    checkPlus,setCheckPlus,checkMinus,setCheckMinus,checkMultiply,setCheckMultiply,checkDivide,setCheckDivide,
    moduleTitle,
}){

    // const {moduleTitle} = useContext(MyContext);
    const [isModalOpen, setModalOpen] = useState(false);
    const [errorModalOpen, setErrorModalOpen] = useState(false);
    const [errormsg1, setErrormsg1] = useState(null);
    const [errormsg2, setErrormsg2] = useState(null);
    const [answerNumberMenu, setAnswerNumberMenu] = useState(false) //  
    const [timeNumberMenu, setTimeNumberMenu] = useState(false) //   
    const [formMenu, setFormMenu] = useState(false) //  
    const [isValid, setIsValid] = useState(false);
   
   
    const answerNumberRef = useRef(null);
    const timeNumberRef = useRef(null);
    const formRef = useRef(null);
   


    const handleClick = () => {
        setModalOpen(!isModalOpen);
    }

    const handleCloseClick = () => {
        if(mathMinValue || mathMaxValue) {
            setErrorModalOpen(true); //   
            
        } else {
            setModalOpen(false); //    
        }
    }

    const handleErrorModalClose = () => {
        setErrorModalOpen(false);
        
    }

    const handleModalClose =()=>{
        setErrorModalOpen(false);
        setModalOpen(false);
        setMathExForm('');
        setMathAnswerNumber(2);
        setMathTimeNumber(10);
        setMathMinValue('');
        setMathMaxValue('');
        // setCheckSelected(1);
        setCheckPlus(true);
    }

    const handleCloseAndConfirm = () => {
            setModalOpen(!isModalOpen);
    
    }

   

    const handleDropNumber = ()=>{
        setAnswerNumberMenu(!answerNumberMenu);
    }
    const handleDropTime = ()=>{
        setTimeNumberMenu(!timeNumberMenu)
    }

    const handleDropform = ()=>{
        setFormMenu(!formMenu)
    }

    const handleFormSelect = (form) => {
        setMathExForm(form);
        setFormMenu(false);
    }

    const handleNumberSelect = (number) => {
        setMathAnswerNumber(number);
        setAnswerNumberMenu(false);
       
    }

    const handleTimeSelect = (number) => {
        setMathTimeNumber(number);
        setTimeNumberMenu(false);
    }


    const handleMinChange = (e) => {
        let value = e.target.value;
    
        if (value !=='' && (isNaN(value) || value.includes('.'))) {
            setErrormsg1('  .');
            // setIsValid(false);
            return;
        }
    
        if (value !=='' && (parseInt(value) < 1 || parseInt(value) > 99)) {
            setErrormsg1('1 99  .');
            setIsValid(false);
            return;
        }
    
        setMathMinValue(value);
    
        if (value !=='' && parseInt(value) > parseInt(mathMaxValue)) {
            setErrormsg1('   .');
            setIsValid(false);
        } else {
            setErrormsg1(null);
            setErrormsg2(null);
    
            if(mathMaxValue !== '' && !isNaN(mathMaxValue) && value !== ''){
                setIsValid(true)
            } else {
                setIsValid(false);
            }
        }
    };
    
    const handleMaxChange = (e) => {
        let value = e.target.value;
    
        if (value !=='' && isNaN(value)) {
            setErrormsg1('  .');
            return;
        }
    
        if (value !=='' && (parseInt(value) < 1 || parseInt(value) > 99)) {
            setErrormsg1('1 99  .');
            return;
        }
    
        setMathMaxValue(value);
    
        if (value !=='' && parseInt(value) < parseInt(mathMinValue)) {
            setErrormsg1('   .');
            setIsValid(false);
        } else {
            setErrormsg1(null);
            setErrormsg2(null);
    
            if(mathMinValue !== '' && !isNaN(mathMinValue) && value !== ''){
                setIsValid(true)
            } else {
                setIsValid(false);
            }
        }
    };

    const handleClickOutside1 = (event) => {
        if (answerNumberRef.current && !answerNumberRef.current.contains(event.target)) {
            setAnswerNumberMenu(false);
        }
      };


    const handleClickOutside2 = (event) => {
        if (timeNumberRef.current && !timeNumberRef.current.contains(event.target)) {
            setTimeNumberMenu(false);
        }
      };

      const handleClickOutside3 = (event) => {
        if (formRef.current && !formRef.current.contains(event.target)) {
            setFormMenu(false);
        }
      };

      useEffect(() => {
        document.addEventListener('click', handleClickOutside1);
        document.addEventListener('click', handleClickOutside2);
        document.addEventListener('click', handleClickOutside3);
        return () => {
            document.removeEventListener('click', handleClickOutside1);
            document.removeEventListener('click', handleClickOutside2);
            document.removeEventListener('click', handleClickOutside3);
        };
      }, []);
    

    
      useEffect(()=>{
        if(errormsg1||errormsg2){
            setTimeout(()=>{
                setErrormsg1(null);
                setErrormsg2(null);
            },3000)
        }
      },[errormsg1,errormsg2])

    //   useEffect(() => {
    //     console.log("checkPlus: ", checkPlus);
    //     console.log("checkMinus: ", checkMinus);
    //     console.log("checkMultiply: ", checkMultiply);
    //     console.log("checkDivide: ", checkDivide);
    // }, [checkPlus, checkMinus, checkMultiply, checkDivide]);



    return(
        <>
        <div className='make_contents_quiz_wrap' onClick={handleClick}>
            <div className='make_contents_quiz1'>
            
            {mathMinValue && mathMaxValue  ? ( //    
                    <div className='make_contents_bg1_thumbnail'>
                        <div style={{position:'absolute', top:30+'%' , left:36+'%'}}>
                            <Bgicon/>
                        </div>
                         
                        <div className='make_contents_bg1_thumbnail_circle'> <p></p> </div>
                    </div>
                    
                ) : (
                    <Bgicon/> //      
                )}
            
                
            </div>
            <div className='make_contents_quiz2'>
                
                    
                {mathMinValue && mathMaxValue ? (
                    <>
                    <div className='make_contents_bgtitle1_wrap'>
                        <p className='make_contents_title1'> </p>
                    </div>   
                    </>
                    
                ):(
                    <>
                    <p className='make_contents_title1'> </p>
                    <p className='make_contents_title2'>   <br/>   <br/>   .</p>

                    </>
                    
                )}
                   
                
            </div>
        </div>

        {isModalOpen && (
            <>
            <div className='overlay'></div>
            <div className='make_modal'>
                <div className='make_modal_header'>
                    <p>{moduleTitle}</p>
                    <div className='make_modal_header_close' onClick={handleCloseClick}>
                        <Close/>
                    </div>
                </div>
                <div className='make_modal_content_wrap'>
                    <div className='make_modal_nev'>
                        <div className='make_modal_nev_li active'>
                            <p> </p>
                        </div>
                    </div>

                    <div className='make_modal_content'>
                        <div className='make_modal_mathquiz_wrap'>
                            <div className='make_modal_mathquiz'>
                                <div className='make_modal_mathquiz_header'>
                                    <div className='setting_icon'><Setting/></div>
                                    <div className='setting_title'> </div>
                                </div>

                                <div className='make_modal_mathquiz_contents'>
                                <div className='make_modal_mathquiz_left'>
                                    <div className='make_modal_mathquiz_left_item'>
                                    <Mathplus />
                                    <div className='make_modal_mathquiz_checkbox' 
                                        style={{ marginLeft: 0.63+'rem' }}
                                        onClick={() => {
                                            if (!checkPlus || checkMinus || checkMultiply || checkDivide) {
                                                setCheckPlus(prev => !prev);
                                            }
                                        }}
                                    >
                                        {checkPlus && <Checkboxon className='make_modal_mathquiz_checkbox_checked'/>}
                                    </div>
                                    </div>
                                    <div className='make_modal_mathquiz_left_item'>
                                    <div className='make_modal_mathquiz_left_item_minus'></div> 
                                    <div className='make_modal_mathquiz_checkbox' 
                                        style={{ marginLeft: 0.63+'rem' }}
                                        onClick={() => {
                                            if (!checkMinus || checkPlus || checkMultiply || checkDivide) {
                                                setCheckMinus(prev => !prev);
                                            }
                                        }}
                                    >
                                        {checkMinus && <Checkboxon className='make_modal_mathquiz_checkbox_checked'/>}
                                    </div>
                                    </div>
                                    <div className='make_modal_mathquiz_left_item'>
                                    <Mathmultiply />
                                    <div className='make_modal_mathquiz_checkbox' 
                                        style={{ marginLeft: 0.5+'rem' }}
                                        onClick={() => {
                                            if (!checkMultiply || checkMinus || checkPlus || checkDivide) {
                                                setCheckMultiply(prev => !prev);
                                            }
                                        }}
                                    >
                                        {checkMultiply && <Checkboxon className='make_modal_mathquiz_checkbox_checked'/>}
                                    </div>
                                    </div>
                                    <div className='make_modal_mathquiz_left_item'>
                                    <Mathdivide />
                                    <div className='make_modal_mathquiz_checkbox' 
                                        style={{ marginLeft: 0.63+'rem' }}
                                        onClick={() => {
                                            if (!checkDivide || checkMinus || checkMultiply || checkPlus) {
                                                setCheckDivide(prev => !prev);
                                            }
                                        }}
                                    >
                                        {checkDivide && <Checkboxon className='make_modal_mathquiz_checkbox_checked'/>}
                                    </div>
                                    </div>
                                </div>

                                    <div className='make_modal_mathquiz_right'>
                                        <div className='make_modal_mathquiz_right_item_wrap up' style={{marginBottom:0.7+'rem'}}>
                                            <div className='make_modal_mathquiz_right_item'>
                                                <p></p>
                                                <div className='quiz_setting_option_btn' onClick={handleDropform} ref={formRef}>
                                                <p>{mathExForm}</p>
                                                  <Optionarrow/>
                                                {formMenu &&(
                                                       <div className='drop_down_number' style={{height:3.875+'rem'}}>
                                                       {['', ''].map(num => (
                                                           <div key={num} 
                                                               className='drop_down_number_item' 
                                                               onClick={() => handleFormSelect(num)}>
                                                               {num}
                                                           </div>
                                                       ))}
                                                   </div>
                                                    )}
                                                </div>
                                            </div>
                                            <div className='make_modal_mathquiz_right_item'>
                                                <p>  </p>
                                                <input 
                                                type='text' 
                                                className='quiz_setting_option_input'
                                                value={mathMinValue}
                                                onChange={handleMinChange}
                                                />
                                                <p>~ </p>
                                                <input 
                                                type='text' 
                                                className='quiz_setting_option_input'
                                                value={mathMaxValue}
                                                onChange={handleMaxChange}
                                                />

                                            </div>
                                        </div>
                                        {errormsg1 && <div className='make_modal_mathquiz_errmsg1'>{errormsg1}</div>}
                                        {errormsg2 && <div className='make_modal_mathquiz_errmsg2'>{errormsg2}</div>}

                                        <div className='make_modal_mathquiz_right_item_wrap'>
                                            <div className='make_modal_mathquiz_right_item'>
                                                <p>  </p>
                                                <div className='quiz_setting_option_btn' onClick={handleDropNumber} ref={answerNumberRef}>
                                                <p>{mathAnswerNumber}</p>
                                                <Optionarrow/>
                                                {answerNumberMenu &&(
                                                       <div className='drop_down_number' style={{height:8.375+'rem'}}>
                                                       {[2,4,6,8,10].map(num => (
                                                           <div key={num} 
                                                               className='drop_down_number_item' 
                                                               onClick={() => handleNumberSelect(num)}>
                                                               {num}
                                                           </div>
                                                       ))}
                                                   </div>
                                                    )}
                                                </div>
                                            </div>
                                            <div className='make_modal_mathquiz_right_item' style={{marginLeft:1+'rem'}}>
                                                <p>  </p>
                                                <div className='quiz_setting_option_btn' onClick={handleDropTime} ref={timeNumberRef}>
                                                <p>{mathTimeNumber}</p>
                                                <Optionarrow/>
                                                {timeNumberMenu &&(
                                                       <div className='drop_down_number' style={{height:8.375+'rem'}}>
                                                       {[10,20,30,40,50,60].map(num => (
                                                           <div key={num} 
                                                               className='drop_down_number_item' 
                                                               onClick={() => handleTimeSelect(num)}>
                                                               {num}
                                                           </div>
                                                       ))}
                                                   </div>
                                                    )}
                                                    
                                                </div>
                                                <p></p>
                                            </div>
                                        </div>
                                        
                                    </div>
                                    
                                </div>
                                
                                
                            </div>
                            <div className='caution_wrap'>
                                <div className='caution'>
                                    <p></p>
                                    <ul>
                                        <li>     .</li>
                                        <li>          .</li>
                                        <li>       .</li>
                                    </ul>
                                </div>

                            </div>
                            
                            
                        </div>
                        
                    </div>

                    <div className='make_modal_footer'>
                            <div className={`make_modal_bg_confirm_btn ${isValid ? 'active' : ''}`} onClick={()=>{if(isValid) handleCloseAndConfirm();}}><p></p></div>
                        </div>
                    
                   
                </div>

                {errorModalOpen && (
                    <div className='error_modal-close'>
                    <div className='error_modal_header'>
                        <p> </p>
                        <div className='make_modal_header_close' onClick={handleErrorModalClose} >
                            <Close/>
                        </div>
                    </div>
                    <div className='error_modal-close_content_wrap'>
                        <div className='error_modal-close_content'>
                            <Warning/>
                            <p>      .<br/> ?</p>
                            
                        </div>
                        <div className='error_modal_footer'>
                            <div className='make_modal_bg_confirm_btn2-1' onClick={handleErrorModalClose} ><p></p></div>
                            <div className='make_modal_bg_confirm_btn2-2   active' onClick={handleModalClose}><p></p></div>

                        </div>   
                    </div>
                    </div>
                )}
            </div>

            </>

        )}

        </>
        
    )
}

export default Makequiz;