import React, { useState, useRef, useEffect, useLayoutEffect,useCallback} from 'react';

import './carousel.css';

import {ReactComponent as Nextbtn} from '../../../img/homepage/slide/nextarrow.svg';
const Carousel = ({ images, current,setCurrent,selectedSlide, isArrowVisible, setModuleTitle, moduleTitles, setModuleIntro, moduleIntros, isDivVisible, onSelectSlide } ) => {
  
  const [clonedImages, setClonedImages] = useState([images]);
  // const [current, setCurrent] = useState(0);  //  current  0

  const [windowWidth, setWindowWidth] = useState(window.innerWidth);
  const [isHovered, setIsHovered] = useState(false);
  const trackRef = useRef();
  let slideInterval = useRef();

//    
const nextSlide = useCallback(() => {
  let newSlideIndex = current + 1 >= clonedImages.length ? 1 : current + 1;
  if (newSlideIndex >= clonedImages.length - 1) {
    setCurrent(1);
    setModuleTitle(moduleTitles[0]);
    setModuleIntro(moduleIntros[0]);
  } else {
    setCurrent(newSlideIndex);
    setModuleTitle(moduleTitles[newSlideIndex - 1]);
    setModuleIntro(moduleIntros[newSlideIndex - 1]);
  }
}, [current, clonedImages.length, moduleTitles, moduleIntros]);

//    
const prevSlide = useCallback(() => {
  let newSlideIndex = current - 1 < 0 ? clonedImages.length - 2 : current - 1;
  if (newSlideIndex <= 0) {
    setCurrent(clonedImages.length - 2);
    setModuleTitle(moduleTitles[moduleTitles.length - 1]);
    setModuleIntro(moduleIntros[moduleIntros.length - 1]);
  } else {
    setCurrent(newSlideIndex);
    setModuleTitle(moduleTitles[newSlideIndex - 1]);
    setModuleIntro(moduleIntros[newSlideIndex - 1]);
  }
}, [current, clonedImages.length, moduleTitles, moduleIntros]);

useEffect(() => {
  let autoSlide;
  if (!isHovered&&!isDivVisible) {
    autoSlide = setInterval(() => {
      nextSlide();
    }, 3000);
  }
  return () => {
    clearInterval(autoSlide);
  };
}, [current, nextSlide, isHovered,isDivVisible]);



  useEffect(() => {
    //     
    setClonedImages([images[images.length-1], ...images, images[0]]);
  }, [images]);


  useLayoutEffect(() => {
    const index = current === 0 ? clonedImages.length - 2 : current;
    setModuleTitle(moduleTitles[index - 1]);
    setModuleIntro(moduleIntros[index - 1]);
  }, [current]);

  // useEffect(() => {
  //   const index = selectedSlide === 0 ? clonedImages.length - 2 : selectedSlide;
  //   setCurrent(index);
  // }, [selectedSlide]);
  
//   
useEffect(() => {
  if (current === clonedImages.length - 1) {
    setTimeout(() => {
      setCurrent(1);
    }, 0);
  } else if (current === 0) {
    setTimeout(() => {
      setCurrent(clonedImages.length - 2);
    }, 0);
  }
}, [current]);



  useEffect(() => {
    const trackElement = trackRef.current;
    const initialOffset = 4140;
    const decrement = 688;
    const newOffset = initialOffset - current * decrement;
    trackElement.style.transform = `translateX(${newOffset}px)`;
  }, [current,windowWidth]);

  
  

  useEffect(() => {
    const handleResize = () => {
      setWindowWidth(window.innerWidth);
    };

    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

// useEffect(()=>{
//   console.log({isHovered})
//   console.log({isDivVisible})
// },[isHovered,isDivVisible])



  if (!Array.isArray(images) || images.length <= 0) {
    return null;
  }


  return (
    <>
    <section 
    className="carousel"
    onMouseEnter={() => {
      setIsHovered(true)
     }}
    onMouseLeave={() => {
    setIsHovered(false)
    ;  //     interval 
  }}>
      <div className={`carousel__arrow carousel__arrow--left ${isArrowVisible ? '' : 'hidden'}`}  onClick={prevSlide}><Nextbtn/></div>
      <div className="carousel__track-container"  ref={trackRef} >
       <ul className="carousel__track">
      {clonedImages.map((image, index) => {
        return (
          <li
            className={`carousel__slide ${index === current ? 'current' : ''}`}
            key={index}
          >
            <img src={image} alt="Carousel" className="carousel__image" />
          </li>
        );
      })}
    </ul>
      </div>
      
      <i className={`carousel__arrow carousel__arrow--right ${isArrowVisible ? '' : 'hidden'}`} onClick={nextSlide}><Nextbtn/></i>
    </section>
   
    </>
  );
};

export default Carousel;
