import React, {useState, useRef, useEffect } from 'react';
import '../makecontents.css';

import {ReactComponent as Bgiconsmall} from '../../../../img/homepage/makecontents/bg-pic_small.svg';

function TextScreen({setNumber, texts: initialTexts, updateTexts}){
    const [text, setText] =  useState(initialTexts || [null, null, null, null]);
    const [txtErrorMsg, setTxtErrorMsg] = useState([false, false, false, false]);
    const [inputMode, setInputMode] = useState([false, false, false, false]);


    const handleAnswerChange = (index, newText) => {
        if (newText.length > 60 || /\.[a-z]+/.test(newText)) {
            setTxtErrorMsg(prev => prev.map((msg, i) => i === index ? true : msg));
        } else {
            setTxtErrorMsg(prev => prev.map((msg, i) => i === index ? false : msg));
            setText(prev => prev.map((item, i) => i === index ? newText : item));
        }
    };
    const handleBlur = (index) => {
        if (text[index] === '' || text[index] === null) {
            setInputMode(prevMode => prevMode.map((mode, i) => i === index ? false : mode));
        }
    };

        const handleTxterror=()=>{
            setTxtErrorMsg([false, false, false, false]);
           
        }

        const handleInputTxt = (index) => {
            setInputMode(prevMode => prevMode.map((mode, i) => i === index ? true : mode));
        };
        useEffect(() => {
            updateTexts(setNumber, text);
        }, [text]);

        useEffect(() => {
            setInputMode(text.map(item => item !== null));
        }, [text]);

        return (
            <>
            <div className='order_contents_wrap'>
                {Array.from({ length: 4 }, (_, index) => (
                        <div key={index} className='make_modal_content_quiz_list_item'>
                            <div className='quiz_list_header'>
                                <p> {index + 1}</p>
                            </div>
                            <div className='quiz_list_contents'>
                                <div className='quiz_order_list_contents_txt ' onClick={() => handleInputTxt(index)}>
                                    {inputMode[index] ? (
                                        <textarea
                                            className='order_txtarea'
                                            value={text[index] || ''}
                                            maxLength={60}
                                            onChange={(e) => handleAnswerChange(index, e.target.value)}
                                            onBlur={() => handleBlur(index)}
                                            autoFocus
                                        />
                                    ) : (
                                        <>
                                            <p className='quiz_list_contents_font'><br/> .</p>
                                            <p className='quiz_list_contents_lightfont'>( 60 )</p>
                                        </>
                                    )}
                                    {txtErrorMsg[index] && (
                                        <div className='quiz_list_contents_img_error order_contents_img' onClick={handleTxterror}>
                                            <p>  <br/> .</p>
                                        </div>
                                    )}
                                </div>
                            </div>
                        </div>
                    ))}
            </div>
                
            </>
        );
      }

    
               

export default TextScreen;