
import React, {useState, useRef, useEffect } from 'react';
import '../makecontents.css';

import {ReactComponent as Bgiconsmall} from '../../../../img/homepage/makecontents/bg-pic_small.svg';

function ImageScreen({ setNumber, images: initialImages, updateImages }){

    const fileInputRef1 = useRef(null);
    const fileInputRef2 = useRef(null);
    const fileInputRef3 = useRef(null);
    const fileInputRef4 = useRef(null);

    const inputRefs = [fileInputRef1, fileInputRef2, fileInputRef3, fileInputRef4];

    const [images, setImages] = useState(initialImages || [null, null, null, null]);
    const [imgErrorMsg, setImgErrorMsg] = useState([false, false, false, false]);

    const handleDivClick = (index) => {
        inputRefs[index].current.click();
    };
    
    const handleFileChange = (index, e) => {
        e.preventDefault();
    
        let reader = new FileReader();
        let file = e.target.files[0];
        
        const validExtensions = ['image/png', 'image/jpeg'];
        if (!validExtensions.includes(file.type)) {
            setImgErrorMsg(prev => prev.map((msg, i) => i === index ? true : msg));
            return;
        } else {
            setImgErrorMsg(prev => prev.map((msg, i) => i === index ? false : msg));
        }
        reader.onloadend = () => {
            setImages(prevImages => prevImages.map((img, i) => i === index ? reader.result : img));
        }
        reader.readAsDataURL(file);
    }

    const handleCloseImgErr = (index) => {
        setImgErrorMsg(prev => prev.map((msg, i) => i === index ? false : msg));
    }

    const handleDrop = (index, e) => {
        e.preventDefault();
    
        let reader = new FileReader();
        let file = e.dataTransfer.files[0];
        
        const validExtensions = ['image/png', 'image/jpeg'];
        if (!validExtensions.includes(file.type)) {
            setImgErrorMsg(prev => prev.map((msg, i) => i === index ? true : msg));
            return;
        } else {
            setImgErrorMsg(prev => prev.map((msg, i) => i === index ? false : msg));
        }
        reader.onloadend = () => {
            setImages(prevImages => prevImages.map((img, i) => i === index ? reader.result : img));
        }
        reader.readAsDataURL(file);
    }

    const handleDragOver = (event) => {
        event.preventDefault();
    };

    useEffect(() => {
        updateImages(setNumber, images);
    }, [images]);

    useEffect(() => {
        let timer;
        if (imgErrorMsg) {
            timer = setTimeout(() => setImgErrorMsg([false, false, false, false]), 3000);
        }
        return () => clearTimeout(timer);
    }, [imgErrorMsg]);

    return(
        <>
        <div className='order_contents_wrap'>
            {Array.from({length: 4}).map((_, index) => (
                    <div className='make_modal_content_quiz_list_item' key={index}>
                        <div className='quiz_list_header'>
                            <p>{index + 1}</p>
                        </div>
                        <div className='quiz_list_contents'>
                            <div className='quiz_list_contents_img order_contents_img' onDrop={(event) => handleDrop(index, event)} onDragOver={handleDragOver}>
                                
                                {images[index] ? (
                                    <>
                                        <img src={images[index]} alt="preview" style={{maxWidth: '100%', maxHeight: '100%'}}/> 
                                        <input type="file" ref={inputRefs[index]} onChange={(e) => handleFileChange(index, e)} style={{display: 'none'}}  />
                                        <div className='quiz_list_contents_imgbtn2' onClick={() => handleDivClick(index)}><p></p></div>
                                    </>
                                ) : (
                                    <>
                                        <div><Bgiconsmall/></div>
                                        <p className='quiz_list_contents_font'> <br/>  </p>
                                        <input 
                                            type="file" 
                                            ref={inputRefs[index]} 
                                            onChange={(e) => handleFileChange(index, e)} 
                                            style={{display: 'none'}}  
                                        />
                                        <div className='quiz_list_contents_imgbtn1' onClick={() => handleDivClick(index)}><p></p></div>
                                    </>
                                )}
                                {imgErrorMsg[index] && (
                                    <div className='quiz_list_contents_img_error order_contents_img' onClick={()=>handleCloseImgErr(index)}>
                                        <p>  .<br/> .jpg, .png   .</p>
                                    </div>
                                )}
                            </div>
                        </div>
                    </div>
                ))}
        </div>
            
        </>
    )
}

export default ImageScreen;

