import React, {useState , useEffect,useRef} from 'react';
import '../makecontents.css';
import QuizItem from './makequiz_tf_item';

import {ReactComponent as Bgicon} from '../../../../img/homepage/makecontents/quiz_pic.svg';
import {ReactComponent as Close} from '../../../../img/homepage/makecontents/close.svg';
import {ReactComponent as Warning} from '../../../../img/homepage/makecontents/icon-warning.svg';
import {ReactComponent as Setting} from '../../../../img/homepage/makecontents/icon-settings.svg';
import {ReactComponent as Optionarrow} from '../../../../img/homepage/makecontents/option_arrow.svg';
import {ReactComponent as Circleplusbg} from '../../../../img/homepage/makecontents/circle_plus_btn1.svg';
import {ReactComponent as Circleplus} from '../../../../img/homepage/makecontents/circle_plus_btn2.svg';




function Makequiz({moduleTitle,listItems,setListItems,quizItemsState,setQuizItemsState,answerNumber, setAnswerNumber,timeNumber, setTimeNumber}){

    const [isModalOpen, setModalOpen] = useState(false);
    const [errorModalOpen, setErrorModalOpen] = useState(false);
    const isConfirmBtnActive = listItems.length > 0 && listItems.every(item => item.answer !== '' && item.answer.length <= 60);
    const [answerNumberMenu, setAnswerNumberMenu] = useState(false)
    const [timeNumberMenu, setTimeNumberMenu] = useState(false)
    const [textCounts, setTextCounts] = useState([]);

    const answerNumberRef = useRef(null);
    const timeNumberRef = useRef(null);
    
    


    const handleClick = () => {
        setModalOpen(!isModalOpen);
    }

    const handleCloseClick = () => {
        if(listItems.length > 0) {
            setErrorModalOpen(true); //   
            
        } else {
            setModalOpen(false); //    
        }
    }

    const handleErrorModalClose = () => {
        setErrorModalOpen(false);
        // console.log({listItems});
        
    }

    const handleModalClose =()=>{
        setErrorModalOpen(false);
        setListItems([]);
        setQuizItemsState([]);
        setModalOpen(false);

    }

    const handleCloseAndConfirm = () => {
        if (isConfirmBtnActive) { 
            setModalOpen(!isModalOpen);
            
        }
    }

    const addListItem = () => {
        setListItems(prevItems => [
          ...prevItems,
          {
            id: Math.max(...prevItems.map(item => item.id), 0) + 1,
            bgImage: null,
            answer: '',
            tfActiveButton: true
          }
        ]);
      };
      
      const deleteListItem = (id) => {
        setListItems(prevItems =>
          prevItems
            .filter(item => item.id !== id)
            .map((item, index) => ({ ...item, id: index + 1 }))
        );
      };

    const handleDropNumber = ()=>{
        setAnswerNumberMenu(!answerNumberMenu);
    }
    const handleDropTime = ()=>{
        setTimeNumberMenu(!timeNumberMenu)
    }

    const handleNumberSelect = (number) => {
        setAnswerNumber(number);
        setAnswerNumberMenu(false);
    }

    const handleTimeSelect = (number) => {
        setTimeNumber(number);
        setTimeNumberMenu(false);
    }

    const updateQuiz = (id, newAnswer) => {
        setListItems(prevItems =>
          prevItems.map(item =>
            item.id === id ? { ...item, answer: newAnswer } : item
          )
        );
      };

      const updateButton = (id, newButtonState) => {
        setListItems(prevItems =>
          prevItems.map(item =>
            item.id === id ? { ...item, tfActiveButton: newButtonState } : item
          )
        );
      };

      const updateTextCount = (id, count) => {
        setTextCounts(prevCounts => ({ ...prevCounts, [id]: count }));
      }

      const handleClickOutside1 = (event) => {
        if (answerNumberRef.current && !answerNumberRef.current.contains(event.target)) {
            setAnswerNumberMenu(false);
        }
      };


    const handleClickOutside2 = (event) => {
        if (timeNumberRef.current && !timeNumberRef.current.contains(event.target)) {
            setTimeNumberMenu(false);
        }
      };

      useEffect(() => {
        document.addEventListener('click', handleClickOutside1);
        document.addEventListener('click', handleClickOutside2);
        return () => {
            document.removeEventListener('click', handleClickOutside1);
            document.removeEventListener('click', handleClickOutside2);
        };
      }, []);




    return(
        <>
        <div className='make_contents_quiz_wrap' onClick={handleClick}>
            <div className='make_contents_quiz1'>
            {listItems.length > 0 ?(
                <>
                <div className='make_contents_bg1_thumbnail'>
                <div className='thumbnail_quiz_font'><p>{listItems[0]?.answer}</p></div>
                <div className='make_contents_bg1_thumbnail_circle'> <p></p> </div>
                </div>
                </>
                
                
            ):(
                <Bgicon/>
            ) }
                
            </div>
            <div className='make_contents_quiz2'>
                {listItems.length > 0 ? (
                    <>
                     <div className='make_contents_bgtitle1_wrap'>
                        <p className='make_contents_title1'> </p>
                    </div>  
                    </>
                ):(
                    <>
                    <p className='make_contents_title1'></p>
                <p className='make_contents_title2'>   <br/>   <br/>   .</p>
                    </>
                )}
                
            </div>
        </div>

        {isModalOpen && (
            <>
            <div className='overlay'></div>
            <div className='make_modal'>
                <div className='make_modal_header'>
                    <p>{moduleTitle}</p>
                    <div className='make_modal_header_close' onClick={handleCloseClick}>
                        <Close/>
                    </div>
                </div>
                <div className='make_modal_content_wrap'>
                    <div className='make_modal_nev'>
                        <div className='make_modal_nev_li active'>
                            <p> </p>
                        </div>
                        <div className='make_modal_content'>
                        <div className='make_modal_content_quiz_wrap'>
                            <div className='make_modal_content_quiz_left'>
                                <div className='make_modal_content_quiz_setting'>
                                    <div className='quiz_setting_header'>
                                        <div className='setting_icon'><Setting/></div>
                                        <div className='setting_title'>  </div>
                                    </div>
                                    <div className='quiz_setting_contents'>
                                        <div className='quiz_setting_content'>
                                            <p className='quiz_setting_content_title'>  </p>
                                            <div className='quiz_setting_option'>
                                                <div className='quiz_setting_option_btn' onClick={handleDropNumber} ref={answerNumberRef}>
                                                    <p>{answerNumber}</p>
                                                    <Optionarrow/>

                                                    {answerNumberMenu && (
                                                        <div className='drop_down_number'>
                                                            {[2, 3, 4, 5, 6, 7, 8, 9, 10].map(num => (
                                                                <div key={num} 
                                                                    className='drop_down_number_item' 
                                                                    onClick={() => handleNumberSelect(num)}>
                                                                    {num}
                                                                </div>
                                                            ))}
                                                        </div>
                                                    )}
                                                    
                                                </div>
                                                <p className='quiz_setting_content_title'></p>
                                            </div>
                                            
                                        </div>
                                        <div className='quiz_setting_content'>
                                            <p className='quiz_setting_content_title'> </p>
                                            <div className='quiz_setting_option'>
                                                <div className='quiz_setting_option_btn' onClick={handleDropTime} ref={timeNumberRef}>
                                                    <p>{timeNumber}</p>
                                                    <Optionarrow/>

                                                    {timeNumberMenu &&(
                                                       <div className='drop_down_number'>
                                                       {[5, 10, 15, 20, 25, 30].map(num => (
                                                           <div key={num} 
                                                               className='drop_down_number_item' 
                                                               onClick={() => handleTimeSelect(num)}>
                                                               {num}
                                                           </div>
                                                       ))}
                                                   </div>
                                                    )}
                                                </div>
                                                <p className='quiz_setting_content_title'></p>
                                            </div>
                                            
                                        </div>
                                    </div>
                                    
                                </div>
                                
                            </div>
                            <div className='make_modal_content_quiz_right'>
                                <div className='make_modal_content_quiz_list'>
                                    <div className='make_modal_content_quiz_list_item make_list' onClick={addListItem}>
                                        <div className='circle_plus_bg'>
                                            <Circleplusbg/>
                                            <div className='circle_plus'>
                                                <Circleplus/>
                                                </div>

                                        </div>
                                        <p className='circle_plus_font'></p>    
                                    </div>
                                    {

                                        [...listItems].reverse().map((item, index)=>(

                                            <QuizItem 
                                            key={item.id} 
                                            id={item.id}                       
                                            updateQuiz={updateQuiz}
                                            deleteItem={deleteListItem} 
                                            newQuizNumber={listItems.length - index}
                                            tfActiveButton={item.tfActiveButton} //   tfActiveButton  props 
                                            updateButton={updateButton}
                                            answer={item.answer} 

                                            textCount={textCounts[item.id] || 0} 
                                            updateTextCount={updateTextCount}
                                            />


                                        ))

                                    }
                                    
                                    
                                    
                                </div>

                            </div>
                        </div>


                        
                        
                    </div>
                    </div>
                    <div className='make_modal_footer'>
                                <div className={`make_modal_bg_confirm_btn ${isConfirmBtnActive  ? 'active' : ''}`} onClick={handleCloseAndConfirm}><p></p></div>

                        </div>
                </div>

                {errorModalOpen && (
                    <div className='error_modal-close'>
                    <div className='error_modal_header'>
                        <p> </p>
                        <div className='make_modal_header_close' onClick={handleErrorModalClose}>
                            <Close/>
                        </div>
                    </div>
                    <div className='error_modal-close_content_wrap'>
                        <div className='error_modal-close_content'>
                            <Warning/>
                            <p>      .<br/> ?</p>
                            
                        </div>
                        <div className='error_modal_footer'>
                            <div className='make_modal_bg_confirm_btn2-1' onClick={handleErrorModalClose}><p></p></div>
                            <div className='make_modal_bg_confirm_btn2-2   active' onClick={handleModalClose}><p></p></div>

                        </div>   
                    </div>
                    </div>
                )}
            </div>

            </>

        )}

        </>
        
    )
}

export default Makequiz;