import React, {useState, useRef, useEffect } from 'react';
import '../makecontents.css';

import {ReactComponent as Bgiconsmall} from '../../../../img/homepage/makecontents/bg-pic_small.svg';


function QuizItem({ id , bgImage1, updateQuiz, newQuizNumber,queOption,korAnswer,engAnswer }) {
    const [imgErrorMsg1, setImgErrorMsg1] = useState(false);
    const [imgErrorMsg2, setImgErrorMsg2] = useState(false);
    const [txtErrorMsg1, setTxtErrorMsg1] = useState(false);
    const [txtErrorMsg2, setTxtErrorMsg2] = useState(false);
    const [korErrorMsg, setKorErrorMsg] = useState(false);
    const [engErrorMsg, setEngErrorMsg] = useState(false);
    const fileInputRef1 = useRef(null);
    const fileInputRef2 = useRef(null);
    
    const [inputMode1, setInputMode1] = useState(false);
    const [inputMode2, setInputMode2] = useState(false);
    
    const handleAnswerChange1 = (newEngAnswer) => { //
        const engReg = /^[a-zA-Z\s]*$/;
        if (!engReg.test(newEngAnswer)) {
          setEngErrorMsg(true);
        } else {
          setEngErrorMsg(false);
          updateQuiz(id, bgImage1, newEngAnswer, korAnswer);
          if (newEngAnswer.length > 11 || /\.[a-z]+/.test(newEngAnswer)) {
            setTxtErrorMsg1(true);
          } else {
            setTxtErrorMsg1(false);
            
          }
        }
      };

    const handleAnswerChange2 = (newKorAnswer) => { //
        const korReg = /^[-|-|-\s]*$/;
        if (!korReg.test(newKorAnswer)) {
          setKorErrorMsg(true);
        } else {
          setKorErrorMsg(false);
          updateQuiz(id, bgImage1, engAnswer, newKorAnswer);
          if (newKorAnswer.length > 4 || /\.[a-z]+/.test(newKorAnswer)) {
            setTxtErrorMsg2(true);
          } else {
            setTxtErrorMsg2(false);
            
          }
        }
      };

    

    const handleDivClick1 = () => {
        fileInputRef1.current.click();
    };

    const handleDivClick2 = () => {
        fileInputRef2.current.click();
    };



    const handleFileChange1 = (e) => {
      e.preventDefault();
  
      let reader = new FileReader();
      let file = e.target.files[0];
  
      //   
      const fileExtension = file.name.split('.').pop().toLowerCase();
      const isValidFile = fileExtension === 'png' || fileExtension === 'jpg';
  
      if (!isValidFile) {
          //  .png .jpg     
          setImgErrorMsg1(true);
          return;
      }
  
      reader.onloadend = () => {
          updateQuiz(id, reader.result, engAnswer, korAnswer); 
      }
  
      reader.readAsDataURL(file)
  }
  

    const handleImgerror1=()=>{
        setImgErrorMsg1(false);
    }

    const handleImgerror2=()=>{
        setImgErrorMsg2(false);
    }


    const handleTxterror=()=>{
        setTxtErrorMsg1(false);
        setTxtErrorMsg2(false);
    }

    const handleInputTxt1 = () => {
        setInputMode1(true);
    };
    
    const handleInputTxt2 = () => {
        setInputMode2(true);
    };

    const handleDrop = (event) => {
      event.preventDefault();
  
      let reader = new FileReader();
      let file = event.dataTransfer.files[0];
  
      //   
      const fileExtension = file.name.split('.').pop().toLowerCase();
      const isValidFile = fileExtension === 'png' || fileExtension === 'jpg';
  
      if (!isValidFile) {
          //  .png .jpg     
          setImgErrorMsg1(true);
          return;
      }
  
      reader.onloadend = () => {
          updateQuiz(id, reader.result, engAnswer, korAnswer); 
      }
  
      reader.readAsDataURL(file)
  }
  
  const handleDragOver = (event) => {
      event.preventDefault();
  };

    useEffect(() => {
        if (engAnswer) {
          setInputMode1(true);
        }
      }, [engAnswer]);

      useEffect(() => {
        if (korAnswer) {
          setInputMode2(true);
        }
      }, [korAnswer]);

    useEffect(() => {
        let timer;
        if (imgErrorMsg1) {
            timer = setTimeout(() => setImgErrorMsg1(false), 3000);
        }
        return () => clearTimeout(timer);
    }, [imgErrorMsg1]);

    useEffect(() => {
        let timer;
        if (imgErrorMsg2) {
            timer = setTimeout(() => setImgErrorMsg2(false), 3000);
        }
        return () => clearTimeout(timer);
    }, [imgErrorMsg2]);

    useEffect(() => {
        let timer;
        if (txtErrorMsg1) {
            timer = setTimeout(() => setTxtErrorMsg1(false), 3000);
        }
        return () => clearTimeout(timer);
    }, [txtErrorMsg1]);

    useEffect(() => {
        let timer;
        if (txtErrorMsg2) {
            timer = setTimeout(() => setTxtErrorMsg2(false), 3000);
        }
        return () => clearTimeout(timer);
    }, [txtErrorMsg2]);

   

    return (
        <div className='make_modal_content_quiz_list_item' key={newQuizNumber}>
                <div className='quiz_list_header'>
                    <p>{` ${newQuizNumber}`}</p>
                    
                </div>
                    <div className='quiz_list_contents'>
                    {queOption === '' ? (
                       <>
                       <div className='quiz_list_contents_img' onDrop={handleDrop} onDragOver={handleDragOver}>
                          {bgImage1 ? (
                            <>
                              <img src={bgImage1} alt="preview" style={{maxWidth: '100%', maxHeight: '100%'}}/> 
                              <input type="file" ref={fileInputRef1} onChange={(e) => handleFileChange1(e)} style={{display: 'none'}}  />
                              <div className='quiz_list_contents_imgbtn2' onClick={handleDivClick1}><p></p></div>
                            </>
                          ) : (
                            <>
                              <div><Bgiconsmall/></div>
                              <p className='quiz_list_contents_font'> <br/>  </p>
                              <input type="file" ref={fileInputRef1} onChange={(e) => handleFileChange1(e)} style={{display: 'none'}}  />
                              <div className='quiz_list_contents_imgbtn1' onClick={handleDivClick1}><p></p></div>
                            </>
                          )}
                          {imgErrorMsg1 && (
                            <div className='quiz_list_contents_img_error' onClick={handleImgerror1}>
                              <p>  .<br/> .jpg, .png <br/> .</p>
                            </div>
                          )}
                        </div>
                        <div className='quiz_list_contents_txt' onClick={handleInputTxt1}>
                        {inputMode1? (

                            <input
                            className='quiz_list_contents_txt_answer'
                            type="text" 
                            value={engAnswer || ''} 
                            maxLength={11}
                            onChange={(e) => handleAnswerChange1(e.target.value)}
                            onBlur={(e) => {
                                if (!e.target.value.trim()) {
                                    setInputMode1(false);
                                }
                            }}
                            onFocus={(e) => {
                                if (!e.target.value.trim()) {
                                    handleAnswerChange1('');
                                }
                            }}
                            onKeyDown={(e) => {
                              if (e.key === ' ') { //  keyCode 32.
                                e.preventDefault(); //   .
                              }
                            }}
                            autoFocus
                            />

                        ):(
                            <>
                            <p className='quiz_list_contents_font'> <br/> .</p>
                            <p className='quiz_list_contents_lightfont'>( 11 )</p>
                            </>
                           
                        )}

                        {engErrorMsg && (
                            <div className='quiz_list_contents_txt_error' onClick={handleTxterror}>
                                <p>  .</p>
                            </div>
                            )}

                        {txtErrorMsg1 &&(
                        <div className='quiz_list_contents_img_error' onClick={handleTxterror}>
                            <p>  <br/><br/>.</p>
                        </div>
                        )}
                      
                        </div>
                    </>
                    ) : (
                        <>
                           <div className='quiz_list_contents_img' onDrop={handleDrop} onDragOver={handleDragOver}>
                         {bgImage1 ? (
                           <>
                             <img src={bgImage1} alt="preview" style={{maxWidth: '100%', maxHeight: '100%'}}/> 
                             <input type="file" ref={fileInputRef1} onChange={(e) => handleFileChange1(e)} style={{display: 'none'}}  />
                             <div className='quiz_list_contents_imgbtn2' onClick={handleDivClick1}><p></p></div>
                           </>
                         ) : (
                           <>
                             <div><Bgiconsmall/></div>
                             <p className='quiz_list_contents_font'> <br/>  </p>
                             <input type="file" ref={fileInputRef1} onChange={(e) => handleFileChange1(e)} style={{display: 'none'}}  />
                             <div className='quiz_list_contents_imgbtn1' onClick={handleDivClick1}><p></p></div>
                           </>
                         )}
                         {imgErrorMsg1 && (
                           <div className='quiz_list_contents_img_error' onClick={handleImgerror1}>
                             <p>  .<br/> .jpg, .png <br/> .</p>
                           </div>
                         )}
                            </div>
                            <div className='quiz_list_contents_txt' onClick={handleInputTxt2}>
                            {inputMode2? (

                                <input
                                className='quiz_list_contents_txt_answer'
                                type="text" 
                                value={korAnswer || ''} 
                                maxLength={4}
                                onChange={(e) => handleAnswerChange2(e.target.value)}
                                onBlur={(e) => {
                                    if (!e.target.value.trim()) {
                                        setInputMode2(false);
                                    }
                                }}
                                onFocus={(e) => {
                                    if (!e.target.value.trim()) {
                                        handleAnswerChange2('');
                                    }
                                }}
                                onKeyDown={(e) => {
                                  if (e.key === ' ') { //  keyCode 32.
                                    e.preventDefault(); //   .
                                  }
                                }}
                                autoFocus
                                />

                            ):(
                                <>
                                <p className='quiz_list_contents_font'> <br/> .</p>
                                <p className='quiz_list_contents_lightfont'>( 4 )</p>
                                </>
                               
                            )}

                            {korErrorMsg && (
                            <div className='quiz_list_contents_txt_error' onClick={handleTxterror}>
                                <p>  .</p>
                            </div>
                            )}

                            {txtErrorMsg2 &&(
                            <div className='quiz_list_contents_img_error' onClick={handleTxterror}>
                                <p>  <br/><br/>.</p>
                            </div>
                            )}
                          
                            </div>
                        </>
                    )}
                        
                        
                    </div>
            </div>
    );
}

export default QuizItem;
