import React,{useState} from 'react';
import { Link,useNavigate,Outlet } from 'react-router-dom';

import './homepage.css'



import {ReactComponent as Logo1} from '../img/homepage/logo1.svg';
import {ReactComponent as Logo2} from '../img/homepage/logo2.svg';
import {ReactComponent as Userlogo} from '../img/homepage/userlogo.svg';
import {ReactComponent as Circle} from '../img/homepage/circle.svg';
import {ReactComponent as Circle2} from '../img/homepage/circle2.svg';
import {ReactComponent as Circle3} from '../img/homepage/circle3.svg';
import {ReactComponent as Bubble} from '../img/homepage/whitebubble.svg';

 
function onClickSelect(e){
    const isActive = e.currentTarget.className.indexOf("active") !== -1;
    if (isActive) {
        e.currentTarget.className = "select";
    } else {
        e.currentTarget.className = "select active";
    }
}

const Homepage = ()=>{

    const navigate = useNavigate();

    const [isDivVisible, setDivVisible] = useState(false);

    const toggleDiv = ()=>{
        setDivVisible(!isDivVisible);
    }
  
    return(
        <>

        <div className='glow_bar'></div>
        
        <div className='home_wrap1'>
            <div className='home_header_wrap'>
                <div className='home_header' style={{justifyContent:'space-between'}}>
                    <div className='home_logo' onClick={()=>{navigate('/')}} style={{cursor:'pointer'}}>
                        <div className='logo_1'><Logo1/></div>
                        <div className='logo_2'><Logo2/></div>
                    </div>
                    <div className='navbar'>
                        <Link className='nav_menu' to={'/company'}></Link>
                        <Link className='nav_menu' to={'/product'}></Link>
                        <Link className='nav_menu' to={'/login'} style={{marginRight:82+'px'}}></Link>
                        <div className='user_logo' onClick={toggleDiv}><Userlogo/> </div>
                        <div className='bubble_wrap' style={{display: isDivVisible ? 'block' : 'none'}}>           
                            
                            <Bubble/>
                            <div className='white_bubble'>
                                <div onClick={()=>{navigate('/login/signup')}}><p></p></div>
                                <div onClick={()=>{navigate('/login')}}><p></p></div>
                            </div>                  
                        </div>  
                                
                        
                    </div>
                     
                </div>
                
            </div>

            

        </div>
        
        <div className='header_bar'></div>

        <div></div>

        <div className='home_content_wrap'><Outlet/></div>
        



        <div className='home_wrap2'>
            <div className='home_footer_wrap'>
                <div className='home_header' style={{height:70+'px', justifyContent:'space-between'}}>
                    <div className='footer_link'>
                        <a href='https://beaverblock.com/pages/termsofservice' target='_blank' style={{marginRight:36+'px'}}></a>
                        <a href='https://beaverblock.com/pages/privacypolicy' target='_blank'> </a>
                    </div>
                    <div className='footer_btn'>
                        <a href="https://blog.naver.com/beaverblock" target='_blank'>
                            <div className='footer_circle'>
                                <Circle/>
                                    <div className='footer_in_circle'>N</div>
                            </div>
                        </a>
                        <a href="https://www.youtube.com/@beaverblock" target='_blank' >
                            <div className='footer_circle'><Circle2/></div>
                        </a>
                        <a href="https://www.instagram.com/beaverblock/"  target='_blank'>
                            <div className='footer_circle'><Circle3/></div>
                        </a>
                        

                    </div>
                    
                </div>

                <div className='home_bar2'></div>

                 <div className='home_header' style={{justifyContent:'space-between'}}>
                    <div className='footer_copyright'>
                        <p>  |  :   |   184()  1009-2<br/>
                         : 070-4354-0803  |   : 510-87-01472  |   :  2020--1231 <br/><br/>

                         BeaverBlock. ALL RIGHTS RESERVED.</p>
                    </div>
                    <div className='footer_contact'>
                        <p className='footer_contact_call'>070-4354-0803</p>
                        <p className='footer_contact_email'>AM 10:00~ PM 6:00 // <br/>
                        help@beaverblock.com</p>
                    </div>
                </div>
                

            </div>
            
            
  
        </div>
        </>
    )
}



export default Homepage