import React,{createContext,useState,useEffect} from 'react';
import { Link,useNavigate,Outlet } from 'react-router-dom';
import './homepage.css'
import {ReactComponent as Logo1} from '../img/homepage/logo1.svg';
import {ReactComponent as Logo2} from '../img/homepage/logo2.svg';
import {ReactComponent as Userlogo2} from '../img/homepage/userlogo2.svg';
import {ReactComponent as Circle} from '../img/homepage/circle.svg';
import {ReactComponent as Circle2} from '../img/homepage/circle2.svg';
import {ReactComponent as Circle3} from '../img/homepage/circle3.svg';
import {ReactComponent as Bubble2} from '../img/homepage/bluebubble.svg';
import {ReactComponent as Close} from '../img/homepage/makecontents/close.svg';
import {ReactComponent as Warning} from '../img/homepage/makecontents/icon-warning.svg';
import {ReactComponent as Soon} from '../img/homepage/soon.svg';

export const MyContext = createContext();


const Homepage2 = ()=>{
    
    const [projectItems, setProjectItems] = useState([]);
    


    const [moduleTitles, setModuleTitles] = useState([
        'OX ',
        ', ',
        ' ',
        '',
        '',
        '',
        '', 
        '',
        '',
        '',
        
      ]); //  
  const [moduleIntros, setModuleIntros] = useState([
    '.  , ,  ,  OX   .',
    ' ,      .',
    ' ,         .', 
    '       .',
    '        .',
    '        .',
    '            .',
    '         .',
    '         .',
    '        .',
  ]); //  

    const [moduleTitle, setModuleTitle] = useState(moduleTitles[0]);
    const [moduleIntro, setModuleIntro]=useState(moduleIntros[0])
   
    const [selectedSlide, setSelectedSlide] = useState(1);
    const navigate = useNavigate();

    const [selectedImage, setSelectedImage] = useState(null); //   

    const [selectedCard, setSelectedCard] = useState(null); //   

    const [text, setText] = useState(''); //   

    const [listItems, setListItems] = useState([]); /// ox,tf,   1
    const [quizItemsState, setQuizItemsState] = useState([]); ///  ox,tf,   2

    

    const [answerNumber, setAnswerNumber] = useState(5); // ox,tf 
    const [timeNumber, setTimeNumber] = useState(5); // ox,tf  
    const [mathMinValue, setMathMinValue] = useState(''); //   
    const [mathMaxValue, setMathMaxValue] = useState('');//   
    const [cardPiece, setCardPiece] = useState('3X2'); //  
    const [piece, setPiece] = useState(6);// 
    const [cardoption, setCardOption] = useState('-'); //  
    

    const [images, setImages] = useState({
        idle: null,
        correct: null,
        incorrect: null
    }); //      state

    const [selectAudio, setSelectAudio] = useState(null) //bgm  

    const [modalSwitch, setModalSwitch] = useState(false);
    const [nextUrl, setNextUrl] = useState('');

    const [myPagePopUp, setMyPagePopUp] = useState(false);

    const modalCloseClick = ()=>{
        return setModalSwitch(false);
    }
    const handleMypagePop = ()=>{
        setMyPagePopUp(!myPagePopUp) 
    }
    
    //  (  )
    //moduleTitle         (ex. moduleTitle ox selectedImage null~~)

    const Navbar = ()=>{
        const [isDivVisible, setDivVisible] = useState(false);
        const toggleDiv = ()=>{
            setDivVisible(!isDivVisible);
        }
        const showmodal = (e, path)=>{
            if (selectedImage !== null ||
                selectedCard !== null ||
                    text !=='' ||
                    listItems.length > 0 ||
                    quizItemsState.length > 0 ||
                    (mathMinValue!=='' && mathMaxValue!=='' )||
                    images.idle ||
                    selectAudio !== null
                   ) { ///
                e.preventDefault();
                
                setModalSwitch(true);
                setNextUrl(path);
            }else{
                setModalSwitch(false);
            }
        }

        const handleLogout = async () => {
            const username = await getUserName();
            // console.log('logout username: ',username);
            const url = `http://13.125.65.215/BS/logout.php?userid=${username}`;
          
            try {
              const response = await fetch(url);
              if (!response.ok) throw new Error('Network response was not ok');
            //   console.log('Logout successful');
              navigate('/');
            //   console.log('logout username1: ',username);
            } catch (error) {
              console.error('Logout failed:', error);
            }
          };

        async function getUserName() {
            const url = 'http://13.125.65.215/BS/GetCookie_userID.php';
            
            try {
              const response = await fetch(url);
              if (!response.ok) throw new Error('Network response was not ok');
              const data = await response.text();
            //   console.log(`User Name: ${data}`);
              return data;
            } catch (error) {
              throw error;
            }
          }


        return(

            <div className='navbar'>
                <Link className='nav_menu' to={'/logged/company'} onClick={(e) => showmodal(e, '/company')} ></Link>
                <Link className='nav_menu' to={'/logged/product'} onClick={(e) => showmodal(e, '/product')}></Link>
                <Link className='nav_menu' to={'/logged/studio'} onClick={(e) => showmodal(e, '/logged/studio')} style={{marginRight:82+'px'}}></Link>
                <div className='user_logo' onClick={toggleDiv}><Userlogo2/> </div>
                <div className='bubble_wrap' style={{display: isDivVisible ? 'block' : 'none'}}>           
                    <Bubble2/>
                    <div className='blue_bubble'>
                        <div onClick={handleMypagePop}><p></p></div>
                        <div onClick={()=>{handleLogout();}}><p></p></div>
                    </div>                  
                </div>  
                        
                
            </div>


        )
    };

    const resetValues = () => {
        setSelectedImage(null);
        setSelectedCard(null);
        setText('');
        setListItems([]);
        setQuizItemsState([]);
        setAnswerNumber(5);
        setTimeNumber(5);
        setImages({idle: null, correct: null, incorrect: null});
        setSelectAudio(null);
        setMathMinValue('');
        setMathMaxValue('');
        setPiece(6);
      };//
    
    
    return(
        <>
        <div className='glow_bar'></div>
        <div className='home_wrap1'>
            <div className='home_header_wrap'>
                <div className='home_header' style={{justifyContent:'space-between'}}>
                    <div className='home_logo' onClick={()=>{navigate('/logged')}}>
                        <div className='logo_1'><Logo1/></div>
                        <div className='logo_2'><Logo2/></div>
                    </div>
                   
                    <Navbar/>
                     
                </div>
                
            </div>

        </div>
        
        <div className='header_bar'></div>

        <div className='home_content_wrap'>
        
        <MyContext.Provider 
        value={{projectItems, 
            setProjectItems,
        moduleTitle, 
        setModuleTitle, 
        moduleIntro, 
        setModuleIntro, 
        moduleTitles, 
        moduleIntros, 
        selectedSlide, 
        setSelectedSlide, 
        setSelectedImage, 
        selectedImage,
        text, 
        setText,
        resetValues,

        listItems, setListItems, quizItemsState, setQuizItemsState,answerNumber, setAnswerNumber,timeNumber, setTimeNumber,
        cardPiece, setCardPiece, cardoption, setCardOption,

        mathMinValue, setMathMinValue, mathMaxValue, setMathMaxValue,

        images, setImages, 

        selectAudio, setSelectAudio,

        selectedCard, setSelectedCard,

        piece, setPiece,


        }}>
            <Outlet/>
        </MyContext.Provider>
        
        
        

        </div>   
        {modalSwitch &&(
            <>
            <div className='overlay'></div>
            <div className='error_modal-close'>
                <div className='error_modal_header'>
                    <p> </p>
                    <div className='make_modal_header_close' onClick={modalCloseClick}>
                        <Close/>
                    </div>
                </div>
                <div className='error_modal-close_content_wrap'>
                    <div className='error_modal-close_content'>
                        <Warning/>
                        <p>      .<br/> ?</p>
                        
                    </div>
                    <div className='error_modal_footer'>
                        <div className='make_modal_bg_confirm_btn2-1' onClick={modalCloseClick}><p></p></div>
                        <div className='make_modal_bg_confirm_btn2-2   active' onClick={() => {navigate(nextUrl); modalCloseClick();}} ><p></p></div>

                    </div>   
                </div>
            </div>
            </>
        )}

        {myPagePopUp &&(
           <>
           <div className='overlay'></div>
           <div className='error_modal-close'>
               <div className='error_modal_header'>
                   <p> </p>
                   <div className='make_modal_header_close' onClick={handleMypagePop}>
                   <Close/>
                   </div>
               </div>
               <div className='error_modal-close_content_wrap'>
                   <div className='notice_modal-close_content'>
                       <Soon/>
                       <p>  .</p>
                       
                   </div>
                   <div className='notice_modal_footer'>
                       <div className='make_modal_bg_confirm_btn2-2 active' onClick={handleMypagePop} ><p></p></div>
                   </div>   
               </div>
           </div>
           </>

        )}
        
       
        
        
       

        <div className='home_wrap2'>
            <div className='home_footer_wrap'>
                <div className='home_header' style={{height:70+'px', justifyContent:'space-between'}}>
                    <div className='footer_link'>
                        <a href='https://beaverblock.com/pages/termsofservice' target='_blank' style={{marginRight:36+'px'}}></a>
                        <a href='https://beaverblock.com/pages/privacypolicy' target='_blank'> </a>
                    </div>
                    <div className='footer_btn'>
                        <a href="https://blog.naver.com/beaverblock" target='_blank'>
                            <div className='footer_circle'>
                                <Circle/>
                                    <div className='footer_in_circle'>N</div>
                            </div>
                        </a>
                        <a href="https://www.youtube.com/@beaverblock" target='_blank' >
                            <div className='footer_circle'><Circle2/></div>
                        </a>
                        <a href="https://www.instagram.com/beaverblock/"  target='_blank'>
                            <div className='footer_circle'><Circle3/></div>
                        </a>
                        

                    </div>
                    
                </div>

                <div className='home_bar2'></div>

                 <div className='home_header' style={{justifyContent:'space-between'}}>
                    <div className='footer_copyright'>
                        <p>  |  :   |   184()  1009-2<br/>
                         : 070-4354-0803  |   : 510-87-01472  |   :  2020--1231 <br/><br/>

                         BeaverBlock. ALL RIGHTS RESERVED.</p>
                    </div>
                    <div className='footer_contact'>
                        <p className='footer_contact_call'>070-4354-0803</p>
                        <p className='footer_contact_email'>AM 10:00~ PM 6:00 // <br/>
                        help@beaverblock.com</p>
                    </div>
                </div>
                

            </div>
            
            
  
        </div>
        </>
    )
}
export default Homepage2