import React from 'react';
import { Outlet } from 'react-router-dom';

import hobi from '../img/loginpage/poli_logo.png';



const LoginPage = ()=>{
    return(
        <div className='index_page'>
          <div className='bg_container'>

            <div className='bg_side'>
              
              <div className='bg_side_content' >
                <div className='bg_side_content_font'>
                  <p>Welcome<br/><span>beaver studio!</span></p>
                </div>
                <div className='bg_side_content_img'>
                  <img  src={hobi} alt='hobi'/>
                </div>
                
               
              </div>
            </div>


            <Outlet></Outlet>

          
          </div>

          <div className='bg_footer'>
          <div className='bg_footer_btn'>
          <a href='https://beaverblock.com/pages/termsofservice' target='_blank' className='bg_footer_btn_font' style={{marginRight:54+'px'}}></a>
          <a href='https://beaverblock.com/pages/privacypolicy' target='_blank' className='bg_footer_btn_font'> </a>
          </div>
          <div className='bg_footer_address'>
          <p>184()  1009-2<br/>
          TEL : 070-4354-0803  : 510-87-01472 MAIL: help@beaverblock.com<br/>
          () </p>
          </div>

          </div>
      </div>
    )
}

export default LoginPage



