import React, {useState, useEffect } from 'react';
import '../makecontents.css';

import {ReactComponent as Bgicon} from '../../../../img/homepage/makecontents/text_pic.svg';
import {ReactComponent as Close} from '../../../../img/homepage/makecontents/close.svg';
import {ReactComponent as Warning} from '../../../../img/homepage/makecontents/icon-warning.svg';


function Maketext({moduleTitle,text,setText,guidetext}){
    // const {moduleTitle} = useContext(MyContext);
    const [isModalOpen, setModalOpen] = useState(false);
    const [errorModalOpen, setErrorModalOpen] = useState(false);
    const [bgImage, setBgImage] = useState(null);
    const handleClick = () => {
        setModalOpen(!isModalOpen);
    }

    const [count, setCount] = useState(0); //   count
    const handleInputChange = (e) => { //   
        const inputLength = e.target.value.length;
        setCount(inputLength); //   
        setText(e.target.value);
        
      }

    const [showError, setShowError] = useState(false); //     

    const handleCloseClick = () => {
        if(text) {
            setErrorModalOpen(true); //   
            
        } else {
            setModalOpen(false); //    
        }
    }

    const handleErrorModalClose = () => {
        setErrorModalOpen(false); //  
        
    }

    const handleModalClose = () => {
        setErrorModalOpen(false); //  
        setModalOpen(false); //    
        setText(''); //  
        setCount(0); //    
        setBgImage(null) //  
    }

    const handleCloseAndConfirm = () => {
        if (count > 0 && count <= 30 && text) { // count      
            setModalOpen(false);
            setBgImage(text);
        }
    }

    useEffect(() => {
        if (count > 30) {
            setShowError(true);
            const timer = setTimeout(() => {
                setShowError(false);
            }, 3000);
            return () => clearTimeout(timer);
        } else {
            setShowError(false);
        }
    }, [count]); // count   

    const handleErrorClick = () => {
        setShowError(false);
    }
    return(
        <>
        <div className='make_contents_text_wrap' onClick={handleClick}>
            <div className='make_contents_text1'>

                {bgImage ? ( //   
                    <div className='make_contents_bg1_thumbnail'>
                        <div className='make_contents_txt_preview'>
                            <div><p>{bgImage}</p></div> 
                        </div>
                        
                        <div className='make_contents_bg1_thumbnail_circle'> <p></p> </div>
                    </div>
                    
                ) : (
                    <Bgicon/> //     
                )}
            </div>
            <div className='make_contents_text2'>
               
                 {bgImage ? (
                    <>
                    <div className='make_contents_bgtitle1_wrap'>
                        <p className='make_contents_title1'> </p>
                    </div>   
                    </>
                    
                ):(
                    <>
                    <p className='make_contents_title1'></p>
                    <p className='make_contents_title2'>  <br/>    <br/>  .</p>

                    </>
                    
                )}
            </div>
        </div>
        {isModalOpen && (
            <>
            <div className='overlay'></div>
            <div className='make_modal'>
                <div className='make_modal_header'>
                    <p>{moduleTitle}</p>
                    <div className='make_modal_header_close' onClick={handleCloseClick}>
                        <Close/>
                    </div>
                </div>
                <div className='make_modal_content_wrap'>
                    <div className='make_modal_nev'>
                            <div className='make_modal_nev_li active'>
                                <p> </p>
                            </div>
                            <div className='make_modal_content'>
                                <div className='make_modal_content_txtarea_wrap'>
                                    <div className='make_modal_content_txtarea'>
                                        <textarea 
                                        className='make_modal_txtarea' 
                                        placeholder={guidetext}
                                        value={text}
                                        onChange={handleInputChange}
                                        maxLength={30}>
                                        </textarea>
                                        
                                        {showError && (
                                            <div className="error_wrong-txtcount" onClick={handleErrorClick}><p>  .</p></div>
                                        )}
                                        
                                        
                                        
                                    </div>
                                    <div className='make_modal_txtarea_typecount_wrap'>
                                        <div className='make_modal_txtarea_typecount'>{count}/30</div>
                                    </div>
                                    
                                    
                                    
 
                                </div>
                                

                           

                            </div>
                            <div className='make_modal_footer' style={{right: 0}}>
                                <div className={`make_modal_bg_confirm_btn ${count > 0 && count <= 30 ? 'active' : ''}`} onClick={handleCloseAndConfirm} ><p></p></div>

                            </div>
                    </div>
                </div>
                {errorModalOpen && (

                    <div className='error_modal-close'>
                    <div className='error_modal_header'>
                        <p> </p>
                        <div className='make_modal_header_close' onClick={handleErrorModalClose}>
                            <Close/>
                        </div>
                    </div>
                    <div className='error_modal-close_content_wrap'>
                        <div className='error_modal-close_content'>
                            <Warning/>
                            <p>      .<br/> ?</p>
                            
                        </div>
                        <div className='error_modal_footer'>
                            <div className='make_modal_bg_confirm_btn2-1' onClick={handleErrorModalClose}><p></p></div>
                            <div className='make_modal_bg_confirm_btn2-2   active' onClick={handleModalClose}><p></p></div>

                        </div>   
                    </div>
                    </div>

                )}
            </div>
            </>

        )}
        </>
        
    )
}

export default Maketext;