import React, {useState,useEffect } from 'react';
import '../makecontents.css';


import {ReactComponent as Bgicon} from '../../../../img/homepage/makecontents/character_pic.svg';
import {ReactComponent as Close} from '../../../../img/homepage/makecontents/close.svg';
import {ReactComponent as Greypic} from '../../../../img/homepage/makecontents/grey_bg_small.svg';
import {ReactComponent as Warning} from '../../../../img/homepage/makecontents/icon-warning.svg';



function Makecharacter({moduleTitle,images, setImages}){
    // const { moduleTitle} = useContext(MyContext);
    const [isModalOpen, setModalOpen] = useState(false);
    const [activeTab, setActiveTab] = useState('upload');
    const [activeIcon, setActiveIcon] = useState('mtc');
    const [dragging, setDragging] = useState({idle: false, correct: false, incorrect: false}); //     state
    const [bgImage, setBgImage] = useState(null); // 
    const [errorModalOpen, setErrorModalOpen] = useState(false);

    const [errorMessageIdle, setErrorMessageIdle] = useState(false);
    const [errorMessageCorrect, setErrorMessageCorrect] = useState(false);
    const [errorMessageIncorrect, setErrorMessageIncorrect] = useState(false);



    const handleImageUpload = (key, newImage, fileName) => {
        const fileExtension = fileName.split('.').pop().toLowerCase();
        const validExtensions = ['jpg', 'jpeg', 'png']; //       .
    
        if (validExtensions.includes(fileExtension)) {
            setImages(prevImages => ({...prevImages,[key]: newImage}));
            setErrorMessageIdle(false);
            setErrorMessageCorrect(false);
            setErrorMessageIncorrect(false);
            if (key === 'idle') {
                setImages(prevState => ({...prevState, correct: newImage, incorrect: newImage}));
            }
        } else {
            if (key === 'idle') {
                setErrorMessageIdle(true);
            } else if (key === 'correct') {
                setErrorMessageCorrect(true);
            } else if (key === 'incorrect') {
                setErrorMessageIncorrect(true);
            }
        }
    }


    const handleClick = () => {
        setModalOpen(!isModalOpen);
    }
    const handleTabClick = (tab) => {
        setActiveTab(tab);
    }

    const handleIconClick = (Icon)=>{
        setActiveIcon(Icon);
    }

    const handleImageClick = (e) => {
        //   active  
        const imageElements = document.querySelectorAll('.make_modal_free_char_contents_img');
        imageElements.forEach(imgDiv => imgDiv.classList.remove('active'));
    
        //   active  
        const imgDiv = e.currentTarget;
        imgDiv.classList.add('active');
    
        const selectedImageSrc = imgDiv.querySelector('img').src;
        setImages({idle: selectedImageSrc, correct: selectedImageSrc, incorrect: selectedImageSrc});
    }

    const handleCloseAndConfirm = () => {
        if (images.idle) { //     
            setModalOpen(false);
            setBgImage(images.idle);
        }
    }

    const handleErrorModalClose = () => {
        setErrorModalOpen(false);
        
    }

    const handleErrorMsgClose =()=>{
        setErrorMessageIdle(false);
        setErrorMessageCorrect(false);
        setErrorMessageIncorrect(false);
    }

    const handleModalClose = () => {
        setErrorModalOpen(false);
        setModalOpen(false);
        setImages({idle: null, correct: null, incorrect: null});
        setBgImage(null)
    }

    const handleCloseClick = () => {
        if(images.idle) {
            setErrorModalOpen(true);
        } else {
            setModalOpen(false);
            setImages({idle: null, correct: null, incorrect: null});
        }
    }


    //      useEffect
    useEffect(() => {
        if(errorMessageIdle) {
            setTimeout(() => {
                setErrorMessageIdle(false);
            }, 3000);
        }
        if(errorMessageCorrect) {
            setTimeout(() => {
                setErrorMessageCorrect(false);
            }, 3000);
        }
        if(errorMessageIncorrect) {
            setTimeout(() => {
                setErrorMessageIncorrect(false);
            }, 3000);
        }
    }, [errorMessageIdle, errorMessageCorrect, errorMessageIncorrect]);

    return(
        <>
        <div className='make_contents_char_wrap' onClick={handleClick}>
            <div className='make_contents_char1'>
                {bgImage ? (
                    <div className='make_contents_bg1_thumbnail'>
                    <img src={bgImage} alt=" " className='bgImage'/> 
                    <div className='make_contents_bg1_thumbnail_circle'> <p></p> </div>
                    </div>


                ):(
                    <Bgicon/>
                )}
               
            </div>
            <div className='make_contents_char2'>
               
                {bgImage ? (
                    <>
                    <div className='make_contents_bgtitle1_wrap'>
                        <p className='make_contents_title1'> </p>
                    </div>   
                    </>
                    
                ):(
                    <>
                    <p className='make_contents_title1'> </p>
                    <p className='make_contents_title2'>    <br/>   !</p>

                    </>
                    
                )}
            </div>
        </div>

        {isModalOpen &&(
            <>
            <div className='overlay'></div>
            <div className='make_modal'>
                <div className='make_modal_header'>
                    <p>{moduleTitle}</p>
                    <div className='make_modal_header_close' onClick={handleCloseClick}>
                        <Close/>
                    </div>
                </div>
                <div className='make_modal_content_wrap'>
                    <div className='make_modal_nev'>
                        <div className={activeTab === 'upload' ? 'make_modal_nev_li active' : 'make_modal_nev_li'} onClick={() => handleTabClick('upload')}>
                            <p>  </p>
                        </div>
                        <div  className={activeTab === 'free' ? 'make_modal_nev_li active' : 'make_modal_nev_li'} onClick={() => handleTabClick('free')}>
                            <p>  </p>
                        </div>
                    </div>
                    <div className='make_modal_content'>
                        {activeTab === 'upload' && (
                            <>
                            <div className='make_modal_char_upload_img'>
                                <div className='make_modal_char_upload_wrap1'>
                                    <div className={images.idle ? 'make_modal_char_upload-active' : (dragging.idle ? 'make_modal_char_upload-dragon' : 'make_modal_char_upload')}
                                        
                                        onDrop={event => {
                                            event.preventDefault();
                                            const file = event.dataTransfer.files[0];
                                            const reader = new FileReader();
                                            reader.onloadend = () => {
                                                handleImageUpload('idle', reader.result, file.name);
                                            };
                                            reader.readAsDataURL(file);
                                            setDragging(prevState => ({...prevState, idle: false}));
                                        }}
                                        onDragOver={event => {
                                            event.preventDefault();
                                            event.stopPropagation();
                                            if (!dragging) {
                                                // setDragging(true);
                                                setDragging(prevState => ({...prevState, idle: true}));

                                            }
                                        }}

                                        onDragEnter={event=>{
                                            event.preventDefault();
                                            event.stopPropagation();
                                            setDragging(prevState => ({...prevState, idle: true}));

                                        }}
                                        
                                        onDragLeave={event=>{
                                            event.preventDefault();
                                            event.stopPropagation();
                                            setDragging(prevState => ({...prevState, idle: false}));

                                        }}

                                        
                                        >
                                        {images.idle ? 
                                        <img src={images.idle} alt="preview"/> 
                                        :<>
                                            <Greypic /> 
                                            <p>   !</p>
                                        </> }
                                    

                                      
                                        <input type="file" 
                                            onChange={event => {
                                                const file = event.target.files[0];
                                                const reader = new FileReader();
                                                reader.onloadend = () => {
                                                    handleImageUpload('idle', reader.result, file.name);
                                                };
                                                reader.readAsDataURL(file);
                                            }} 
                                            style={{display: 'none'}} 
                                            id="upload-idle"/>
                                        <label htmlFor="upload-idle" className='make_modal_bg_upload_btn' style={{width: 40+'%'}}> </label>

                                        {errorMessageIdle && (
                                            <>
                                            <div className='make_char_msg_error' onClick={handleErrorMsgClose}>
                                                <p>  .<br/>.jpg, .png   .</p>
                                            </div>
                                            </>
                                        )}
                                    </div>
                                    <p className='make_modal_char_bottom_font'>  <br/><span style={{color: '#F00'}}>( )</span></p>              

                                </div>

                                <div className='make_modal_char_upload_wrap2'>
                                    <div className={images.correct ? 'make_modal_char_upload-active' : (dragging.correct ? 'make_modal_char_upload-dragon' : 'make_modal_char_upload')}
                                       onDrop={event => {
                                        event.preventDefault();
                                        const file = event.dataTransfer.files[0];
                                        const reader = new FileReader();
                                        reader.onloadend = () => {
                                            handleImageUpload('correct', reader.result, file.name);
                                        };
                                        reader.readAsDataURL(file);
                                        setDragging(prevState => ({...prevState, correct: false}));
                                    }}

                                        onDragOver={event => {
                                            event.preventDefault();
                                            event.stopPropagation();
                                            setDragging(prevState => ({...prevState, correct: true}));
                                        }}

                                        onDragEnter={event=>{
                                            event.preventDefault();
                                            event.stopPropagation();
                                            setDragging(prevState => ({...prevState, correct: true}));
                                        }}
                                        
                                        onDragLeave={event=>{
                                            event.preventDefault();
                                            event.stopPropagation();
                                            setDragging(prevState => ({...prevState, correct: false}));
                                        }}
                                        >
                                        {images.correct ? 
                                        <img src={images.correct} alt="preview"/> 
                                        :<>
                                            <Greypic /> 
                                            <p>   !</p>
                                        </> }
                                                                    
                                        <input type="file" 
                                            onChange={event => {
                                                const file = event.target.files[0];
                                                const reader = new FileReader();
                                                reader.onloadend = () => {
                                                    handleImageUpload('correct', reader.result, file.name);
                                                };
                                                reader.readAsDataURL(file);
                                            }} 
                                            style={{display: 'none'}} 
                                            id="upload-correct"/>
                                        <label htmlFor="upload-correct" className='make_modal_bg_upload_btn' style={{width: 40+'%'}}> </label>
                                        {errorMessageCorrect && (
                                            <>
                                            <div className='make_char_msg_error' onClick={handleErrorMsgClose}>
                                                <p>  .<br/>.jpg, .png   .</p>
                                            </div>
                                            </>
                                        )}
                                    </div>
                                    <p className='make_modal_char_bottom_font'> <br/><span style={{fontWeight:'400'}}>()</span></p>              
                                </div>





                                <div className='make_modal_char_upload_wrap3'>
                                    <div className={images.incorrect ? 'make_modal_char_upload-active' : (dragging.incorrect ? 'make_modal_char_upload-dragon' : 'make_modal_char_upload')}
                                       onDrop={event => {
                                            event.preventDefault();
                                            const file = event.dataTransfer.files[0];
                                            const reader = new FileReader();
                                            reader.onloadend = () => {
                                                handleImageUpload('incorrect', reader.result, file.name);
                                            };
                                            reader.readAsDataURL(file);
                                            setDragging(prevState => ({...prevState, incorrect: false}));
                                        }}

                                        onDragOver={event => {
                                            event.preventDefault();
                                            event.stopPropagation();
                                            setDragging(prevState => ({...prevState, incorrect: true}));

                                        }}

                                        onDragEnter={event=>{
                                            event.preventDefault();
                                            event.stopPropagation();
                                            setDragging(prevState => ({...prevState, incorrect: true}));
                                        }}
                                        
                                        onDragLeave={event=>{
                                            event.preventDefault();
                                            event.stopPropagation();
                                            setDragging(prevState => ({...prevState, incorrect: false}));
                                        }}
                                        >
                                        {images.incorrect ? 
                                        <img src={images.incorrect} alt="preview"/> 
                                        :<>
                                            <Greypic /> 
                                            <p>   !</p>
                                        </> }
                                    
                                        <input type="file" 
                                        onChange={event => {
                                            const file = event.target.files[0];
                                            const reader = new FileReader();
                                            reader.onloadend = () => {
                                                handleImageUpload('incorrect', reader.result, file.name);
                                            };
                                            reader.readAsDataURL(file);
                                        }} 
                                        style={{display: 'none'}} 
                                        id="upload-incorrect"/>
                                        <label htmlFor="upload-incorrect" className='make_modal_bg_upload_btn' style={{width: 40+'%'}}> </label>
                                        {errorMessageIncorrect && (
                                            <>
                                            <div className='make_char_msg_error' onClick={handleErrorMsgClose}>
                                                <p>  .<br/>.jpg, .png   .</p>
                                            </div>
                                            </>
                                        )}
                                    </div>
                                    <p className='make_modal_char_bottom_font'> <br/><span style={{fontWeight:'400'}}>()</span></p>              
                                </div>

                                {}






                            </div>
                                
                            </>
                        )}
                        {activeTab === 'free' && (
                            <div className='make_modal_free_bg'>
                            <div className='make_modal_free_bg_icons'>
                                <div className={activeIcon === 'mtc' ? 'make_modal_free_bg_icon active' : 'make_modal_free_bg_icon' } onClick={() => handleIconClick('mtc')}>
                                    <img src="http://13.125.65.215/BS/CommonRes/icon/common_icon_mtc.png" alt=""/>
                                </div>
                                <div className={activeIcon === 'omnom' ? 'make_modal_free_bg_icon active' : 'make_modal_free_bg_icon' } onClick={() => handleIconClick('omnom')}>
                                    <img src="http://13.125.65.215/BS/CommonRes/icon/common_icon_omnom.png" alt=""/>
                                </div>
                                <div className={activeIcon === 'poli' ? 'make_modal_free_bg_icon active' : 'make_modal_free_bg_icon' } onClick={() => handleIconClick('poli')}>
                                    <img src="http://13.125.65.215/BS/CommonRes/icon/common_icon_poli.png" alt=""/>
                                </div>
                                <div className={activeIcon === 'ppd' ? 'make_modal_free_bg_icon active' : 'make_modal_free_bg_icon' } onClick={() => handleIconClick('ppd')}>
                                    <img src="http://13.125.65.215/BS/CommonRes/icon/common_icon_ppd.png" alt=""/>
                                </div>
                            </div>
                            <div className='make_modal_free_bg_contents_wrap'>
                                 <div className='make_modal_free_char_contents'>
                                    {activeIcon === 'mtc' &&(
                                        <>
                                        <div className='make_modal_free_char_contents_img' onClick={handleImageClick}>
                                            <img src="http://13.125.65.215/BS/CommonRes/character/mtc/common_char_mtc_0.png" alt=""/>
                                        </div>
                                        <div className='make_modal_free_char_contents_img' onClick={handleImageClick}>
                                            <img src="http://13.125.65.215/BS/CommonRes/character/mtc/common_char_mtc_1.png" alt=""/>
                                        </div>
                                        <div className='make_modal_free_char_contents_img' onClick={handleImageClick}>
                                            <img src="http://13.125.65.215/BS/CommonRes/character/mtc/common_char_mtc_2.png" alt=""/>
                                        </div>
                                        <div className='make_modal_free_char_contents_img' onClick={handleImageClick}>
                                            <img src="http://13.125.65.215/BS/CommonRes/character/mtc/common_char_mtc_3.png" alt=""/>
                                        </div>
                                        <div className='make_modal_free_char_contents_img' onClick={handleImageClick}>
                                            <img src="http://13.125.65.215/BS/CommonRes/character/mtc/common_char_mtc_4.png" alt=""/>
                                        </div>
                                        <div className='make_modal_free_char_contents_img' onClick={handleImageClick}>
                                            <img src="http://13.125.65.215/BS/CommonRes/character/mtc/common_char_mtc_5.png" alt=""/>
                                        </div>
                                        </>
                                    )}
                                    {activeIcon === 'omnom' &&(
                                        <>
                                        <div className='make_modal_free_char_contents_img' onClick={handleImageClick}>
                                            <img src="http://13.125.65.215/BS/CommonRes/character/omnom/common_char_omnom_0.png" alt=""/>
                                        </div>
                                        <div className='make_modal_free_char_contents_img' onClick={handleImageClick}>
                                            <img src="http://13.125.65.215/BS/CommonRes/character/omnom/common_char_omnom_1.png" alt=""/>
                                        </div>
                                        <div className='make_modal_free_char_contents_img' onClick={handleImageClick}>
                                            <img src="http://13.125.65.215/BS/CommonRes/character/omnom/common_char_omnom_2.png" alt=""/>
                                        </div>
                                        <div className='make_modal_free_char_contents_img' onClick={handleImageClick}>
                                            <img src="http://13.125.65.215/BS/CommonRes/character/omnom/common_char_omnom_3.png" alt=""/>
                                        </div>
                                        <div className='make_modal_free_char_contents_img' onClick={handleImageClick}>
                                            <img src="http://13.125.65.215/BS/CommonRes/character/omnom/common_char_omnom_4.png" alt=""/>
                                        </div>
                                        <div className='make_modal_free_char_contents_img' onClick={handleImageClick}>
                                            <img src="http://13.125.65.215/BS/CommonRes/character/omnom/common_char_omnom_5.png" alt=""/>
                                        </div>
                                        </>
                                    )}
                                    {activeIcon === 'ppd' &&(
                                        <>
                                        <div className='make_modal_free_char_contents_img' onClick={handleImageClick}>
                                            <img src="http://13.125.65.215/BS/CommonRes/character/pangpang/common_char_ppd_0.png" alt=""/>
                                        </div>
                                        <div className='make_modal_free_char_contents_img' onClick={handleImageClick}>
                                            <img src="http://13.125.65.215/BS/CommonRes/character/pangpang/common_char_ppd_1.png" alt=""/>
                                        </div>
                                        <div className='make_modal_free_char_contents_img' onClick={handleImageClick}>
                                            <img src="http://13.125.65.215/BS/CommonRes/character/pangpang/common_char_ppd_2.png" alt=""/>
                                        </div>
                                        <div className='make_modal_free_char_contents_img' onClick={handleImageClick}>
                                            <img src="http://13.125.65.215/BS/CommonRes/character/pangpang/common_char_ppd_3.png" alt=""/>
                                        </div>
                                        <div className='make_modal_free_char_contents_img' onClick={handleImageClick}>
                                            <img src="http://13.125.65.215/BS/CommonRes/character/pangpang/common_char_ppd_4.png" alt=""/>
                                        </div>
                                        <div className='make_modal_free_char_contents_img' onClick={handleImageClick}>
                                            <img src="http://13.125.65.215/BS/CommonRes/character/pangpang/common_char_ppd_5.png" alt=""/>
                                        </div>
                                        </>
                                    )}
                                    {activeIcon === 'poli' &&(
                                        <>
                                         <div className='make_modal_free_char_contents_img' onClick={handleImageClick}>
                                            <img src="http://13.125.65.215/BS/CommonRes/character/poli/common_char_poli_0.png" alt=""/>
                                        </div>
                                        <div className='make_modal_free_char_contents_img' onClick={handleImageClick}>
                                            <img src="http://13.125.65.215/BS/CommonRes/character/poli/common_char_poli_1.png" alt=""/>
                                        </div>
                                        <div className='make_modal_free_char_contents_img' onClick={handleImageClick}>
                                            <img src="http://13.125.65.215/BS/CommonRes/character/poli/common_char_poli_2.png" alt=""/>
                                        </div>
                                        <div className='make_modal_free_char_contents_img' onClick={handleImageClick}>
                                            <img src="http://13.125.65.215/BS/CommonRes/character/poli/common_char_poli_3.png"alt=""/>
                                        </div>
                                        <div className='make_modal_free_char_contents_img' onClick={handleImageClick}>
                                            <img src="http://13.125.65.215/BS/CommonRes/character/poli/common_char_poli_4.png" alt=""/>
                                        </div>
                                        <div className='make_modal_free_char_contents_img' onClick={handleImageClick}>
                                            <img src="http://13.125.65.215/BS/CommonRes/character/poli/common_char_poli_5.png" alt=""/>
                                        </div>
                                        </>
                                    )}

                                 </div>

                            </div>
                        </div>
                        )}

                            
                    </div>
                    <div className='make_modal_footer'>
                                <div className={`make_modal_bg_confirm_btn ${images.idle ? 'active' : ''}`} onClick={handleCloseAndConfirm}><p></p></div>
                            </div>
                </div>
                {errorModalOpen && (

                    <div className='error_modal-close'>
                    <div className='error_modal_header'>
                        <p> </p>
                        <div className='make_modal_header_close' onClick={handleErrorModalClose}>
                            <Close/>
                        </div>
                    </div>
                    <div className='error_modal-close_content_wrap'>
                        <div className='error_modal-close_content'>
                            <Warning/>
                            <p>      .<br/> ?</p>
                            
                        </div>
                        <div className='error_modal_footer'>
                            <div className='make_modal_bg_confirm_btn2-1' onClick={handleErrorModalClose}><p></p></div>
                            <div className='make_modal_bg_confirm_btn2-2   active' onClick={handleModalClose}><p></p></div>

                        </div>   
                    </div>
                    </div>

                    )}
            </div>

            </>
        )}
        </>
        
    )
}

export default Makecharacter;