import React, {useState, useRef, useEffect } from 'react';
import '../makecontents.css';

import {ReactComponent as Bgiconsmall} from '../../../../img/homepage/makecontents/bg-pic_small.svg';


function QuizItem({ id , bgImage1,bgImage2, answer1,answer2, updateQuiz, newQuizNumber,cardoption }) {
    const [imgErrorMsg1, setImgErrorMsg1] = useState(false);
    const [imgErrorMsg2, setImgErrorMsg2] = useState(false);
    const [txtErrorMsg1, setTxtErrorMsg1] = useState(false);
    const [txtErrorMsg2, setTxtErrorMsg2] = useState(false);
    const fileInputRef1 = useRef(null);
    const fileInputRef2 = useRef(null);
    
    const [inputMode1, setInputMode1] = useState(false);
    const [inputMode2, setInputMode2] = useState(false);
   
   
    
    const handleAnswerChange1 = (newAnswer1) => {
        updateQuiz(id, bgImage1, bgImage2, newAnswer1, answer2);
        if (newAnswer1.length > 5 || /\.[a-z]+/.test(newAnswer1)) {
            setTxtErrorMsg1(true);
        } else {
            setTxtErrorMsg1(false);
            
        }
    };

    const handleAnswerChange2 = (newAnswer2) => {
        updateQuiz(id, bgImage1, bgImage2, answer1, newAnswer2);
        if (newAnswer2.length > 5 || /\.[a-z]+/.test(newAnswer2)) {
            setTxtErrorMsg2(true);
        } else {
            setTxtErrorMsg2(false);
          
        }
    };

    

    const handleDivClick1 = () => {
        fileInputRef1.current.click();
    };

    const handleDivClick2 = () => {
        fileInputRef2.current.click();
    };

    const handleFileChange1 = (e) => {

        const file = e.target.files[0];
        if (file && (file.type === 'image/png' || file.type === 'image/jpeg')) {
            const reader = new FileReader();
            reader.onloadend = () => {
                updateQuiz(id, reader.result, bgImage2, answer2);            
            };
            reader.readAsDataURL(file);
        } else {
            setImgErrorMsg1(true);
            e.target.value = null;
            }
            setInputMode2(true);
    };

    const handleFileChange2 = (e) => {
        const file = e.target.files[0];
        if (file && (file.type === 'image/png' || file.type === 'image/jpeg')) {
            const reader = new FileReader();
            reader.onloadend = () => {
                updateQuiz(id,bgImage1 ,reader.result, answer1);            
            };
            reader.readAsDataURL(file);
        } else {
            setImgErrorMsg2(true);
            e.target.value = null;
            }
            
    };

    const handleImgerror1=()=>{
        setImgErrorMsg1(false);
    }

    const handleImgerror2=()=>{
        setImgErrorMsg2(false);
    }


    const handleTxterror=()=>{
        setTxtErrorMsg1(false);
        setTxtErrorMsg2(false);
    }

    const handleInputTxt1 = () => {
        setInputMode1(true);
    };
    
    const handleInputTxt2 = () => {
        setInputMode2(true);
    };

    const handleDrop1 = (event) => {
        event.preventDefault();
        const file = event.dataTransfer.files[0];
        if (file && (file.type === 'image/png' || file.type === 'image/jpeg')) {
            const reader = new FileReader();
            reader.onloadend = () => {
                updateQuiz(id, reader.result, bgImage2, answer2);            
            };
            reader.readAsDataURL(file);
        } else {
            setImgErrorMsg1(true);
        }
    };
    
    const handleDragOver1 = (event) => {
        event.preventDefault();
    };

    const handleDrop2 = (event) => {
        event.preventDefault();
        const file = event.dataTransfer.files[0];
        if (file && (file.type === 'image/png' || file.type === 'image/jpeg')) {
            const reader = new FileReader();
            reader.onloadend = () => {
                updateQuiz(id,bgImage1 ,reader.result, answer1);            
            };
            reader.readAsDataURL(file);
        } else {
            setImgErrorMsg2(true);
        }
    };
    
    const handleDragOver2 = (event) => {
        event.preventDefault();
    };

    useEffect(() => {
        if (answer1) {
          setInputMode1(true);
        }
      }, [answer1]);

      useEffect(() => {
        if (answer2) {
          setInputMode2(true);
        }
      }, [answer2]);

    useEffect(() => {
        let timer;
        if (imgErrorMsg1) {
            timer = setTimeout(() => setImgErrorMsg1(false), 3000);
        }
        return () => clearTimeout(timer);
    }, [imgErrorMsg1]);

    useEffect(() => {
        let timer;
        if (imgErrorMsg2) {
            timer = setTimeout(() => setImgErrorMsg2(false), 3000);
        }
        return () => clearTimeout(timer);
    }, [imgErrorMsg2]);

    useEffect(() => {
        let timer;
        if (txtErrorMsg1) {
            timer = setTimeout(() => setTxtErrorMsg1(false), 3000);
        }
        return () => clearTimeout(timer);
    }, [txtErrorMsg1]);

    useEffect(() => {
        let timer;
        if (txtErrorMsg2) {
            timer = setTimeout(() => setTxtErrorMsg2(false), 3000);
        }
        return () => clearTimeout(timer);
    }, [txtErrorMsg2]);



    return (
        <div className='make_modal_content_quiz_list_item' key={newQuizNumber}>
                <div className='quiz_list_header'>
                    <p>{` ${newQuizNumber}`}</p>
                    
                </div>
                    <div className='quiz_list_contents'>
                    {cardoption === '-' ? (
                       <>
                       <div className='quiz_list_contents_img' onDrop={handleDrop1} onDragOver={handleDragOver1}>
                         {bgImage1 ? (
                           <>
                             <img src={bgImage1} alt="preview" style={{maxWidth: '100%', maxHeight: '100%'}}/> 
                             <input type="file" ref={fileInputRef1} onChange={(e) => handleFileChange1(e)} style={{display: 'none'}}  />
                             <div className='quiz_list_contents_imgbtn2' onClick={handleDivClick1}><p></p></div>
                           </>
                         ) : (
                           <>
                             <div><Bgiconsmall/></div>
                             <p className='quiz_list_contents_font'> <br/>  </p>
                             <input type="file" ref={fileInputRef1} onChange={(e) => handleFileChange1(e)} style={{display: 'none'}}  />
                             <div className='quiz_list_contents_imgbtn1' onClick={handleDivClick1}><p></p></div>
                           </>
                         )}
                         {imgErrorMsg1 && (
                           <div className='quiz_list_contents_img_error' onClick={handleImgerror1}>
                             <p>  .<br/> .jpg, .png <br/> .</p>
                           </div>
                         )}
                       </div>
                       <div className='quiz_list_contents_img' onDrop={handleDrop2} onDragOver={handleDragOver2}>
                         {bgImage2 ? (
                           <>
                             <img src={bgImage2} alt="preview" style={{maxWidth: '100%', maxHeight: '100%'}}/> 
                             <input type="file" ref={fileInputRef2} onChange={(e) => handleFileChange2(e)} style={{display: 'none'}}  />
                             <div className='quiz_list_contents_imgbtn2' onClick={handleDivClick2}><p></p></div>
                           </>
                         ) : (
                           <>
                             <div><Bgiconsmall/></div>
                             <p className='quiz_list_contents_font'> <br/>  </p>
                             <input type="file" ref={fileInputRef2} onChange={(e) => handleFileChange2(e)} style={{display: 'none'}}  />
                             <div className='quiz_list_contents_imgbtn1' onClick={handleDivClick2}><p></p></div>
                           </>
                         )}
                         {imgErrorMsg2 && (
                           <div className='quiz_list_contents_img_error' onClick={handleImgerror2}>
                             <p>  .<br/> .jpg, .png <br/> .</p>
                           </div>
                         )}
                       </div>
                     </>
                    ) : cardoption === '-' ? (
                        <>
                           <div className='quiz_list_contents_img' onDrop={handleDrop1} onDragOver={handleDragOver1}>
                         {bgImage1 ? (
                           <>
                             <img src={bgImage1} alt="preview" style={{maxWidth: '100%', maxHeight: '100%'}}/> 
                             <input type="file" ref={fileInputRef1} onChange={(e) => handleFileChange1(e)} style={{display: 'none'}}  />
                             <div className='quiz_list_contents_imgbtn2' onClick={handleDivClick1}><p></p></div>
                           </>
                         ) : (
                           <>
                             <div><Bgiconsmall/></div>
                             <p className='quiz_list_contents_font'> <br/>  </p>
                             <input type="file" ref={fileInputRef1} onChange={(e) => handleFileChange1(e)} style={{display: 'none'}}  />
                             <div className='quiz_list_contents_imgbtn1' onClick={handleDivClick1}><p></p></div>
                           </>
                         )}
                         {imgErrorMsg1 && (
                           <div className='quiz_list_contents_img_error' onClick={handleImgerror1}>
                             <p>  .<br/> .jpg, .png <br/> .</p>
                           </div>
                         )}
                       </div>
                            <div className='quiz_list_contents_txt' onClick={handleInputTxt2}>
                            {inputMode2? (

                                <input
                                className='quiz_list_contents_txt_answer'
                                type="text" 
                                value={answer2 || ''} 
                                maxLength={5}
                                onChange={(e) => handleAnswerChange2(e.target.value)}
                                onBlur={(e) => {
                                    if (!e.target.value.trim()) {
                                        setInputMode2(false);
                                    }
                                }}
                                onFocus={(e) => {
                                    if (!e.target.value.trim()) {
                                        handleAnswerChange2('');
                                    }
                                }}
                                autoFocus
                                />

                            ):(
                                <>
                                <p className='quiz_list_contents_font'><br/> .</p>
                                <p className='quiz_list_contents_lightfont'>( 5 )</p>
                                </>
                               
                            )}

                            {txtErrorMsg2 &&(
                            <div className='quiz_list_contents_img_error' onClick={handleTxterror}>
                                <p>  <br/><br/>.</p>
                            </div>
                            )}
                          
                        </div>
                        </>
                    ) : (
                        <>
                            <div className='quiz_list_contents_txt' onClick={handleInputTxt1}>
                            {inputMode1 ? (

                                <input
                                className='quiz_list_contents_txt_answer'
                                type="text" 
                                value={answer1 || ''} 
                                maxLength={5}
                                onChange={(e) => handleAnswerChange1(e.target.value)}
                                onBlur={(e) => {
                                    if (!e.target.value.trim()) {
                                        setInputMode1(false);
                                    }
                                }}
                                onFocus={(e) => {
                                    if (!e.target.value.trim()) {
                                        handleAnswerChange1('');
                                    }
                                }}
                                autoFocus
                                />

                            ):(
                                <>
                                <p className='quiz_list_contents_font'><br/> .</p>
                                <p className='quiz_list_contents_lightfont'>( 5 )</p>
                                </>
                               
                            )}

                            {txtErrorMsg1 &&(
                            <div className='quiz_list_contents_img_error' onClick={handleTxterror}>
                                <p>  <br/><br/>.</p>
                            </div>
                            )}
                          
                        </div>
                        <div className='quiz_list_contents_txt' onClick={handleInputTxt2}>
                            {inputMode2 ? (

                                <input
                                className='quiz_list_contents_txt_answer'
                                type="text" 
                                value={answer2 || ''} 
                                maxLength={5}
                                onChange={(e) => handleAnswerChange2(e.target.value)}
                                onBlur={(e) => {
                                    if (!e.target.value.trim()) {
                                        setInputMode2(false);
                                    }
                                }}
                                onFocus={(e) => {
                                    if (!e.target.value.trim()) {
                                        handleAnswerChange2('');
                                    }
                                }}
                                autoFocus
                                />

                            ):(
                                <>
                                <p className='quiz_list_contents_font'><br/> .</p>
                                <p className='quiz_list_contents_lightfont'>( 5 )</p>
                                </>
                               
                            )}

                            {txtErrorMsg2 &&(
                            <div className='quiz_list_contents_img_error' onClick={handleTxterror}>
                                <p>  <br/><br/>.</p>
                            </div>
                            )}
                          
                        </div>
                        </>
                    )}
                        
                        
                    </div>
            </div>
    );
}

export default QuizItem;
