import React, {useState, useEffect,useRef } from 'react';
import '../makecontents.css';



import {ReactComponent as Bgicon} from '../../../../img/homepage/makecontents/bgm_pic.svg';
import {ReactComponent as Close} from '../../../../img/homepage/makecontents/close.svg';
import {ReactComponent as Warning} from '../../../../img/homepage/makecontents/icon-warning.svg';
import {ReactComponent as Greybg} from '../../../../img/homepage/makecontents/grey_bg.svg';
import {ReactComponent as Radioboxoff} from '../../../../img/homepage/makecontents/icon-radio-box-off.svg';
import {ReactComponent as Radioboxon} from '../../../../img/homepage/makecontents/icon-radio-box-on.svg';
import {ReactComponent as Playgo} from '../../../../img/homepage/makecontents/icon_play_go.svg';
import {ReactComponent as Playstop} from '../../../../img/homepage/makecontents/icon_play_stop.svg';
import {ReactComponent as Note} from '../../../../img/homepage/makecontents/bgm_note.svg';



function Makebgm({moduleTitle,selectAudio, setSelectAudio}){
    // const { moduleTitle} = useContext(MyContext);
    const [isModalOpen, setModalOpen] = useState(false);
    const [activeTab, setActiveTab] = useState('upload');
    const [errorMessage, setErrorMessage] = useState(null);
    const [errorModalOpen, setErrorModalOpen] = useState(false);

    // const [selectAudio, setSelectAudio] = useState(null);/////
    const [fileName, setFileName] = useState(null);
    const [musicName, setMusicName] = useState('');
    const [fileExtension, setFileExtension] = useState(null);
    const [activeIcon, setActiveIcon] = useState('mtc');

    const [selected, setSelected] = useState(null);
    const [playing, setPlaying] = useState(null);
    const [isFileFromFreeTab, setIsFileFromFreeTab] = useState(false);

    const [timerId, setTimerId] = useState(null);


    const audioRef = useRef(null);



    const mtcFiles = [
        'common_mts_bgm_0.mp3',
        'common_mts_bgm_1.mp3',
        'common_mts_bgm_2.mp3',
        'common_mts_bgm_3.mp3',
        'common_mts_bgm_4.mp3',
        'common_mts_bgm_5.mp3',
        'common_mts_bgm_6.mp3',
      ];
      
      const omnomFiles = [
        'common_omnom_bgm_0.mp3',
        'common_omnom_bgm_1.mp3',
        'common_omnom_bgm_2.mp3',
      ];
      
      const poliFiles = [
        'common_poli_bgm_0.mp3',
        'common_poli_bgm_1.mp3',
        'common_poli_bgm_2.mp3',
        'common_poli_bgm_3.mp3',
        'common_poli_bgm_4.mp3',
        'common_poli_bgm_5.mp3',
      ];
      
      const ppdFiles = [
        'common_pangpang_bgm_0.mp3',
        'common_pangpang_bgm_1.mp3',
      ];

      const mtcFilesTitle = [
        ' OST. ',
        ' OST. ',
        ' OST. ',
        ' OST. &',
        ' OST. ',
        ' OST. ',
        ' OST. ',
      ];
      
      const omnomFilesTitle = [
        'Intro Song',
        'Nibble Nom Song',
        'Wash You Hands',
      ];
      
      const poliFilesTitle = [
        'If You are Happy And You Know It',
        'The Wheels On The Bus',
        '  ',
        '  ',
        ' ',
        '',
      ];
      
      const ppdFilesTitle = [
        ' OST. ',
        ' OST. ',
      ];

    const handleClick = () => {
        setModalOpen(!isModalOpen);
    }
    const handleTabClick = (tab) => {
        setActiveTab(tab);
    }
    const handleIconClick = (Icon)=>{
        setActiveIcon(Icon);
    }
    const handleDrop = (event) => {
        event.preventDefault();
        const file = event.dataTransfer.files[0];
        if (file) {  
            setSelectAudio(file); //     
            const fullName = file.name;
            setFileName(fullName); //    
            const extension = fullName.slice(fullName.lastIndexOf('.') + 1);
            setFileExtension(extension); //  
            validateAndPreviewFile(file);
        }
    }

    const handleDragOver = (event) => {
        event.preventDefault();
    }

   

    const validateAndPreviewFile = (file) => {
        if(!isFileFromFreeTab && file && file.type !== 'audio/mpeg' && file.type !== 'audio/mp3') {
            setErrorMessage("  . .mp3    .");
            setSelectAudio(null); //    null 
            setFileName(null);
        } else {
            setSelectAudio(file); //   
            // console.log(file); //    
            // console.log({selectAudio});
        }
    }

  


    const fileInputRef = useRef(); //    

    const handleUploadClick = () => {
        fileInputRef.current.click(); //    
    }

    const handleModifyClick = () => {
        if(fileInputRef.current) {
            fileInputRef.current.click(); //   input   
        }
    }

    const shortenFileName = (fullName) => {
        const nameParts = fullName.split('.');
        const extension = nameParts.pop();
        const name = nameParts.join('.');
        const shortName = name.length > 30 ? name.slice(0, 30) + '...' : name;
        return `${shortName}.${extension}`;
    }

   
    const handleFileUpload = event => {
        const file = event.target.files[0];
        if (file) {  
            setSelectAudio(file); //     
            const fullName = file.name;
            setFileName(fullName); //    
            const extension = fullName.slice(fullName.lastIndexOf('.') + 1);
            setFileExtension(extension); //  
        }
    }

      

    const handleCloseClick = () => {
        if(selectAudio) {
            setErrorModalOpen(true);
        } else {
            setModalOpen(false);
        }
        if (audioRef.current) {
            audioRef.current.pause();
            audioRef.current = null;  
            setPlaying(null);
        }
    }

    const handleErrorModalClose = () => {
        setErrorModalOpen(false);
        
    }

    const handleModalClose = () => {
        setErrorModalOpen(false);
        setModalOpen(false);
        setSelectAudio(null);
        setFileName(null);
        setSelected(null);
      
    }


    const handleFileSelect = (file, index) => {
        //     .
        if (timerId) {
            clearTimeout(timerId);
        }

        //    
        setSelected(index);
    
        //    'fileName'  
        setFileName(file);
    
        //    URL 'selectAudio'  
        const audioUrl = `http://13.125.65.215/BS/CommonRes/sound/${activeIcon}/${file}`;
        setSelectAudio(audioUrl);

        setIsFileFromFreeTab(true);
    }



      const handleCloseAndConfirm = () => {
        if (selectAudio) { //     
            setModalOpen(false);
            
        }
        if(audioRef.current){
            audioRef.current.pause();
            audioRef.current = null;  
            setPlaying(null);
        }
    }

    const handleFileTitle = (Title)=>{
        setMusicName(Title);
    }
      
      
    useEffect(() => {
        setSelected(null);
        setSelectAudio(null);
    }, [activeIcon]); 
      

    useEffect(() => {
        if (selectAudio) {
            validateAndPreviewFile(selectAudio); //  
            // console.log(selectAudio.name); //   
        }
    }, [selectAudio]);

    useEffect(() => {
        if(errorMessage) {
            setTimeout(() => {
                setErrorMessage(null);
            }, 3000);
        }
    }, [errorMessage]);

   
      useEffect(() => {
        if (activeTab !== 'free') {
        
          setPlaying(null);
      
          //  
          if (audioRef.current) {
            audioRef.current.pause();
          }
        }
      }, [activeTab]);

    //   useEffect(() => {
    //     console.log('audioRef:', audioRef.current);
    //     console.log('playing:', playing);
    // }, [audioRef.current, playing]);
      
      
      
    return(
        <>
        <div className='make_contents_bgm_wrap' onClick={handleClick}>
            <div className='make_contents_bgm1'>
                {selectAudio ? ( 
                
                    <div className='make_contents_bg1_thumbnail'>
                        <div className='make_contents_txt_preview'>
                            <div><p style={{wordWrap:'break-word',wordBreak:'keep-all'}}>{musicName}</p></div> 
                            <div className='make_contents_bgm_preview_logo'><Note/></div>
                        </div>
                        <div className='make_contents_bg1_thumbnail_circle'> <p></p> </div>
                    </div> 
                ) : (
                    <Bgicon/>
                )}
            </div>
            <div className='make_contents_bgm2'>
            {selectAudio ? (
                    <>
                    <div className='make_contents_bgtitle1_wrap'>
                        <p className='make_contents_title1'>BGM </p>
                    </div>   
                    </>
                    
                ):(
                    <>
                    <p className='make_contents_title1'>BGM </p>
                    <p className='make_contents_title2'>   <br/>   BGM<br/>  .</p>

                    </>
                    
                )}
                
            </div>
        </div>
        {isModalOpen && (
            <>
            <div className='overlay'></div>
            <div className='make_modal'>
                <div className='make_modal_header'>
                    <p>{moduleTitle}</p>
                    <div className='make_modal_header_close' onClick={handleCloseClick}>
                        <Close/>
                    </div>
                </div>
                    <div className='make_modal_content_wrap'>
                        <div className='make_modal_nev'>
                            <div className={activeTab === 'upload' ? 'make_modal_nev_li active' : 'make_modal_nev_li'} onClick={() => handleTabClick('upload')}>
                                <p>BGM  </p>
                            </div>
                            <div  className={activeTab === 'free' ? 'make_modal_nev_li active' : 'make_modal_nev_li'} onClick={() => handleTabClick('free')}>
                                <p> BGM</p>
                            </div>
                        </div>
                        <div className='make_modal_content'>
                        {activeTab === 'upload' && (
                                <div className='make_modal_bg_upload_wrap'  onDrop={handleDrop} onDragOver={handleDragOver}>
                                    {errorMessage && <div className="error_wrong-image"><p>  . .mp3  .</p></div>} 
                                    {/*   */}
                                    {selectAudio ? ( //     
                                        <>
                                        
                                        <div className='make_modal_bg_upload_img'>
                                            <div className='make_modal_bgm_upload_file_wrap'>
                                                <p>{shortenFileName(fileName)}</p> {/*      */}
                                            </div>
                                        </div>
                                        
                                        <input type="file" ref={fileInputRef} style={{display: 'none'}} onChange={handleFileUpload} accept="audio/mpeg" />
                                        <div className='make_modal_bg_modify_btn' onClick={handleModifyClick}></div>
                                        </>
                                        
                                         
                                    ):( //        

                                        <div className='make_modal_bg_upload'>
                                            
                                            <div><Greybg/></div>
                                            <p>   !<br/>(MP3  )</p>
                                            <input type="file" ref={fileInputRef} style={{display: 'none'}} onChange={handleFileUpload} accept="audio/mpeg" />
                                            <div className='make_modal_bg_upload_btn' onClick={handleUploadClick}> </div>
                                        </div>

                                    )}
                                    
                                </div>
                                
                            )}
                            {activeTab ==='free' &&(

                                <>
                                <div className='make_modal_free_bgm'>
                                <div className='make_modal_free_bg'>
                                    <div className='make_modal_free_bg_icons'>
                                        <div className={activeIcon === 'mtc' ? 'make_modal_free_bg_icon active' : 'make_modal_free_bg_icon' } 
                                        onClick={() => {handleIconClick('mtc');
                                                if(audioRef.current){
                                                    audioRef.current.pause();
                                                    audioRef.current = null;  
                                                    setPlaying(null);
                                                }}}>
                                            <img src="http://13.125.65.215/BS/CommonRes/icon/common_icon_mtc.png" alt=""/>
                                        </div>
                                        <div className={activeIcon === 'omnom' ? 'make_modal_free_bg_icon active' : 'make_modal_free_bg_icon' } 
                                        onClick={() => {handleIconClick('omnom');
                                        if(audioRef.current){
                                            audioRef.current.pause();
                                            audioRef.current = null;  
                                            setPlaying(null);
                                        }}}>
                                            <img src="http://13.125.65.215/BS/CommonRes/icon/common_icon_omnom.png" alt=""/>
                                        </div>
                                        <div className={activeIcon === 'poli' ? 'make_modal_free_bg_icon active' : 'make_modal_free_bg_icon' } 
                                        onClick={() => {handleIconClick('poli');
                                        if(audioRef.current){
                                            audioRef.current.pause();
                                            audioRef.current = null;  
                                            setPlaying(null);
                                        }}}>
                                            <img src="http://13.125.65.215/BS/CommonRes/icon/common_icon_poli.png" alt=""/>
                                        </div>
                                        <div className={activeIcon === 'pangpang' ? 'make_modal_free_bg_icon active' : 'make_modal_free_bg_icon' } 
                                        onClick={() => {handleIconClick('pangpang');
                                        if(audioRef.current){
                                            audioRef.current.pause();
                                            audioRef.current = null;  
                                            setPlaying(null);
                                        }}}>
                                            <img src="http://13.125.65.215/BS/CommonRes/icon/common_icon_ppd.png" alt=""/>
                                        </div>
                                    </div>
                                    <div className='make_modal_free_bg_contents_wrap'>
                                        <div className='make_modal_free_bg_contents'>
                                       

                                        {activeIcon === 'mtc' &&(
                                            <div className='make_modal_free_bgm_contents_files'>
                                                {mtcFiles.map((file, index) => (
                                                    <div key={index} className='make_modal_free_bgm_contents_file' 
                                                    onClick={() => {handleFileSelect(file, index);
                                                    handleFileTitle(mtcFilesTitle[index])}}>
                                                        <div className='make_modal_free_bgm_contents_file_name'>
                                                            {selected === index ? <Radioboxon/> : <Radioboxoff/>}
                                                            <p>{mtcFilesTitle[index]}</p>
                                                        </div>
                                                        
                                                        <div className="make_modal_free_bgm_btn">
                                                            {playing === index ? 
                                                                <Playstop onClick={(e) => { 
                                                                    e.stopPropagation(); 
                                                                    if (audioRef.current) { 
                                                                        audioRef.current.pause(); 
                                                                        audioRef.current = null; 
                                                                        setPlaying(null); 
                                                                    } 
                                                                }}/>
                                                            : 
                                                            <Playgo onClick={(e) => { 
                                                                e.stopPropagation(); 
                                                                if (audioRef.current) { 
                                                                    audioRef.current.pause();
                                                                    audioRef.current = null;
                                                                } 
                                                                setPlaying(index); 
                                                            
                                                                const newAudio = new Audio(`http://13.125.65.215/BS/CommonRes/sound/${activeIcon}/${mtcFiles[index]}`);
                                                                audioRef.current = newAudio;  // audioRef.current    
                                                            
                                                                newAudio.play().then(() => {
                                                                    //     .
                                                                    if (timerId) {
                                                                        clearTimeout(timerId);
                                                                    }
                                                            
                                                                    //  , 30     .
                                                                    const newTimerId = setTimeout(() => {
                                                                        // console.log("5 seconds passed, stopping music."); //   
                                                                        if(audioRef.current) {
                                                                            audioRef.current.pause();
                                                                            audioRef.current = null;
                                                                            setPlaying(null);
                                                                        }
                                                                    }, 30000);
                                                            
                                                                    //   ID .
                                                                    setTimerId(newTimerId);
                                                                }).catch((error) => {
                                                                    // audio.play() was interrupted
                                                                    // console.log(error);
                                                                });
                                                            }}/>

                                                            
                                                            
                                                            }
                                                        </div>
                                                    </div>
                                                ))}
                                            </div>  
                                        )}
                                        
                                        {activeIcon === 'omnom' && (
                                        
                                        <div className='make_modal_free_bgm_contents_files'>
                                                {omnomFiles.map((file, index) => (
                                                    <div key={index} className='make_modal_free_bgm_contents_file' 
                                                    onClick={() => {handleFileSelect(file, index);
                                                        handleFileTitle(omnomFilesTitle[index])}}>
                                                        <div className='make_modal_free_bgm_contents_file_name'>
                                                            {selected === index ? <Radioboxon/> : <Radioboxoff/>}
                                                            <p>{omnomFilesTitle[index]}</p>
                                                        </div>
                                                        
                                                        <div className="make_modal_free_bgm_btn">
                                                            {playing === index ? 
                                                                <Playstop onClick={(e) => { 
                                                                    e.stopPropagation(); 
                                                                    if (audioRef.current) { 
                                                                        audioRef.current.pause(); 
                                                                        audioRef.current = null; 
                                                                        setPlaying(null); 
                                                                    } 
                                                                }}/>
                                                            : 
                                                            <Playgo onClick={(e) => { 
                                                                e.stopPropagation(); 
                                                                if (audioRef.current) { 
                                                                    audioRef.current.pause();
                                                                    audioRef.current = null;
                                                                } 
                                                                setPlaying(index); 
                                                            
                                                                const newAudio = new Audio(`http://13.125.65.215/BS/CommonRes/sound/${activeIcon}/${omnomFiles[index]}`);
                                                                audioRef.current = newAudio;  // audioRef.current    
                                                            
                                                                newAudio.play().then(() => {
                                                                    //     .
                                                                    if (timerId) {
                                                                        clearTimeout(timerId);
                                                                    }
                                                            
                                                                    //  , 30     .
                                                                    const newTimerId = setTimeout(() => {
                                                                        // console.log("5 seconds passed, stopping music."); //   
                                                                        if(audioRef.current) {
                                                                            audioRef.current.pause();
                                                                            audioRef.current = null;
                                                                            setPlaying(null);
                                                                        }
                                                                    }, 30000);
                                                            
                                                                    //   ID .
                                                                    setTimerId(newTimerId);
                                                                }).catch((error) => {
                                                                    // audio.play() was interrupted
                                                                    // console.log(error);
                                                                });
                                                            }}/>

                                                            
                                                            
                                                            }
                                                        </div>
                                                    </div>
                                                ))}
                                            </div>
                                        )}

                                        {activeIcon === 'poli' && (
                                        <div className='make_modal_free_bgm_contents_files'>
                                        {poliFiles.map((file, index) => (
                                            <div key={index} className='make_modal_free_bgm_contents_file' 
                                            onClick={() => {handleFileSelect(file, index);
                                                handleFileTitle(poliFilesTitle[index])}}>
                                                <div className='make_modal_free_bgm_contents_file_name'>
                                                    {selected === index ? <Radioboxon/> : <Radioboxoff/>}
                                                    <p>{poliFilesTitle[index]}</p>
                                                </div>
                                                
                                                <div className="make_modal_free_bgm_btn">
                                                    {playing === index ? 
                                                        <Playstop onClick={(e) => { 
                                                            e.stopPropagation(); 
                                                            if (audioRef.current) { 
                                                                audioRef.current.pause(); 
                                                                audioRef.current = null; 
                                                                setPlaying(null); 
                                                            } 
                                                        }}/>
                                                    : 
                                                    <Playgo onClick={(e) => { 
                                                        e.stopPropagation(); 
                                                        if (audioRef.current) { 
                                                            audioRef.current.pause();
                                                            audioRef.current = null;
                                                        } 
                                                        setPlaying(index); 
                                                    
                                                        const newAudio = new Audio(`http://13.125.65.215/BS/CommonRes/sound/${activeIcon}/${poliFiles[index]}`);
                                                        audioRef.current = newAudio;  // audioRef.current    
                                                    
                                                        newAudio.play().then(() => {
                                                            //     .
                                                            if (timerId) {
                                                                clearTimeout(timerId);
                                                            }
                                                    
                                                            //  , 30     .
                                                            const newTimerId = setTimeout(() => {
                                                                // console.log("5 seconds passed, stopping music."); //   
                                                                if(audioRef.current) {
                                                                    audioRef.current.pause();
                                                                    audioRef.current = null;
                                                                    setPlaying(null);
                                                                }
                                                            }, 30000);
                                                    
                                                            //   ID .
                                                            setTimerId(newTimerId);
                                                        }).catch((error) => {
                                                            // audio.play() was interrupted
                                                            // console.log(error);
                                                        });
                                                    }}/>

                                                    
                                                    
                                                    }
                                                </div>
                                            </div>
                                        ))}
                                    </div> 
                                        )}

                                        {activeIcon === 'pangpang' && (
                                       <div className='make_modal_free_bgm_contents_files'>
                                       {ppdFiles.map((file, index) => (
                                           <div key={index} className='make_modal_free_bgm_contents_file' 
                                           onClick={() => {handleFileSelect(file, index);
                                            handleFileTitle(ppdFilesTitle[index])}}>
                                               <div className='make_modal_free_bgm_contents_file_name'>
                                                   {selected === index ? <Radioboxon/> : <Radioboxoff/>}
                                                   <p>{ppdFilesTitle[index]}</p>
                                               </div>
                                               
                                               <div className="make_modal_free_bgm_btn">
                                                   {playing === index ? 
                                                       <Playstop onClick={(e) => { 
                                                           e.stopPropagation(); 
                                                           if (audioRef.current) { 
                                                               audioRef.current.pause(); 
                                                               audioRef.current = null; 
                                                               setPlaying(null); 
                                                           } 
                                                       }}/>
                                                   : 
                                                   <Playgo onClick={(e) => { 
                                                       e.stopPropagation(); 
                                                       if (audioRef.current) { 
                                                           audioRef.current.pause();
                                                           audioRef.current = null;
                                                       } 
                                                       setPlaying(index); 
                                                   
                                                       const newAudio = new Audio(`http://13.125.65.215/BS/CommonRes/sound/${activeIcon}/${ppdFiles[index]}`);
                                                       audioRef.current = newAudio;  // audioRef.current    
                                                   
                                                       newAudio.play().then(() => {
                                                           //     .
                                                           if (timerId) {
                                                               clearTimeout(timerId);
                                                           }
                                                   
                                                           //  , 30     .
                                                           const newTimerId = setTimeout(() => {
                                                            //    console.log("5 seconds passed, stopping music."); //   
                                                               if(audioRef.current) {
                                                                   audioRef.current.pause();
                                                                   audioRef.current = null;
                                                                   setPlaying(null);
                                                               }
                                                           }, 30000);
                                                   
                                                           //   ID .
                                                           setTimerId(newTimerId);
                                                       }).catch((error) => {
                                                           // audio.play() was interrupted
                                                        //    console.log(error);
                                                       });
                                                   }}/>

                                                   
                                                   
                                                   }
                                               </div>
                                           </div>
                                       ))}
                                   </div>  
                                        )}

                                        
                                        
                                        </div>
                                    </div>
                                </div>
                                </div>
                                
                                </>

                            )}
                            
                                    
                        </div>  
                        <div className='make_modal_footer'>
                                <div className={`make_modal_bg_confirm_btn ${selectAudio ? 'active' : ''}`} onClick={handleCloseAndConfirm}><p></p></div>
                            </div>                    
                    </div>
                    {errorModalOpen && (

                        <div className='error_modal-close'>
                        <div className='error_modal_header'>
                            <p> </p>
                            <div className='make_modal_header_close' onClick={handleErrorModalClose}>
                                <Close/>
                            </div>
                        </div>
                        <div className='error_modal-close_content_wrap'>
                            <div className='error_modal-close_content'>
                                <Warning/>
                                <p>      .<br/> ?</p>
                                
                            </div>
                            <div className='error_modal_footer'>
                                <div className='make_modal_bg_confirm_btn2-1' onClick={handleErrorModalClose}><p></p></div>
                                <div className='make_modal_bg_confirm_btn2-2   active' onClick={handleModalClose}><p></p></div>

                            </div>   
                        </div>
                        </div>

                        )}
                    
                
            </div>
            </>
            
        )}

        </>
        
    )
}

export default Makebgm;