import React, {useState,useEffect,useRef } from 'react';
import '../makecontents.css';
// import QuizItem from './makequiz_multiple_item';

import {ReactComponent as Bgicon} from '../../../../img/homepage/makecontents/quiz_pic.svg';
import {ReactComponent as Close} from '../../../../img/homepage/makecontents/close.svg';
import {ReactComponent as Warning} from '../../../../img/homepage/makecontents/icon-warning.svg';
import {ReactComponent as Setting} from '../../../../img/homepage/makecontents/icon-settings.svg';
import {ReactComponent as Optionarrow} from '../../../../img/homepage/makecontents/option_arrow.svg';
import {ReactComponent as Toggleon} from '../../../../img/homepage/makecontents/toggle_on.svg';
import {ReactComponent as Toggleoff} from '../../../../img/homepage/makecontents/toggle_off.svg';
import { useBootstrapBreakpoints } from 'react-bootstrap/esm/ThemeProvider';

function Makequiz({moduleTitle,listItems,setListItems,
    setQuizItemsState,queOption,setQueOption,queCnt,setQueCnt,
    useHint,setUseHint,usePopup, setUsePopUp}){

    const [isModalOpen, setModalOpen] = useState(false);
    const [errorModalOpen, setErrorModalOpen] = useState(false);
    const [apply, setapply] = useState(false); //
    const [queOptionMenu, setQueOptionMenu] = useState(false) // 
    const [queCntMenu, setQueCntMenu] = useState(false) // 
    const [placeholderText, setPlaceholderText] = useState("");
    const [applyPopup, setApplyPopup] = useState(false);

    const [textErr, setTextErr] = useState({}); //     

    const [textMeanErr, setTextMeanErr] = useState([]);
    let isConfirmBtnActive;

    const specialChar = /[ `!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?~]/; //  
    const number = /\d/; //    

    const queOptionRef = useRef(null);
    const queCntRef = useRef(null);

    if (usePopup) {
        isConfirmBtnActive = listItems.length > 0 && listItems.every(item => item.answer1 !== '' && !item.answer1.includes(' ')&& !specialChar.test(item.answer1) && !number.test(item.answer1) && item.answer1.length >= 2 && item.answer1.length <=13 && item.answer2 !== '' && item.answer1.length <=40);
    } else {
        isConfirmBtnActive = listItems.length > 0 && listItems.every(item => item.answer1 !== '' && !item.answer1.includes(' ')&& !specialChar.test(item.answer1)&& !number.test(item.answer1)  && item.answer1.length >= 2 && item.answer1.length <=13);
    }   



    const handleClick = () => {
        setModalOpen(!isModalOpen);
    }

    const handleCloseClick = () => {
        if(listItems.length > 0) {
            setErrorModalOpen(true); //   
            
        } else {
            setModalOpen(false); //    
        }
    }

    const handleErrorModalClose = () => {
        setErrorModalOpen(false);
       
        
    }

    const handleModalClose =()=>{
        setErrorModalOpen(false);
        setapply(false);
        setListItems([]);
        setQuizItemsState([]);
        setModalOpen(false);

    }
    


    const handleCloseAndConfirm = () => {
        if (isConfirmBtnActive) { 
            setModalOpen(!isModalOpen);
        }
    }

   


    const handleDropQueOption =()=>{
        setQueOptionMenu(!queOptionMenu);
    }
    const handleDropQueCnt=()=>{
        setQueCntMenu(!queCntMenu);
    }

   
    const handleQueOptionSelect = (option) => {
        setQueOption(option);
        setQueOptionMenu(false);
    }

    const handleQueCntSelect = (number) => {
        setQueCnt(number);  
        setQueCntMenu(false);
    }

   
    const handleHint =() =>{
        setUseHint(!useHint);
    }
    const handlePopup =()=>{
        setUsePopUp(!usePopup);
    }


   
    const handleApply = ()=>{      
        const itemCount = queCnt;  
    
        let newItems = Array.from({ length: itemCount }, (_, i) => ({
            id: i + 1,
            answer1: '',
            answer2: ''
        }));
    
        setPlaceholderText(getPlaceholderText());
    
        setApplyPopup(usePopup);
        setListItems(newItems);
        setapply(true);     
    };

    const handleErrOff=()=>{
        setTextErr(false)
        setTextMeanErr(false)
    }

    
    const handleInputChange1 = (e, index) => {
        const { value } = e.target;
        setListItems(prevItems => prevItems.map((item, i) => 
            i === index ? {...item, answer1: value} : item
        ));
    
        const regex = /[ `!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?~]/; //    
        const number = /\d/; //    
    
        if (value.length === 1 || number.test(value)) {
            setTextErr(prevErr => ({ ...prevErr, [index]: true })); //     true 
        } else if (value.length >= 14 || regex.test(value)) {
            setTextErr(prevErr => ({ ...prevErr, [index]: true })); //     true 
        } else {
            setTextErr(prevErr => ({ ...prevErr, [index]: false })); //     false 
        }
    };
    
    
      const handleInputChange2 = (e, index) => {
        const { value } = e.target;
        setListItems(prevItems => prevItems.map((item, i) =>
          i === index ? {...item, answer2: value} : item
        ));
      
        if (value.length >= 40) { //  40  
          setTextMeanErr(prevErr => ({ ...prevErr, [index]: true })); //     true 
        } else {
          setTextMeanErr(prevErr => ({ ...prevErr, [index]: false })); //     false 
        }
      };
      

    const getPlaceholderText = () => {
        if (queOption === '-') {
            return "   .";
        } else if (queOption === '-') {
            return "   .";
        } else if (queOption === '') {
            return "   .";
        } else {
            return "";
        }
    };

    const handleClickOutside1 = (event) => {
        if (queOptionRef.current && !queOptionRef.current.contains(event.target)) {
            setQueOptionMenu(false);
        }
      };


    const handleClickOutside2 = (event) => {
        if (queCntRef.current && !queCntRef.current.contains(event.target)) {
            setQueCntMenu(false);
        }
      };

      useEffect(() => {
        document.addEventListener('click', handleClickOutside1);
        document.addEventListener('click', handleClickOutside2);
        return () => {
            document.removeEventListener('click', handleClickOutside1);
            document.removeEventListener('click', handleClickOutside2);
        };
      }, []);

    useEffect(() => {
        // textErr     3    .
        const errorTimers = Object.keys(textErr).map(key => 
            textErr[key] && setTimeout(() => {
                setTextErr(prev => ({...prev, [key]: false}));
            }, 3000)
        );
    
        //  unmount textErr    .
        return () => errorTimers.forEach(timerId => clearTimeout(timerId));
    }, [textErr]);
    
    useEffect(() => {
        // textMeanErr     3    .
        const errorTimers = Object.keys(textMeanErr).map(key => 
            textMeanErr[key] && setTimeout(() => {
                setTextMeanErr(prev => ({...prev, [key]: false}));
            }, 3000)
        );
    
        //  unmount textMeanErr    .
        return () => errorTimers.forEach(timerId => clearTimeout(timerId));
    }, [textMeanErr]);

    return(
        <>
        <div className='make_contents_quiz_wrap' onClick={handleClick}>
            <div className='make_contents_quiz1'>
            {listItems.length > 0 ?(
                <>
                <div className='make_contents_bg1_thumbnail'>
                    <div className='thumbnail_quiz_font'><p>{listItems[0].answer1}</p></div>
                     
                
                <div className='make_contents_bg1_thumbnail_circle'> <p></p> </div>
                </div>
                </>
                
                
            ):(
                <Bgicon/>
            ) }
                
            </div>
            <div className='make_contents_quiz2'>
                {listItems.length > 0 ? (
                    <>
                     <div className='make_contents_bgtitle1_wrap'>
                        <p className='make_contents_title1'> </p>
                    </div>  
                    </>
                ):(
                    <>
                    <p className='make_contents_title1'></p>
                <p className='make_contents_title2'>   <br/>   <br/>   .</p>
                    </>
                )}
                
            </div>
        </div>

        {isModalOpen && (
            <>
            <div className='overlay'></div>
            <div className='make_modal'>
                <div className='make_modal_header'>
                    <p>{moduleTitle}</p>
                    <div className='make_modal_header_close' onClick={handleCloseClick}>
                        <Close/>
                    </div>
                </div>
                <div className='make_modal_content_wrap'>
                    <div className='make_modal_nev'>
                        <div className='make_modal_nev_li active'>
                            <p> </p>
                        </div>
                        <div className='make_modal_content'>
                        <div className='make_modal_content_quiz_wrap'>
                            <div className='make_modal_content_quiz_left'>
                                <div className='make_modal_content_quiz_setting'>
                                    <div className='quiz_setting_header'>
                                        <div className='setting_icon'><Setting/></div>
                                        <div className='setting_title'> </div>
                                    </div>
                                    <div className='quiz_setting_contents'>
                                        <div className='quiz_setting_content'>
                                            <p className='quiz_setting_content_title'></p>
                                            <div className='quiz_setting_option'>
                                                <div className='quiz_setting_option_btn' style={{width: 7.8125+'rem', marginRight:1.3+"rem"}} onClick={handleDropQueOption} ref={queOptionRef}>
                                                    <p>{queOption}</p>
                                                    <Optionarrow/>

                                                    {queOptionMenu && (
                                                        <div className='drop_down_number' style={{width: 7.8125+'rem',height:4.9+'rem'}}>
                                                            {['-', '-', ''].map(num => (
                                                                <div key={num} 
                                                                    className='drop_down_number_item' 
                                                                    onClick={() => {handleQueOptionSelect(num)
                                                                        handleErrOff()}}>
                                                                    {num}
                                                                </div>
                                                            ))}
                                                        </div>
                                                    )}
                                                    
                                                </div>
                                               
                                            </div>
                                            
                                        </div>
                                        <div className='quiz_setting_content'>
                                            <p className='quiz_setting_content_title'> </p>
                                            <div className='quiz_setting_option'>
                                                <div className='quiz_setting_option_btn'  style={{width: 7.8125+'rem',  marginRight:0+"rem"}} onClick={handleDropQueCnt} ref={queCntRef}>
                                                    <p>{queCnt}</p>
                                                    <Optionarrow/>

                                                    {queCntMenu &&(
                                                       <div className='drop_down_number'  style={{width: 7.8125+'rem'}}>
                                                       {[3,4,5,6,7,8,9,10].map(num => (
                                                           <div key={num} 
                                                               className='drop_down_number_item' 
                                                               onClick={() => {handleQueCntSelect(num)
                                                                handleErrOff()}}>
                                                               {num}
                                                           </div>
                                                       ))}
                                                   </div>
                                                    )}
                                                </div>
                                                <p className='quiz_setting_content_title' style={{marginRight:0+'rem',marginLeft:0.5+'rem'}}></p>
                                            </div>                                           
                                        </div>  
                                        <div className='quiz_setting_content'>
                                            <p className='quiz_setting_content_title'></p>
                                            <div className='quiz_setting_option'>
                                                <div onClick={handleHint}>
                                                    {useHint &&(<Toggleon/>)}
                                                    {!useHint &&(<Toggleoff/>)}
                                                </div>                                                
                                            </div>                                           
                                        </div> 
                                        <div className='quiz_setting_content'>
                                            <p className='quiz_setting_content_title'>()</p>
                                            <div className='quiz_setting_option'>
                                                <div onClick={handlePopup}>
                                                    {usePopup &&(<Toggleon/>)}
                                                    {!usePopup &&(<Toggleoff/>)}
                                                </div>                                                
                                            </div>                                                                                       
                                        </div>                                               
                                    </div>
                                    
                                    <div className='quiz_setting_btn' onClick={handleApply}><p></p></div>
                                    
                                </div>

                                <div className='caution_wrap' style={{marginLeft:0,marginBottom:1+'rem'}}>
                                    <div className='caution'>
                                        <p></p>
                                        <ul>
                                            <li>  2~13    .</li>
                                            <li> , ,   .</li>
                                            <li>   1    .</li>
                                        </ul>
                                    </div>

                                </div>
        
                            </div>
                            <div className='make_modal_content_quiz_right'>

                            {apply ? (
                                <>
                                {applyPopup ? (
                                    <div className='make_modal_content_quiz_list  mean_input'>
                                        {listItems.map((item, index) => (
                                            <div className='search_input_wrap' key={index}>
                                                <p>{index + 1}.</p>
                                                <div className='search_input_text'>
                                                    <input 
                                                    type="text" 
                                                    value={item.answer1} 
                                                    onChange={(e) => handleInputChange1(e, index)}
                                                    placeholder={placeholderText}
                                                    style={{marginRight:1+'rem'}}
                                                    minLength={2}
                                                    maxLength={13}
                                                    />
                                                    {textErr[index] &&(
                                                    <>
                                                    <div className='search_input_text_err'
                                                            onClick={() => setTextErr(prev => ({...prev, [index]: false}))}
                                                            style={{left:-1+'%'}}><p> 1  14/// .</p></div>
                                                    </>
                                                    )}
                                                </div>
                                                <div className='search_input_text'>
                                                    <input 
                                                    type="text"
                                                    value={item.answer2} 
                                                    onChange={(e) => handleInputChange2(e, index)}
                                                    placeholder="  ."
                                                    maxLength={40}
                                                   />
                                                    {textMeanErr[index] &&(
                                                    <>
                                                    <div className='search_input_text_err'
                                                            onClick={() => setTextMeanErr(prev => ({...prev, [index]: false}))}
                                                            style={{left:27+"%"}}><p> 40  .</p></div>
                                                    </>
                                                    )}
                                                </div>
                                               

                                                
                                            </div>
                                        ))}
                                    </div>
                                ):(
                                    <div className='make_modal_content_quiz_list'>
                                        {listItems.map((item, index) => (
                                            <div className='search_input_wrap' key={index}>
                                                <p>{index + 1}.</p>
                                                <div className='search_input_text'>
                                                    <input 
                                                    type="text" 
                                                    value={item.answer1} 
                                                    onChange={(e) => handleInputChange1(e, index)}
                                                    placeholder={placeholderText}
                                                    minLength={2}
                                                    maxLength={13} />
                                                    {textErr[index] &&(
                                                    <>
                                                    <div className='search_input_text_err'
                                                            onClick={() => setTextErr(prev => ({...prev, [index]: false}))}
                                                            ><p> 1  14// .</p></div>
                                                    </>
                                                    )}
                                                </div>
                                                
                                            </div>
                                        ))}
                                    </div>
                                )}
                                </>
                            ):(
                                <>
                                <p className='content_quiz_before_font'>     .</p>
                                </>
                            )}                   
                               

                            </div>
                        </div>


                        
                        
                    </div>
                    </div>
                    <div className='make_modal_footer'>
                                <div className={`make_modal_bg_confirm_btn ${isConfirmBtnActive  ? 'active' : ''}`} onClick={handleCloseAndConfirm}><p></p></div>

                        </div>
                </div>

                {errorModalOpen && (
                    <div className='error_modal-close'>
                    <div className='error_modal_header'>
                        <p> </p>
                        <div className='make_modal_header_close' onClick={handleErrorModalClose}>
                            <Close/>
                        </div>
                    </div>
                    <div className='error_modal-close_content_wrap'>
                        <div className='error_modal-close_content'>
                            <Warning/>
                            <p>      .<br/> ?</p>
                            
                        </div>
                        <div className='error_modal_footer'>
                            <div className='make_modal_bg_confirm_btn2-1' onClick={handleErrorModalClose}><p></p></div>
                            <div className='make_modal_bg_confirm_btn2-2   active' onClick={handleModalClose}><p></p></div>

                        </div>   
                    </div>
                    </div>
                )}
            </div>

            </>

        )}

        </>
        
    )
}

export default Makequiz;