import React, { useState } from 'react';
import {ReactComponent as Logo} from '../../img/loginpage/logo.svg';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';



const LoginForm = ()=>{
     

    const navigate = useNavigate();

    const [formData, setFormData] = useState({
      email: '',
      password: '',
    });
  
    const [errorMessages, setErrorMessages] = useState({
      emailError: '',
      passwordError: '',
      loginError: '',
    });
  
    const handleChange = (e) => {
      const { name, value } = e.target;
      setFormData((prevData) => ({
        ...prevData,
        [name]: value,
      }));
  
      //       
      setErrorMessages({
        emailError: '',
        passwordError: '',
        loginError: '',
        emptyFieldError: '',
      });
    };
  
    const handleSubmit = (e) => {
      e.preventDefault();
  
      //    .
      if (!formData.email && !formData.password) {
        setErrorMessages((prevErrors) => ({
          ...prevErrors,
          emptyFieldError: '  .',
        }));
        return;
      }

      if (!formData.email) {
        setErrorMessages((prevErrors) => ({
          ...prevErrors,
          emailError: ' .',
        }));
        return;
      }
  
      if (!formData.password) {
        setErrorMessages((prevErrors) => ({
          ...prevErrors,
          passwordError: ' .',
        }));
        return;
      }
  
      //    .
      // axios.post('http://13.125.65.215/BS/login.php', {
      //   id: formData.email,
      //   pw: formData.password,
      // })
      //   .then((response) => {
      //       if (response.data.status === 'success') {
      //           //    
      //           // console.log(' :', response.data);
      //           navigate('/logged');
      //       } else {
      //           //  
      //           // console.log(' ');
      //           // console.log(' :', response.data);
      //           setErrorMessages((prevErrors) => ({
      //               ...prevErrors,
      //               loginError: '    .    ',
      //           }));
      //       }
      //   })
      //   .catch((error) => {
      //       console.error(':', error);
      //   });

      // 
    };


  

    return(
        <div className='bg_login_content'>
          <div className='bg_login_content_logo'>
          <Logo/>
          </div>
          <div className='bar'></div>
          <p className='login_font'>LOGIN</p>
          

          <form onSubmit={handleSubmit}>

            <div className='bg_login_content_input'>
              <label htmlFor='email' className='content_label'>email</label>
              <input 
              type="email" 
              name='email' 
              value={formData.email}
              onChange={handleChange}
              id='email'

              className='content_input' 
              placeholder=' '/>
            </div>

            <div className='bg_login_content_input'>
              <label htmlFor='password' className='content_label'>password</label>
              <input 
              
              type="password"
              name="password"
              value={formData.password}
              onChange={handleChange}
              
              className='content_input' 
              id='password' 
              placeholder=' '/>
            </div>

            <div className='err'>
            <p className='err_msg'>{errorMessages.emailError}</p>
            <p className='err_msg'>{errorMessages.passwordError}</p>
            <p className='err_msg'>{errorMessages.loginError}</p>
            <p className='err_msg'>{errorMessages.emptyFieldError}</p>
            </div>

            <button className='blue_btn' type='submit'>LOGIN</button>

            <div className='bg_login_btn'>
              <Link className='blue_font' type='button' style={{marginRight: 30+'px'}}>/</Link>
              <Link className='blue_font' to='/login/signup'></Link>
            </div>
           
          </form>

          <div className='bg_login_footer' style={{marginTop:110+'px'}}>
            <p> BeaverBlock. ALL RIGHTS RESERVED.</p>
          </div>
          

          

         
        </div>
    )
  };

  export default LoginForm;