import React, {useState ,useRef,useEffect} from 'react';
import '../makecontents.css';
import QuizItem from './makequiz_memory_item';

import {ReactComponent as Bgicon} from '../../../../img/homepage/makecontents/quiz_pic.svg';
import {ReactComponent as Close} from '../../../../img/homepage/makecontents/close.svg';
import {ReactComponent as Warning} from '../../../../img/homepage/makecontents/icon-warning.svg';
import {ReactComponent as Setting} from '../../../../img/homepage/makecontents/icon-settings.svg';
import {ReactComponent as Optionarrow} from '../../../../img/homepage/makecontents/option_arrow.svg';




function Makequiz({moduleTitle,listItems,setListItems,setQuizItemsState,piece ,setPiece,cardoption, setCardOption}){

    const [isModalOpen, setModalOpen] = useState(false);
    const [errorModalOpen, setErrorModalOpen] = useState(false);
    // const isConfirmBtnActive = listItems.length > 0 && listItems.every(item => item.bgImage !== null && item.answer !== '');
    const [pieceNumberMenu, setPieceNumberMenu] = useState(false) //
    const [optionMenu, setOptionMenu] = useState(false) //
    const [apply, setapply] = useState(false); //
    const [tempOption, setTempOption] = useState('-');

    const pieceNumberRef = useRef(null);
    const optionRef = useRef(null);
    
    
    let isConfirmBtnActive = false;  


    const handleClick = () => {
        setModalOpen(!isModalOpen);
    }

    const handleCloseClick = () => {
        if(listItems.length > 0) {
            setErrorModalOpen(true); //   
            
        } else {
            setModalOpen(false); //    
        }
    }

    const handleErrorModalClose = () => {
        setErrorModalOpen(false);
       
        
    }

    const handleModalClose =()=>{
        setErrorModalOpen(false);
        setapply(false);
        setListItems([]);
        setQuizItemsState([]);
        setModalOpen(false);

    }


    if(listItems.length>0){
        isConfirmBtnActive = listItems.every(item => {
            if (cardoption === '-') {
                return item.bgImage1 && item.bgImage2;  // bgImage1, bgImage2     
            } else if (cardoption === '-') {
                return item.bgImage1 && item.answer2 && item.answer2.length <= 5;  // bgImage1, answer1     
            } else if (cardoption === '-') {
                return item.answer1 && item.answer2 && item.answer1.length <= 5 && item.answer2.length <= 5;  // answer1, answer2     
            }
        })
    }

    const handleCloseAndConfirm = () => {
        if (isConfirmBtnActive) { 
            setModalOpen(!isModalOpen);
            
        }
    }

    const updateListItem = (id, newBgImage1, newBgImage2, newAnswer1, newAnswer2) => {
        setListItems(prevItems => prevItems.map(item => 
            item.id === id ? {
                ...item, 
                bgImage1: newBgImage1, 
                bgImage2: newBgImage2, 
                answer1: newAnswer1,
                answer2: newAnswer2
            } : item
        ));
    };

    const handleDropPiece = ()=>{
        setPieceNumberMenu(!pieceNumberMenu);
    }
    const handleDropOption = ()=>{
        setOptionMenu(!optionMenu)
    }

    const handlePieceSelect = (number) => {
        setPiece(number);
        setPieceNumberMenu(false);
    }

    const handleOptionSelect = (option) => {
        setTempOption(option);  //     
        setOptionMenu(false);
    }


    const handleApply = ()=>{
        
        const itemCount = piece
    
        let newItems = [];
        if (tempOption === '-') {  //   
            newItems = Array.from({ length: itemCount }, (_, i) => ({
                id: i + 1,
                bgImage1: null,
                bgImage2: null
            }));
        } else if (tempOption === '-') {  //   
            newItems = Array.from({ length: itemCount }, (_, i) => ({
                id: i + 1,
                bgImage1: null,
                answer1: ''
            }));
        } else if (tempOption === '-') {  //   
            newItems = Array.from({ length: itemCount }, (_, i) => ({
                id: i + 1,
                answer1: '',
                answer2: ''
            }));
        }
       
        setListItems(newItems);
        setCardOption(tempOption);  //   cardOption 
        setapply(true);

        
    };

    const handleClickOutside1 = (event) => {
        if (pieceNumberRef.current && !pieceNumberRef.current.contains(event.target)) {
            setPieceNumberMenu(false);
        }
      };


    const handleClickOutside2 = (event) => {
        if (optionRef.current && !optionRef.current.contains(event.target)) {
          setOptionMenu(false);
        }
      };

      useEffect(() => {
        document.addEventListener('click', handleClickOutside1);
        document.addEventListener('click', handleClickOutside2);
        return () => {
            document.removeEventListener('click', handleClickOutside1);
            document.removeEventListener('click', handleClickOutside2);
        };
      }, []);


    return(
        <>
        <div className='make_contents_quiz_wrap' onClick={handleClick}>
            <div className='make_contents_quiz1'>
            {listItems.length > 0 ?(
                <>
                <div className='make_contents_bg1_thumbnail'>
                    {listItems[0].bgImage1 && (
                        <img src={listItems[0].bgImage1} alt="Quiz" />
                    )}
                     {listItems[0].answer1 && (
                        <div className='thumbnail_quiz_font'><p>{listItems[0]?.answer1}</p></div>
                    )}
                
                <div className='make_contents_bg1_thumbnail_circle'> <p></p> </div>
                </div>
                </>
                
                
            ):(
                <Bgicon/>
            ) }
                
            </div>
            <div className='make_contents_quiz2'>
                {listItems.length > 0 ? (
                    <>
                     <div className='make_contents_bgtitle1_wrap'>
                        <p className='make_contents_title1'> </p>
                    </div>  
                    </>
                ):(
                    <>
                    <p className='make_contents_title1'></p>
                <p className='make_contents_title2'>   <br/>   <br/>   .</p>
                    </>
                )}
                
            </div>
        </div>

        {isModalOpen && (
            <>
            <div className='overlay'></div>
            <div className='make_modal'>
                <div className='make_modal_header'>
                    <p>{moduleTitle}</p>
                    <div className='make_modal_header_close' onClick={handleCloseClick}>
                        <Close/>
                    </div>
                </div>
                <div className='make_modal_content_wrap'>
                    <div className='make_modal_nev'>
                        <div className='make_modal_nev_li active'>
                            <p> </p>
                        </div>
                        <div className='make_modal_content'>
                        <div className='make_modal_content_quiz_wrap'>
                            <div className='make_modal_content_quiz_left'>
                                <div className='make_modal_content_quiz_setting'>
                                    <div className='quiz_setting_header'>
                                        <div className='setting_icon'><Setting/></div>
                                        <div className='setting_title'>  </div>
                                    </div>
                                    <div className='quiz_setting_contents'>
                                        <div className='quiz_setting_content'>
                                            <p className='quiz_setting_content_title'> </p>
                                            <div className='quiz_setting_option'>
                                                <div className='quiz_setting_option_btn' style={{width: 9.375+'rem', marginRight:0}} onClick={handleDropPiece} ref={pieceNumberRef}>
                                                    <p>{piece}</p>
                                                    <Optionarrow/>

                                                    {pieceNumberMenu && (
                                                        <div className='drop_down_number' style={{width: 9.375+'rem',height:6.5+'rem'}}>
                                                            {[6, 9, 12, 15].map(num => (
                                                                <div key={num} 
                                                                    className='drop_down_number_item' 
                                                                    onClick={() => handlePieceSelect(num)}>
                                                                    {num}
                                                                </div>
                                                            ))}
                                                        </div>
                                                    )}
                                                    
                                                </div>
                                               
                                            </div>
                                            
                                        </div>
                                        <div className='quiz_setting_content'>
                                            <p className='quiz_setting_content_title'> </p>
                                            <div className='quiz_setting_option'>
                                                <div className='quiz_setting_option_btn'  style={{width: 9.375+'rem', marginRight:0}} onClick={handleDropOption} ref={optionRef}>
                                                    <p>{tempOption}</p>
                                                    <Optionarrow/>

                                                    {optionMenu &&(
                                                       <div className='drop_down_number'  style={{width: 9.375+'rem', height:5+'rem'}}>
                                                       {['-','-','-'].map(num => (
                                                           <div key={num} 
                                                               className='drop_down_number_item' 
                                                               onClick={() => handleOptionSelect(num)}>
                                                               {num}
                                                           </div>
                                                       ))}
                                                   </div>
                                                    )}
                                                </div>
                                                
                                            </div>
                                           
                                        </div>  
                                               
                                    </div>
                                    
                                    <div className='quiz_setting_btn' onClick={handleApply}><p></p></div>
                                    
                                </div>
        
                            </div>
                            <div className='make_modal_content_quiz_right'>

                                {apply ? (
                                    <>
                                <div className='make_modal_content_quiz_list'>
                                   
                                    {

                                        [...listItems].reverse().map((item, index)=>(

                                            <QuizItem 
                                            key={item.id} 
                                            id={item.id} 
                                            bgImage1={item.bgImage1} 
                                            bgImage2={item.bgImage2} 
                                            answer1={item.answer1} 
                                            answer2={item.answer2} 
                                            updateQuiz={updateListItem}
                                            cardoption={cardoption}
                                           
                                            

                                            newQuizNumber={listItems.length - index}
                                            />


                                        ))

                                    }
                                    
                                </div>
                                    </>
                                ):(
                                    <>
                                    <p className='content_quiz_before_font'>     .</p>
                                    </>
                                )}                            
                               

                            </div>
                        </div>


                        
                        
                    </div>
                    </div>
                    <div className='make_modal_footer'>
                                <div className={`make_modal_bg_confirm_btn ${isConfirmBtnActive  ? 'active' : ''}`} onClick={handleCloseAndConfirm}><p></p></div>

                        </div>   
                </div>

                {errorModalOpen && (
                    <div className='error_modal-close'>
                    <div className='error_modal_header'>
                        <p> </p>
                        <div className='make_modal_header_close' onClick={handleErrorModalClose}>
                            <Close/>
                        </div>
                    </div>
                    <div className='error_modal-close_content_wrap'>
                        <div className='error_modal-close_content'>
                            <Warning/>
                            <p>      .<br/> ?</p>
                            
                        </div>
                        <div className='error_modal_footer'>
                            <div className='make_modal_bg_confirm_btn2-1' onClick={handleErrorModalClose}><p></p></div>
                            <div className='make_modal_bg_confirm_btn2-2   active' onClick={handleModalClose}><p></p></div>

                        </div>   
                    </div>
                    </div>
                )}
            </div>

            </>

        )}

        </>
        
    )
}

export default Makequiz;